import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { useNavigate, useOutletContext } from 'react-router-dom';
import { fetchLeaderboard, deleteScore, fetchMoneyLeaderboard } from '../api/starcadeApi';
import GlassCard from '../components/ui/GlassCard';
import Button from '../components/ui/Button';
import { motion, LayoutGroup } from 'framer-motion';
import { useAuth } from '../context/AuthContext';
import ProfileDecoration from '../components/ui/ProfileDecoration';
import { Trophy, RefreshCcw } from 'lucide-react';
import { useEffects } from '../effects/EffectLayer';
import Dialog from '../components/ui/Dialog';
import useScrollGradient from '../lib/useScrollGradient';

const isProfileViewable = (viewer, targetProfile) => {
  if (!targetProfile) return false;
  if (viewer && (viewer.isMaster || viewer.id === targetProfile.id)) return true; // Master or self
  if (targetProfile.profileVisibility === 'public') return true;
  if (targetProfile.profileVisibility === 'friends-only' && viewer && targetProfile.friends.some(f => f.id === viewer.id)) return true;
  return false;
};

const isTodayBirthday = (birthday) => {
  if (!birthday) return false;
  const today = new Date();
  const todayMMDD = `${(today.getMonth() + 1).toString().padStart(2, '0')}-${today.getDate().toString().padStart(2, '0')}`;
  const userBirthdayMMDD = birthday.substring(5);
  return userBirthdayMMDD === todayMMDD;
};

// 등수에 따라 테두리 색상을 결정하는 함수
const getRankColor = (rank) => {
  if (rank === 1) return 'border-yellow-400 shadow-yellow-400/50 shadow-lg';
  if (rank === 2) return 'border-gray-300 shadow-gray-300/50 shadow-lg';
  if (rank === 3) return 'border-amber-600 shadow-amber-600/50 shadow-lg';
  if (rank <= 5) return 'border-sky-500/50';
  return 'border-transparent';
};

const TabButton = ({ label, isActive, ...props }) => (
    <motion.button
        className={`px-4 py-2 text-sm font-bold relative transition-colors ${isActive ? 'text-white' : 'text-white/50 hover:text-white'}`}
        {...props}
    >
        {label}
        {isActive && <motion.div layoutId="active-ranking-underline" className="absolute bottom-[-1px] left-0 right-0 h-0.5 bg-emerald-400" />}
    </motion.button>
);

export default function RankingPage() {
  const { games, handleViewProfile } = useOutletContext();
  const navigate = useNavigate();
  const { user } = useAuth();
  const { triggerEffect } = useEffects();
  const [activeTab, setActiveTab] = useState('single-player');
  const [selectedGame, setSelectedGame] = useState(null);
  const [rankings, setRankings] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const { scrollContainerRef, gradientClasses } = useScrollGradient();
  const [scoreToDelete, setScoreToDelete] = useState(null);

  const filteredGames = useMemo(() => {
      if (activeTab === 'money') return [];
      return games.filter(g => g.category === activeTab);
  }, [games, activeTab]);

  useEffect(() => {
    if (activeTab !== 'money') {
        if (filteredGames.length > 0) {
            setSelectedGame(filteredGames[0].id);
        } else {
            setSelectedGame(null);
        }
    } else {
        setSelectedGame(null);
    }
  }, [activeTab, filteredGames]);

  const loadRankings = useCallback(async () => {
    setIsLoading(true);
    setRankings([]);
    let data;
    try {
        if (activeTab === 'money') {
            data = await fetchMoneyLeaderboard();
        } else if (selectedGame) {
            data = await fetchLeaderboard(selectedGame);
        }

        if (data && data.success) {
            setRankings(data.scores);
        } else if (data) {
            console.error('Failed to fetch rankings:', data.message);
        }
    } catch (error) {
        console.error('Error fetching rankings:', error);
    } finally {
        setIsLoading(false);
    }
  }, [activeTab, selectedGame]);

  useEffect(() => {
    loadRankings();
  }, [loadRankings]);

  const handleDeleteScore = async (scoreId, deleteAll) => {
    triggerEffect('Toast', { message: '랭킹 삭제 중...', type: 'info' });
    const result = await deleteScore(scoreId, deleteAll);
    if (result.success) {
        triggerEffect('Toast', { message: '랭킹이 삭제되었습니다.', type: 'success' });
        loadRankings();
    } else {
        triggerEffect('Toast', { message: result.message || '랭킹 삭제에 실패했습니다.', type: 'error' });
    }
    setScoreToDelete(null);
  };

  const renderRankingItem = (entry, index) => {
    const rank = index + 1;
    const isBirthday = isTodayBirthday(entry.userBirthday);
    const bgClass = entry.cardEffect === 'Birthday' ? 'birthday-gradient-bg-animated' : '';
    const showCardDecoration = entry.cardDecoration === 'Birthday';
    const scoreLabel = activeTab === 'money' ? '코인' : '점';

    return (
        <motion.li 
            key={entry.id || entry.userId}
            className={`relative flex items-center justify-between p-3 rounded-xl bg-white/5 border-2 ${getRankColor(rank)} ${bgClass}`}
            initial={{ opacity: 0, y: 0 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0 }}
            transition={{ delay: index * 0.05 }}
        >
            {showCardDecoration && (
                <>
                    <motion.div 
                        className="absolute top-0 left-0 text-lg" 
                        style={{transform: 'translate(-30%, -30%)'}}
                        animate={{ rotate: [0, 10, -10, 0], scale: [1, 1.1, 1] }}
                        transition={{ duration: 1.5, repeat: Infinity, ease: 'easeInOut' }}
                    >
                        🎉
                    </motion.div>
                    <motion.div 
                        className="absolute bottom-0 right-0 text-lg"
                        style={{transform: 'translate(30%, 30%)'}}
                        animate={{ rotate: [0, -10, 10, 0], scale: [1, 1.1, 1] }}
                        transition={{ duration: 1.5, repeat: Infinity, ease: 'easeInOut' }}
                    >
                        🎁
                    </motion.div>
                </>
            )}
            <div className="flex items-center gap-4 flex-grow min-w-0">
                <span className="font-bold text-lg w-6 text-center">{rank}</span>
                <div className={`relative flex items-center gap-2 ${isBirthday ? 'text-shadow-lg text-white' : ''}`}>
                    <div className="relative">
                        <img src={entry.userAvatar} alt="avatar" className={`w-8 h-8 rounded-full bg-white/10 ${isBirthday && isProfileViewable(user, entry) ? 'birthday-avatar-glow' : ''}`} />
                        {isBirthday && isProfileViewable(user, entry) && <ProfileDecoration decorationId="Birthday" target="list" />}
                    </div>
                    <span 
                        className={`font-semibold truncate ${entry.userName !== '익명' && entry.userName !== '삭제된 계정' ? 'cursor-pointer hover:text-emerald-400' : ''}`}
                        onClick={() => {
                            if (entry.userName !== '익명' && entry.userName !== '삭제된 계정') {
                                if (user && entry.userId === user.id) {
                                    navigate('/profile'); 
                                } else {
                                    handleViewProfile(entry.userId); 
                                }
                            }
                        }}
                    >
                        {entry.userName}
                    </span>
                </div>
            </div>
            <div className="flex items-center flex-shrink-0">
                <div className={`flex flex-col items-end ${isBirthday ? 'text-shadow-lg text-white' : ''}`}>
                    <span className="font-semibold text-white/90">{entry.score.toLocaleString()}{scoreLabel}</span>
                    {entry.timestamp && <span className="text-xs text-white/50">{new Date(entry.timestamp).toLocaleDateString()}</span>}
                </div>
                {user?.isMaster && activeTab !== 'money' && <Button variant="danger" className="ml-4 px-2 py-1 text-xs" onClick={() => setScoreToDelete(entry)}>삭제</Button>}
            </div>
        </motion.li>
    );
  }

  return (
    <div className="flex flex-col gap-4">
      <GlassCard title={
          <div className="flex justify-between items-center">
            <span>🏆 랭킹</span>
            <Button variant="secondary" className="px-2 py-1 text-xs" onClick={loadRankings}><RefreshCcw className="w-3 h-3"/></Button>
          </div>
      }>
        <div className="px-1 mb-3">
            <LayoutGroup>
                <div className="flex border-b border-white/10">
                    <TabButton label="싱글플레이" isActive={activeTab === 'single-player'} onClick={() => setActiveTab('single-player')} />
                    <TabButton label="멀티플레이" isActive={activeTab === 'multi-player'} onClick={() => setActiveTab('multi-player')} />
                    <TabButton label="소지금" isActive={activeTab === 'money'} onClick={() => setActiveTab('money')} />
                </div>
            </LayoutGroup>
        </div>

        {activeTab !== 'money' && (
            <div className="p-2 flex flex-wrap gap-2 bg-black/10 rounded-xl mb-4">
            {filteredGames.map(game => (
                <Button 
                key={game.id} 
                variant={selectedGame === game.id ? 'primary' : 'secondary'}
                onClick={() => setSelectedGame(game.id)}
                className="text-sm px-3 py-1"
                >
                {game.icon} {game.name}
                </Button>
            ))}
            </div>
        )}

        <div className={`relative ${gradientClasses}`}>
          <ul ref={scrollContainerRef} className="space-y-2 max-h-[60vh] overflow-y-auto pr-2">
          {isLoading ? (
            <p className="text-center text-white/50 py-8">로딩 중...</p>
          ) : rankings.length > 0 ? (
            rankings.map(renderRankingItem)
          ) : (
            <p className="text-center text-white/50 py-8">랭킹 정보가 없습니다.</p>
          )}
          </ul>
        </div>
      </GlassCard>
      <Dialog open={!!scoreToDelete} onOpenChange={() => setScoreToDelete(null)} title="랭킹 삭제 확인">
        <div className="p-4 text-center">
          <p className="mb-4">정말로 이 랭킹을 삭제하시겠습니까?</p>
          <p className="mb-4 font-bold">{scoreToDelete?.userName}: {scoreToDelete?.score.toLocaleString()}점</p>
          <div className="my-4 text-left text-sm space-y-2">
              <p className="font-semibold text-white/80">삭제 옵션:</p>
              <Button className="w-full text-left p-2" variant="secondary" onClick={() => handleDeleteScore(scoreToDelete.id, false)}>
                  <p className="font-bold">이 점수만 삭제</p>
                  <p className="text-xs text-white/70">이 유저의 다음으로 높은 점수가 랭킹에 표시됩니다.</p>
              </Button>
              <Button className="w-full text-left p-2" variant="danger" onClick={() => handleDeleteScore(scoreToDelete.id, true)}>
                  <p className="font-bold">이 게임의 모든 점수 삭제</p>
                  <p className="text-xs text-white/70">이 유저의 모든 기록이 이 게임 랭킹에서 사라집니다.</p>
              </Button>
          </div>
          <div className="flex justify-center gap-4 mt-4">
            <Button variant="secondary" onClick={() => setScoreToDelete(null)}>취소</Button>
          </div>
        </div>
      </Dialog>
    </div>
  );
}