import React from 'react';
import { motion } from 'framer-motion';

const Flicker = ({ children }) => (
  <motion.div
    animate={{
      opacity: [1, 0.7, 0.9, 0.6, 1],
    }}
    transition={{
      duration: 2,
      repeat: Infinity,
      repeatType: "loop",
      ease: "easeInOut",
      repeatDelay: Math.random() * 1.5 + 0.5
    }}
    style={{
        filter: `drop-shadow(0 0 8px currentColor)`
    }}
  >
    {children}
  </motion.div>
);

export default Flicker;
