import React, { Suspense } from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import Layout from './Layout';
import GamePage from './pages/GamePage';
import ProfilePage from './pages/ProfilePage';
import ShopPage from './pages/ShopPage';
import RankingPage from './pages/RankingPage';
import CalendarPage from './pages/CalendarPage';
import MainPage from './pages/MainPage';
import ProtectedRoute from './components/auth/ProtectedRoute';
import MultiplayerLobbyPage from './pages/MultiplayerLobbyPage'; // Add this line
import { useTheme } from './context/ThemeContext';

function LoadingScreen() {
    // A minimal loading screen
    return <div className="w-full h-screen grid place-items-center bg-slate-950 text-white/70">Loading...</div>;
}

export default function AppContent() {
    return (
        <Suspense fallback={<LoadingScreen />}>
            <Routes>
                <Route path="/" element={<Layout />}>
                    <Route index element={<MainPage />} />
                    <Route path="games" element={<GamePage />} />
                    <Route path="games/:gameId" element={<GamePage />} />
                    <Route path="games/:gameId/:roomId" element={<GamePage />} /> {/* Add this line */}
                    <Route path="ranking" element={<RankingPage />} />
                    
                    <Route element={<ProtectedRoute />}>
                        <Route path="profile" element={<ProfilePage />} />
                        <Route path="shop" element={<ShopPage />} />
                        <Route path="calendar" element={<CalendarPage />} />
                        <Route path="lobby" element={<MultiplayerLobbyPage />} /> {/* Add this line */}
                    </Route>

                    <Route path="*" element={<Navigate to="/" />} />
                </Route>
            </Routes>
        </Suspense>
    );
}