import React, { useMemo, useEffect } from 'react';
import { motion, useAnimation } from 'framer-motion';

const generateRandomStripPart = (symbols) => {
  // symbols가 비어있으면 빈 배열을 반환하여 오류 방지
  if (!symbols || symbols.length === 0) {
    return [];
  }
  const totalSpinSymbols = 30;
  const baseStrip = [];
  while (baseStrip.length < totalSpinSymbols) {
    baseStrip.push(...symbols);
  }
  for (let i = baseStrip.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [baseStrip[i], baseStrip[j]] = [baseStrip[j], baseStrip[i]];
  }
  return baseStrip.slice(0, totalSpinSymbols);
};

export default function Reel({
  symbols,
  startColumn,
  finalColumn,
  spinning,
  numRows,
  spinDuration,
  delayIndex,
  isBankrupt,
}) {
  const SYMBOL_HEIGHT = 64;
  const STOP_DELAY_PER_REEL = 200;
  const controls = useAnimation();

  // 멈춤 현상을 해결하기 위해 복잡한 내부 state를 제거하고 useMemo로 간결하게 변경
  const symbolStrip = useMemo(() => {
    const randomPart = generateRandomStripPart(symbols);
    // startColumn이 없거나 비어있는 경우를 대비하여 finalColumn으로 대체
    const effectiveStartColumn = startColumn && startColumn.length > 0 ? startColumn : finalColumn;
    return [...effectiveStartColumn, ...randomPart, ...finalColumn];
  }, [symbols, startColumn, finalColumn]);

  useEffect(() => {
    if (spinning) {
      const final_y = - (symbolStrip.length - numRows) * SYMBOL_HEIGHT;
      
      // y 위치를 0으로 리셋하여 스트립의 시작(이전 결과)부터 보이게 함
      controls.set({ y: 0 });

      controls.start({
        y: final_y,
        transition: {
          ease: [0.25, 1, 0.5, 1], 
          duration: spinDuration,
        },
      });
    } else {
      const final_y = - (symbolStrip.length - numRows) * SYMBOL_HEIGHT;
      const stopTimer = setTimeout(() => {
        controls.stop(); 
        controls.start({
          y: final_y,
          transition: { 
            type: "spring", 
            stiffness: 100, 
            damping: 20,
            mass: 1.2
          }
        });
      }, delayIndex * STOP_DELAY_PER_REEL);

      return () => clearTimeout(stopTimer);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [spinning, symbolStrip, controls]);


  return (
    <div
      className="relative overflow-hidden"
      style={{ height: SYMBOL_HEIGHT * numRows, width: SYMBOL_HEIGHT }}
    >
      <motion.div animate={controls} className="flex flex-col">
        {symbolStrip.map((symbol, idx) => {
          const isFinalSymbol = idx >= symbolStrip.length - numRows;
          const isSkull = symbol === '💀';
          const shouldWiggle = isBankrupt && !spinning && isFinalSymbol && isSkull;

          if (shouldWiggle) {
            return (
              <motion.div
                key={idx}
                className="flex items-center justify-center text-4xl sm:text-5xl h-16 w-16 flex-shrink-0"
                animate={{
                  scale: [1, 1.05, 1],
                  rotate: [0, 2, -2, 0],
                }}
                transition={{
                  duration: 2.5,
                  repeat: Infinity,
                  repeatType: "mirror",
                  ease: "easeInOut",
                  delay: Math.random() * 0.5,
                }}
              >
                {symbol}
              </motion.div>
            )
          }

          return (
            <div
              key={idx}
              className="flex items-center justify-center text-4xl sm:text-5xl h-16 w-16 flex-shrink-0"
            >
              {symbol}
            </div>
          );
        })}
      </motion.div>
    </div>
  );
}