import React from 'react';
import { motion } from 'framer-motion';
import config from './config'; // config 파일 임포트

const colors = ["#e63946", "#f1faee", "#a8dadc", "#457b9d", "#1d3557"];

const ConfettiPiece = ({ x, y, rotate, delay }) => (
  <motion.div
    className="absolute w-3 h-5"
    style={{
      x,
      y,
      rotate,
      backgroundColor: colors[Math.floor(Math.random() * colors.length)],
    }}
    initial={{ opacity: 1, y: y - 200, scale: 0 }}
    animate={{ opacity: [1, 1, 0], y: window.innerHeight + 100, scale: 1, rotate: rotate + 360 }}
    transition={{ duration: 2, delay, ease: "linear" }}
  />
);

const Confetti = ({ duration = config.duration }) => {
  const pieces = Array.from({ length: config.particleCount }).map((_, i) => ({
    id: i,
    x: Math.random() * window.innerWidth,
    y: -50,
    rotate: Math.random() * 360,
    delay: Math.random() * (duration / 1000),
  }));

  return (
    <div className="fixed inset-0 pointer-events-none z-[101]">
      {pieces.map(({ id, x, y, rotate, delay }) => (
        <ConfettiPiece key={id} x={x} y={y} rotate={rotate} delay={delay} />
      ))}
    </div>
  );
};

export default Confetti;