import React from 'react';
import { motion } from 'framer-motion';

const Star = ({ x, y, size, delay }) => (
    <motion.div
        className="absolute"
        style={{
            x,
            y,
            width: size,
            height: size,
            backgroundColor: 'white',
            clipPath: 'polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%)',
            filter: 'drop-shadow(0 0 5px rgba(255, 255, 200, 0.8))'
        }}
        animate={{
            opacity: [0.5, 1, 0.5],
            scale: [0.8, 1.2, 0.8],
        }}
        transition={{
            duration: 2,
            delay,
            ease: "easeInOut",
            repeat: Infinity,
            repeatType: "mirror"
        }}
    />
);

const Stars = ({ duration = 4000 }) => {
    const starField = Array.from({ length: 100 }).map((_, i) => ({
        id: i,
        x: Math.random() * window.innerWidth,
        y: Math.random() * window.innerHeight,
        size: Math.random() * 10 + 5,
        delay: Math.random() * 2,
    }));

    return (
        <motion.div 
            className="fixed inset-0 pointer-events-none z-[101] overflow-hidden"
            initial={{ opacity: 0 }}
            animate={{ opacity: [0, 1, 1, 0] }}
            transition={{ duration: duration / 1000, times: [0, 0.2, 0.8, 1] }}
        >
            {starField.map(s => <Star key={s.id} {...s} />)}
        </motion.div>
    );
};

export default Stars;
