
import React from 'react'
export default function Button({variant='primary', className='', ...props}){
  const base = 'px-3 py-2 rounded-2xl transition font-medium overflow-hidden';
  const styles = {
    primary: 'bg-emerald-600 hover:bg-emerald-500 border border-transparent',
    secondary: 'bg-white/10 hover:bg-white/20 border border-white/10',
    danger: 'bg-red-600 hover:bg-red-500 border border-red-700',
  }[variant] || '';
  return <button className={[base, styles, className].join(' ')} {...props} />;
}
