import React from 'react';
import { motion } from 'framer-motion';

const Snowflake = ({ x, size, duration, delay }) => (
    <motion.div
        className="absolute rounded-full bg-white"
        style={{
            x,
            width: size,
            height: size,
            boxShadow: '0 0 10px rgba(255, 255, 255, 0.8)',
        }}
        initial={{ y: -50, opacity: 0 }}
        animate={{
            y: window.innerHeight + 50,
            x: [x, x + 20, x - 20, x],
            opacity: 1,
        }}
        transition={{
            duration,
            delay,
            ease: "linear",
            repeat: Infinity,
            repeatType: "loop"
        }}
    />
);


const Snow = ({ duration = 5000 }) => {
    const flakes = Array.from({ length: 100 }).map((_, i) => ({
        id: i,
        x: Math.random() * window.innerWidth,
        size: Math.random() * 5 + 2,
        duration: Math.random() * 5 + 5,
        delay: Math.random() * 5,
    }));

    return (
        <div className="fixed inset-0 pointer-events-none z-[101] overflow-hidden">
            {flakes.map(f => <Snowflake key={f.id} {...f} />)}
        </div>
    );
};

export default Snow;