// Vite의 import.meta.glob 기능을 사용해 패턴에 맞는 파일들을 모두 가져옵니다.
const modules = import.meta.glob('./patterns/**/index.jsx', { eager: true });
const cssUrls = import.meta.glob('./patterns/**/style.css', { query: '?url', eager: true });

// 자동화된 이펙트 맵을 생성합니다.
const effects = {};

for (const path in modules) {
  // 경로에서 이펙트 이름(폴더 이름)을 추출합니다. 예: './patterns/Skull/index.jsx' -> 'Skull'
  const effectName = path.split('/')[2];

  if (effectName) {
    const cssPath = `./patterns/${effectName}/style.css`;
    const componentPath = `./patterns/${effectName}/index.jsx`;

    effects[effectName] = {
      // 이펙트 컴포넌트
      Component: modules[componentPath]?.default,
      // 이펙트 CSS 파일의 빌드 후 URL
      cssUrl: cssUrls[cssPath],
      // 이펙트 설정 정보 (이제 백엔드에서 가져올 것이므로 빈 객체로 초기화)
      config: {},
    };
  }
}

// 이펙트 이름 배열 (Select 옵션용)은 이제 백엔드에서 가져올 것이므로 여기서 export하지 않습니다.
// export const availableEffectNames = Object.keys(effects).map(name => ({
//   value: name,
//   label: name,
// }));

// 완성된 이펙트 정보 맵
export default effects;