import React from 'react';
import { motion } from 'framer-motion';

const range = (n) => Array.from({ length: n }, (_, i) => i);
const sample = (arr) => arr[Math.floor(Math.random() * arr.length)];

const LAUGH_TEXTS = ['lamo', 'ㅋㅋㅋㅋ', 'lol', '🤣', '풉', 'ㅋㄷㅋㄷ'];

export default function Laughing() {
  const count = 15; // 파티클 수 감소 (20 -> 15)

  return (
    <div className="absolute inset-0 overflow-hidden pointer-events-none">
      {range(count).map(i => {
        const x = Math.random() * 100;
        const y = Math.random() * 100;
        const delay = Math.random() * 2;
        const duration = 0.5 + Math.random() * 1;
        const text = sample(LAUGH_TEXTS);
        const fontSize = 24 + Math.random() * 24;
        const rotation = -30 + Math.random() * 60;

        return (
          <motion.div
            key={i}
            className="absolute text-white font-bold"
            style={{ 
              left: `${x}%`, 
              top: `${y}%`,
              fontSize: `${fontSize}px`,
              textShadow: '0 2px 4px rgba(0,0,0,0.5)',
              willChange: 'transform, opacity', // 애니메이션 성능 최적화
            }}
            initial={{ opacity: 0, scale: 0.5, rotate: 0 }}
            animate={{ 
                opacity: [0, 1, 1, 0],
                scale: [0.5, 1, 1, 0.8],
                rotate: rotation,
            }}
            transition={{
              delay,
              duration,
              ease: 'easeOut',
              times: [0, 0.2, 0.8, 1]
            }}
          >
            {text}
          </motion.div>
        );
      })}
    </div>
  );
}