import React, { useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'

export default function Celebration({ show, message, onComplete }) {
  useEffect(() => {
    if (show) {
      document.body.style.overflow = 'hidden';
      document.documentElement.style.overflow = 'hidden'; // Add to html as well
      const timer = setTimeout(() => {
        onComplete();
      }, 3000); // Show for 3 seconds
      return () => clearTimeout(timer);
    } else {
      document.body.style.overflow = '';
      document.documentElement.style.overflow = ''; // Remove from html as well
    }
    return () => {
      document.body.style.overflow = ''; // Clean up on unmount
      document.documentElement.style.overflow = ''; // Clean up on unmount
    };
  }, [show, onComplete]);

  return (
    <AnimatePresence>
      {show && (
        <motion.div
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: 1, scale: 1 }}
          exit={{ opacity: 0, scale: 1.2 }}
          transition={{ duration: 0.3 }}
          className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm overflow-hidden" // Add overflow-hidden
        >
          <motion.div
            initial={{ y: -50, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            className="bg-white/10 rounded-3xl p-8 text-white text-xl sm:text-2xl md:text-3xl font-bold text-center shadow-2xl border border-white/20 will-change-transform" // Add will-change-transform
          >
            {message}
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
}