import React, { useState, useEffect, useCallback } from 'react';
import Dialog from '../ui/Dialog';
import Button from '../ui/Button';
import { useAuth } from '../../context/AuthContext';
import { AnimatePresence, motion } from 'framer-motion';
import { useEffects } from '../../effects/EffectLayer';

const NICKNAME_REGEX = /^[a-zA-Z0-9_가-힣]{3,15}$/;
const EMAIL_REGEX = /^[^@]+@[^@]+\.[^@]+$/;

const maskEmail = (email) => {
  if (!email || typeof email !== 'string') return '';
  const parts = email.split('@');
  if (parts.length !== 2) return email;

  const localPart = parts[0];
  const domainPart = parts[1];

  if (localPart.length <= 2) {
    return `*@${domainPart}`;
  }

  const firstChar = localPart[0];
  const lastChar = localPart[localPart.length - 1];
  return `${firstChar}***${lastChar}@${domainPart}`;
};

export default function LoginDialog({ open, onOpenChange }) {
  const { findUser, isIdentifierAvailable, requestRegistrationCode, verifyRegistrationCode, requestLoginCode, verifyLoginCode } = useAuth();
  const { triggerEffect } = useEffects();

  const [step, setStep] = useState('initial');
  const [identifier, setIdentifier] = useState('');
  const [nickname, setNickname] = useState('');
  const [email, setEmail] = useState('');
  const [code, setCode] = useState('');
  const [error, setError] = useState('');
  const [isRequestingCode, setIsRequestingCode] = useState(false);
  const [isVerifyingCode, setIsVerifyingCode] = useState(false);

  useEffect(() => {
    if (!open) {
      setStep('initial');
      setIdentifier('');
      setNickname('');
      setEmail('');
      setCode('');
      setError('');
      setIsRequestingCode(false);
      setIsVerifyingCode(false);
    }
  }, [open]);

  const handleInitialSubmit = useCallback(async (e) => {
    e.preventDefault();
    setError('');
    setIsRequestingCode(true);
    const foundUser = await findUser(identifier);

    if (foundUser) {
      // Existing user: request a login code
      const result = await requestLoginCode(identifier);
      if (result.success) {
        setEmail(result.email);
        setNickname(result.name);
        setStep('enterCode');
        triggerEffect('Toast', { message: result.message, type: 'success' });
      } else {
        setError(result.message);
      }
    } else {
      // New user: start registration flow
      if (EMAIL_REGEX.test(identifier)) {
        if (!(await isIdentifierAvailable(identifier))) {
          setError('이미 사용 중인 이메일입니다.');
          setIsRequestingCode(false);
          return;
        }
        setEmail(identifier);
        // For email-based registration, let backend generate nickname if not provided
        const result = await requestRegistrationCode(identifier, null); // Pass null for name
        if (result.success) {
          setNickname(result.generatedName); // Set generated nickname
          setStep('enterCode');
          triggerEffect('Toast', { message: result.message, type: 'success' });
        } else {
          setError(result.message);
        }
      } else {
        if (!NICKNAME_REGEX.test(identifier)) {
          setError('닉네임은 3-15자의 영문, 숫자, 밑줄(_)만 사용 가능합니다.');
          setIsRequestingCode(false);
          return;
        }
        if (!(await isIdentifierAvailable(identifier))) {
          setError('이미 사용 중인 닉네임입니다.');
          setIsRequestingCode(false);
          return;
        }
        setNickname(identifier);
        setStep('enterEmail');
      }
    }
    setIsRequestingCode(false);
  }, [identifier, findUser, isIdentifierAvailable, requestLoginCode, requestRegistrationCode, triggerEffect]);

  const handleEmailSubmit = useCallback(async (e) => {
    e.preventDefault();
    setError('');
    if (!EMAIL_REGEX.test(email)) {
      setError('유효한 이메일 주소를 입력해주세요.');
      return;
    }
    // Check if email is already registered
    const emailUser = await findUser(email);
    if (emailUser) {
        // This email is already registered, so we should switch to login flow.
        setIsRequestingCode(true);
        const result = await requestLoginCode(email);
        setIsRequestingCode(false);
        if (result.success) {
            setEmail(result.email);
            setNickname(result.name);
            setStep('enterCode');
            triggerEffect('Toast', { message: '이미 가입된 이메일입니다. 로그인 코드를 전송했습니다.', type: 'info' });
        } else {
            setError(result.message);
        }
        return;
    }

    setIsRequestingCode(true);
    const result = await requestRegistrationCode(email, nickname); // Pass nickname
    setIsRequestingCode(false);
    if (result.success) {
      setNickname(result.generatedName); // Set generated nickname
      setStep('enterCode');
      triggerEffect('Toast', { message: result.message, type: 'success' });
    } else {
      setError(result.message);
    }
  }, [email, nickname, findUser, requestLoginCode, requestRegistrationCode, triggerEffect]);

  const handleCodeSubmit = useCallback(async (e) => {
    e.preventDefault();
    setError('');
    setIsVerifyingCode(true);

    const isLoginAttempt = await findUser(email); // Check if it's a login or registration

    if (isLoginAttempt) {
      // Existing user login verification
      const result = await verifyLoginCode(email, code);
      if (result.success) {
        onOpenChange(false);
      } else {
        setError(result.message);
      }
    } else {
      // New user registration verification
      const result = await verifyRegistrationCode(email, code);
      if (result.success) {
        onOpenChange(false);
      } else {
        setError(result.message);
      }
    }
    setIsVerifyingCode(false);
  }, [code, email, findUser, verifyLoginCode, verifyRegistrationCode, onOpenChange]);

  const renderStep = () => {
    const motionProps = { 
        initial: { opacity: 0, y: 10 }, 
        animate: { opacity: 1, y: 0 }, 
        exit: { opacity: 0, y: -10 },
        transition: { duration: 0.2 }
    };

    switch (step) {
      case 'initial':
        return (
          <motion.form {...motionProps} key="initial" onSubmit={handleInitialSubmit} className="flex flex-col gap-4">
            <p className="text-white/70 text-sm">이메일 또는 닉네임을 입력하여 로그인하거나, 새 계정을 만드세요.</p>
            <input type="text" placeholder="이메일 또는 닉네임" value={identifier} onChange={(e) => setIdentifier(e.target.value)} autoFocus className="w-full px-3 py-2 rounded-xl bg-black/20 border border-white/10 focus:border-emerald-500 focus:ring-emerald-500/50 focus:ring-1 outline-none transition-colors" />
            <Button type="submit" disabled={!identifier.trim() || isRequestingCode}>
              {isRequestingCode ? '요청 중...' : '계속하기'}
            </Button>
          </motion.form>
        );
      case 'enterEmail':
        return (
          <motion.form {...motionProps} key="enterEmail" onSubmit={handleEmailSubmit} className="flex flex-col gap-4">
            <p className="text-white/70 text-sm">"{nickname || '새로운 사용자'}" 닉네임으로 계정을 생성하려면, 사용할 이메일을 입력해주세요.</p>
            <input type="email" placeholder="이메일 주소" value={email} onChange={(e) => setEmail(e.target.value)} autoFocus className="w-full px-3 py-2 rounded-xl bg-black/20 border border-white/10 focus:border-emerald-500 focus:ring-emerald-500/50 focus:ring-1 outline-none transition-colors" />
            <Button type="submit" disabled={!email.trim() || isRequestingCode}>
              {isRequestingCode ? '전송 중...' : '인증코드 받기'}
            </Button>
          </motion.form>
        );
      case 'enterCode':
        return (
          <motion.form {...motionProps} key="enterCode" onSubmit={handleCodeSubmit} className="flex flex-col gap-4">
            <p className="text-white/70 text-sm">{maskEmail(email)} (으)로 전송된 인증 코드를 입력하세요.</p>
            <input type="text" placeholder="인증 코드 6자리" value={code} onChange={(e) => setCode(e.target.value)} autoFocus className="w-full px-3 py-2 rounded-xl bg-black/20 border border-white/10 focus:border-emerald-500 focus:ring-emerald-500/50 focus:ring-1 outline-none transition-colors" />
            <Button type="submit" disabled={code.length < 6 || isVerifyingCode}>
              {isVerifyingCode ? '확인 중...' : '로그인 또는 가입 완료'}
            </Button>
          </motion.form>
        );
      default:
        return null;
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange} title="로그인 또는 회원가입">
        <div className="min-h-[140px]">
            {error && <p className="text-red-400 text-sm mb-2">{error}</p>}
            <AnimatePresence mode="wait">
                {renderStep()}
            </AnimatePresence>
        </div>
        <div className="flex justify-end gap-2 mt-4">
            {step !== 'initial' && <Button type="button" variant="secondary" onClick={() => setStep('initial')}>이전</Button>}
            <Button type="button" variant="secondary" onClick={() => onOpenChange(false)}>취소</Button>
        </div>
    </Dialog>
  );
}