import React from 'react';
import Dialog from '../ui/Dialog';
import Button from '../ui/Button';
import { useAuth } from '../../context/AuthContext';
import { useEffects } from '../../effects/EffectLayer';
import { motion } from 'framer-motion';

export default function TitleSelectionDialog({ open, onOpenChange }) {
    const { user, updateUser } = useAuth();
    const { triggerEffect } = useEffects();

    const handleSelectTitle = (title) => {
        updateUser({ title: title });
        triggerEffect('Toast', { message: title ? `칭호가 [${title}](으)로 변경되었습니다.` : '칭호가 해제되었습니다.', type: 'info' });
        onOpenChange(false);
    };

    if (!user) return null;

    return (
        <Dialog open={open} onOpenChange={onOpenChange} title="칭호 변경">
            <motion.div 
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -10 }}
                transition={{ duration: 0.2 }}
            >
                <div className="flex flex-col gap-2 p-4 max-h-[60vh] overflow-y-auto">
                    {user.unlockedTitles && user.unlockedTitles.length > 0 ? (
                        <>
                            <p className="text-sm text-white/70 mb-2">장착할 칭호를 선택하세요.</p>
                            <Button
                                variant={!user.title ? 'primary' : 'secondary'}
                                onClick={() => handleSelectTitle('')}
                                className="w-full text-left p-3"
                            >
                                칭호 없음
                            </Button>
                        </>
                    ) : null}
                    {user.unlockedTitles && user.unlockedTitles.map(title => (
                        <Button
                            key={title}
                            variant={user.title === title ? 'primary' : 'secondary'}
                            onClick={() => handleSelectTitle(title)}
                            className="w-full text-left p-3"
                        >
                            {title}
                        </Button>
                    ))}
                    {(!user.unlockedTitles || user.unlockedTitles.length === 0) && (
                        <p className="text-center text-white/50 py-4">사용 가능한 칭호가 없습니다.</p>
                    )}
                </div>
                <div className="flex justify-end gap-2 mt-2 px-4 pb-4">
                    <Button variant='secondary' onClick={() => onOpenChange(false)}>취소</Button>
                </div>
            </motion.div>
        </Dialog>
    );
}