import React, { useEffect, useState, useCallback, useMemo, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import Celebration from '../components/Celebration';
import Button from '../components/ui/Button';
import PauseOverlay from '../components/PauseOverlay';
import { useEffects } from '../effects/EffectLayer';
import { useAuth } from '../context/AuthContext';
import { submitScore } from '../lib/scoreManager';
import SubmitScoreDialog from '../components/ui/SubmitScoreDialog';

export const meta = { id: 'slider', name: '슬라이더', icon: '🧩', topic: 'slider', description: '밀고, 정렬하기', category: 'single-player' };

const GAME_ID = meta.id;

const generateSolvablePuzzle = () => {
  let board = [...Array(15).keys(), null];
  for (let i = 0; i < 150; i++) {
    const emptyIndex = board.indexOf(null);
    const neighbors = [];
    if (emptyIndex > 3) neighbors.push(emptyIndex - 4);
    if (emptyIndex < 12) neighbors.push(emptyIndex + 4);
    if (emptyIndex % 4 > 0) neighbors.push(emptyIndex - 1);
    if (emptyIndex % 4 < 3) neighbors.push(emptyIndex + 1);
    const randomIndex = neighbors[Math.floor(Math.random() * neighbors.length)];
    [board[emptyIndex], board[randomIndex]] = [board[randomIndex], board[emptyIndex]];
  }
  return board;
};

export default function Slider15({ isPaused, lastKeyEvent }) {
  const { triggerEffect } = useEffects();
  const { user, updateUser } = useAuth();
  const [showCelebration, setShowCelebration] = useState(false);
  const [showSubmitScoreDialog, setShowSubmitScoreDialog] = useState(false);
  const keyProcessedRef = useRef(null);

  const solutionTiles = useMemo(() => [...Array(15).keys(), null], []);

  const getInitialState = () => {
      try {
          const savedState = localStorage.getItem(`starcade-gamestate-${GAME_ID}`);
          if (savedState) {
              const parsed = JSON.parse(savedState);
              if (parsed.tiles && typeof parsed.moveCount === 'number') {
                  return parsed;
              }
          }
      } catch (e) {
          console.error("Failed to load saved game state", e);
      }
      return {
          tiles: generateSolvablePuzzle(),
          moveCount: 0,
      };
  };

  const [gameState, setGameState] = useState(getInitialState);

  useEffect(() => {
    const solved = JSON.stringify(gameState.tiles) === JSON.stringify(solutionTiles);
    if (solved && !showCelebration) {
        setShowSubmitScoreDialog(true);
        triggerEffect('Confetti');
    }
  }, []);

  useEffect(() => {
      try {
          localStorage.setItem(`starcade-gamestate-${GAME_ID}`, JSON.stringify(gameState));
      } catch (e) {
          console.error("Failed to save game state", e);
      }
  }, [gameState, solutionTiles]);

  const TILE_SIZE = 64;
  const GAP_SIZE = 8;
  const BOARD_SIZE = (TILE_SIZE + GAP_SIZE) * 4 - GAP_SIZE;

  const getTilePosition = (index) => {
    const row = Math.floor(index / 4);
    const col = index % 4;
    return { x: col * (TILE_SIZE + GAP_SIZE), y: row * (TILE_SIZE + GAP_SIZE) };
  };
  
  const checkWin = useCallback((currentTiles) => {
    const solved = JSON.stringify(currentTiles) === JSON.stringify(solutionTiles);
    if (solved) {
      setShowSubmitScoreDialog(true);
    }
    return solved;
  }, [solutionTiles]);

  const resetGame = useCallback(() => {
    localStorage.removeItem(`starcade-gamestate-${GAME_ID}`);
    setGameState({
        tiles: generateSolvablePuzzle(),
        moveCount: 0,
    });
    setShowCelebration(false);
    setShowSubmitScoreDialog(false);
  }, []);

  const move = useCallback((clickedIndex) => {
    if (showCelebration) return;

    setGameState(s => {
        const currentTiles = s.tiles;
        const emptyIndex = currentTiles.indexOf(null);
        const clickedRow = Math.floor(clickedIndex / 4);
        const clickedCol = clickedIndex % 4;
        const emptyRow = Math.floor(emptyIndex / 4);
        const emptyCol = emptyIndex % 4;

        if (clickedRow !== emptyRow && clickedCol !== emptyCol) return s;

        const newTiles = [...currentTiles];
        let numMoved = 0;

        if (clickedRow === emptyRow) {
            numMoved = Math.abs(clickedCol - emptyCol);
            const direction = Math.sign(clickedIndex - emptyIndex);
            for (let i = emptyIndex; i !== clickedIndex; i += direction) {
                newTiles[i] = newTiles[i + direction];
            }
        } else if (clickedCol === emptyCol) {
            numMoved = Math.abs(clickedRow - emptyRow);
            const direction = Math.sign(clickedIndex - emptyIndex);
            const step = direction * 4;
            for (let i = emptyIndex; i !== clickedIndex; i += step) {
                newTiles[i] = newTiles[i + step];
            }
        }

        if (numMoved > 0) {
            newTiles[clickedIndex] = null;
            if (checkWin(newTiles)) {
                triggerEffect('Confetti');
            }
            return { ...s, tiles: newTiles, moveCount: s.moveCount + numMoved };
        }
        
        return s;
    });
  }, [checkWin, triggerEffect, showCelebration]);

  const handleSubmitScore = () => {
    submitScore({
        gameId: meta.id,
        score: 1000 - gameState.moveCount,
        user: user,
        options: { moves: gameState.moveCount }
    }, triggerEffect, updateUser);
    setShowSubmitScoreDialog(false);
  };

  const handleCloseSubmitDialog = () => {
    if (user) {
        notifyGameOver({
            gameId: meta.id,
            score: 1000 - gameState.moveCount,
            userId: user.id
        }).then(result => {
            if (result.success && result.user) {
                updateUser(result.user, true);
            }
        });
    }
    setShowSubmitScoreDialog(false);
  };

  useEffect(() => {
    if (isPaused || !lastKeyEvent || keyProcessedRef.current === lastKeyEvent.time) return;
    keyProcessedRef.current = lastKeyEvent.time;

    const { key } = lastKeyEvent;
    const lowerKey = key.toLowerCase();
    
    const emptyIndex = gameState.tiles.indexOf(null);
    let targetIndex = -1;

    if (lowerKey === 'arrowup' || lowerKey === 'w') {
      if (emptyIndex < 12) targetIndex = emptyIndex + 4;
    } else if (lowerKey === 'arrowdown' || lowerKey === 's') {
      if (emptyIndex > 3) targetIndex = emptyIndex - 4;
    } else if (lowerKey === 'arrowleft' || lowerKey === 'a') {
      if (emptyIndex % 4 < 3) targetIndex = emptyIndex + 1;
    } else if (lowerKey === 'arrowright' || lowerKey === 'd') {
      if (emptyIndex % 4 > 0) targetIndex = emptyIndex - 1;
    }
    
    if (targetIndex !== -1) move(targetIndex);

  }, [lastKeyEvent, isPaused, move, gameState.tiles]);

  return (
    <div className="flex flex-col items-center gap-4 relative">
      <div className="text-sm text-white/70 mb-2">이동 횟수: {gameState.moveCount}</div>
      <div className="relative bg-white/5 rounded-2xl p-2" style={{ width: BOARD_SIZE + 16, height: BOARD_SIZE + 16 }}>
        <AnimatePresence>
          {gameState.tiles.map((value, index) => {
            if (value === null) return null;
            const { x, y } = getTilePosition(index);
            return (
              <motion.div
                key={value}
                layout
                animate={{ x, y }}
                transition={{ type: "spring", stiffness: 400, damping: 30 }}
                onClick={() => move(index)}
                className="absolute w-16 h-16 rounded-xl font-bold border border-white/10 bg-white/10 hover:bg-white/20 grid place-items-center cursor-pointer text-xl"
              >
                {value + 1}
              </motion.div>
            );
          })}
        </AnimatePresence>
      </div>
      <div className="mt-2">
        <Button onClick={resetGame} variant="secondary">다시 시작</Button>
      </div>
      <Celebration show={showCelebration} message="🎉 축하합니다! 퍼즐을 맞췄어요! 🎉" onComplete={() => setShowCelebration(false)} />
      <PauseOverlay isPaused={isPaused} />

      <SubmitScoreDialog
          open={showSubmitScoreDialog}
          onOpenChange={setShowSubmitScoreDialog}
          score={1000 - gameState.moveCount}
          scoreLabel={`점수 (이동: ${gameState.moveCount})`}
          user={user}
          onSubmit={handleSubmitScore}
          onClose={handleCloseSubmitDialog}
      />
    </div>
  );
}