import config from '../config';

const request = async (endpoint, options = {}) => {
    try {
        const token = localStorage.getItem('starcade-token');
        const headers = {
            'Content-Type': 'application/json',
            ...options.headers,
        };

        if (token) {
            headers['Authorization'] = `Bearer ${token}`;
        }

        const response = await fetch(`${config.backendUrl}${endpoint}`, {
            ...options,
            headers,
        });

        // Handle cases where the server returns a non-JSON response (e.g., HTML for a 404)
        const contentType = response.headers.get("content-type");
        if (response.ok && contentType && contentType.indexOf("application/json") !== -1) {
            return response.json();
        } else if (response.ok) {
            return { success: true, data: await response.text() };
        } else {
            // For errors, try to parse as JSON, but fall back to text.
            try {
                return await response.json();
            } catch (e) {
                return { success: false, message: response.statusText || 'An unknown error occurred.' };
            }
        }
    } catch (error) {
        console.error(`API call to ${endpoint} failed:`, error);
        return { success: false, message: error.message || 'An unknown network error occurred.' };
    }
};

// Auth & User
export const checkSession = () => request('/api/session');
export const findUser = (identifier) => request(`/api/users/${encodeURIComponent(identifier)}`);
export const isIdentifierAvailable = (identifier) => request(`/api/identifier-exists/${encodeURIComponent(identifier)}`);
