export const requestRegistrationCode = (email, name) => request('/api/request-registration-code', { method: 'POST', body: JSON.stringify({ email, name }) });
export const verifyRegistrationCode = (email, code) => request('/api/verify-registration-code', { method: 'POST', body: JSON.stringify({ email, code }) });
