export const updateUser = (userId, userData) => request(`/api/users/${userId}`, { method: 'POST', body: JSON.stringify(userData) });
export const requestDeleteCode = () => request('/api/request-delete-code', { method: 'POST' });
export const deleteAccount = (code, rankingAction) => request('/api/delete-account', { method: 'POST', body: JSON.stringify({ code, rankingAction }) });

