import React from 'react';
import { motion } from 'framer-motion';

const SparkleParticle = ({ x, y, size, delay, duration }) => {
  const colors = ['#FFFFFF', '#FFD700', '#FFA500'];
  const color = colors[Math.floor(Math.random() * colors.length)];

  return (
    <motion.div
      className="absolute rounded-full"
      style={{
        left: x,
        top: y,
        width: size,
        height: size,
        backgroundColor: color,
        boxShadow: `0 0 ${size * 2}px ${color}`,
      }}
      initial={{ opacity: 0, scale: 0 }}
      animate={{ 
        opacity: [0, 1, 1, 0],
        scale: [0, 1, 1, 0],
      }}
      transition={{ 
        duration: duration,
        delay: delay,
        ease: 'easeInOut',
        times: [0, 0.2, 0.8, 1]
      }}
    />
  );
};

const Sparkle = ({ duration = 1500 }) => {
  const particles = Array.from({ length: 30 }).map((_, i) => ({
    id: i,
    x: `${Math.random() * 100}%`,
    y: `${Math.random() * 100}%`,
    size: Math.random() * 6 + 3,
    delay: Math.random() * (duration / 1000 - 0.5),
    duration: 0.5 + Math.random() * 0.5,
  }));

  return (
    <div className="absolute inset-0 pointer-events-none">
      {particles.map(p => <SparkleParticle key={p.id} {...p} />)}
    </div>
  );
};

export default Sparkle;
