import React from 'react';
import { motion } from 'framer-motion';
import '../crt-styles.css';
import config from './config'; // duration(ms) 사용

/**
 * 켜짐 시퀀스 (요구사항 반영)
 * - 완전 검정 → 1초 대기 → 섬광 & 가로선 동시 시작
 * - 가로선이 다 펴진 뒤의 홀드(대기) 시간은 짧게
 */
const CRTon = () => {
  const { duration } = config;         // ms
  const d = duration / 1000;           // sec
  const HOLD_BLACK = 1.0;              // 🔒 검정 상태 고정 대기 0.5초

  // 가로선/섬광 타이밍: 0.5초 뒤 시작
  const lineDelay = HOLD_BLACK;
  const lineDur   = d * 0.45;
  const dotDelay  = HOLD_BLACK;
  const dotDur    = d * 0.35;

  return (
    <motion.div className="crt-on-effect" initial={{ opacity: 1 }} animate={{ opacity: 1 }} transition={{ duration: 3, delay: d }}>
      {/* A) 블랙아웃: 처음엔 완전 검정(0.5초 유지) → 선이 펼치기 시작할 때 아주 빠르게 걷힘 */}
      <motion.div
        className="crt-blackout"
        initial={{ opacity: 1 }}
        animate={{ opacity: 0 }}
        transition={{
          delay: HOLD_BLACK,            // 🔥 0.5초 뒤에 사라지기 시작
          duration: 0.12,               // 빠르게 제거
          ease: 'easeIn'
        }}
      />

      {/* B) 섬광(밝은 점): 0.5초 뒤에 터지기 시작 → 짧고 강하게 */}
      <motion.div
        className="crt-bright-dot"
        initial={{ scale: 0, opacity: 0 }}
        animate={{
          scale:   [0, 1.8, 0.18, 0],
          opacity: [0, 1, 0.5, 0],
        }}
        transition={{
          delay: dotDelay,
          duration: dotDur,
          times: [0, 0.4, 0.75, 1],
          ease: 'easeInOut',
        }}
      />

      {/* C) 가로선: 0.5초 뒤에 동시에 쫙 펼침 */}
      <motion.div
        className="crt-h-line"
        initial={{ opacity: 0, scaleX: 0 }}
        animate={{
          opacity: [0, 1, 1, 0.9, 0],
          scaleX:  [0, 0.28, 1, 1, 1],
        }}
        transition={{
          delay: lineDelay,
          duration: lineDur,
          times: [0, 0.25, 0.65, 0.9, 1],
          ease: 'easeInOut',
        }}
      />
    </motion.div>
  );
};

export default CRTon;
