import React, { useEffect } from 'react';
import decorations from '../../decorations';

const ProfileDecoration = ({ decorationId, target = 'profile', dialogRef }) => {
  useEffect(() => {
    if (decorationId && decorations[decorationId]?.cssUrl) {
      const linkId = `decoration-style-${decorationId}`;
      if (!document.getElementById(linkId)) {
        const link = document.createElement('link');
        link.id = linkId;
        link.rel = 'stylesheet';
        link.href = decorations[decorationId].cssUrl.default;
        document.head.appendChild(link);
      }
    }
    // No cleanup needed, styles are small and can persist for the session
  }, [decorationId]);

  if (!decorationId || !decorations[decorationId]) {
    return null;
  }

  const DecorationComponent = decorations[decorationId].Component;

  return <DecorationComponent target={target} dialogRef={dialogRef} />;
};

export default ProfileDecoration;
