import React, { useState } from 'react';
import Dialog from '../ui/Dialog';
import Button from '../ui/Button';
import Select from '../ui/Select';

const CreateRoomDialog = ({ open, onOpenChange, onCreate, gameId }) => {
    const [boardSize, setBoardSize] = useState(4);
    const [maxGauge, setMaxGauge] = useState(3);
    const [isTeamMode, setIsTeamMode] = useState(false);

    const handleCreate = () => {
        onCreate({ gameId, options: { boardSize, maxGauge, isTeamMode } });
        onOpenChange(false);
    };

    // Reset state when dialog is closed/opened
    useState(() => {
        if (open) {
            setBoardSize(4);
            setMaxGauge(3);
            setIsTeamMode(false);
        }
    }, [open]);

    return (
        <Dialog open={open} onOpenChange={onOpenChange} title={`${gameId} 방 만들기`}>
            <div className="flex flex-col gap-4 p-4">
                <div className="flex justify-between items-center">
                    <label className="font-semibold">보드 크기</label>
                    <Select 
                        options={[{value: 4, label: '4x4'}, {value: 5, label: '5x5'}]}
                        value={boardSize}
                        onChange={setBoardSize}
                        className="w-32"
                    />
                </div>
                <div className="flex justify-between items-center">
                    <label className="font-semibold">최대 게이지</label>
                    <Select 
                        options={[{value: 1, label: '1'}, {value: 2, label: '2'}, {value: 3, label: '3'}, {value: 5, label: '5'}]}
                        value={maxGauge}
                        onChange={setMaxGauge}
                        className="w-32"
                    />
                </div>
                <div className="flex justify-between items-center">
                    <label className="font-semibold">팀전</label>
                    <Button onClick={() => setIsTeamMode(!isTeamMode)} variant={isTeamMode ? 'primary' : 'secondary'} className="w-32">
                        {isTeamMode ? '활성화' : '비활성화'}
                    </Button>
                </div>
                <div className="mt-4 flex justify-end gap-2">
                    <Button variant="secondary" onClick={() => onOpenChange(false)}>취소</Button>
                    <Button onClick={handleCreate}>방 만들기</Button>
                </div>
            </div>
        </Dialog>
    );
};

export default CreateRoomDialog;