import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import GlassCard from '../components/ui/GlassCard';
import Button from '../components/ui/Button';
import { useAuth } from '../context/AuthContext';
import { fetchProfileEffects, fetchProfileDecorations, fetchCardEffects, fetchCardDecorations, fetchBundles, fetchThemes, buyItem } from '../api/starcadeApi';
import { motion, AnimatePresence } from 'framer-motion';

const ShopItem = ({ item, owned, onBuy, user }) => {
  const canAfford = user && user.money >= item.price;
  const cantBuy = owned || !canAfford;

  return (
    <motion.div 
      className="bg-white/5 p-4 rounded-xl flex flex-col items-center text-center gap-2 border border-white/10 h-full"
      whileHover={{ scale: 1.05, backgroundColor: 'rgba(255, 255, 255, 0.1)' }}
      transition={{ type: 'spring', stiffness: 300 }}
    >
      <div className="text-4xl flex-shrink-0">{item.icon || '✨'}</div>
      <h3 className="font-bold h-10 flex-grow">{item.name}</h3>
      <p className="text-xs text-white/60 h-12 flex-grow">{item.description || '멋진 아이템!'}</p>
      <div className="flex items-center gap-2 text-yellow-400 font-bold flex-shrink-0">
        <span>💰</span>
        <span>{item.price.toLocaleString()}</span>
      </div>
      <Button onClick={() => onBuy(item)} disabled={cantBuy} className="w-full mt-2 flex-shrink-0">
        {owned ? '보유 중' : canAfford ? '구매' : '코인 부족'}
      </Button>
    </motion.div>
  );
};

const BundleItem = ({ item, onBuy, user }) => {
    const canAfford = user && user.money >= item.price;
    const owned = false; // TODO: Implement ownership check for bundles
    const cantBuy = owned || !canAfford;

    return (
        <motion.div 
          className="bg-gradient-to-br from-purple-500/10 to-indigo-500/10 p-4 rounded-xl flex flex-col items-center text-center gap-2 border border-purple-400/20 h-full"
          whileHover={{ scale: 1.02, boxShadow: '0 0 15px rgba(128, 90, 213, 0.5)' }}
          transition={{ type: 'spring', stiffness: 300 }}
        >
          <div className="text-4xl flex-shrink-0">{item.icon || '🎁'}</div>
          <h3 className="font-bold h-10 flex-grow">{item.name}</h3>
          <p className="text-xs text-white/60 h-12 flex-grow">{item.description}</p>
          <div className="flex items-center gap-2 text-yellow-400 font-bold flex-shrink-0">
            <span>💰</span>
            <span>{item.price.toLocaleString()}</span>
          </div>
          <Button onClick={() => onBuy(item)} disabled={cantBuy} className="w-full mt-2 flex-shrink-0">
            {owned ? '보유 중' : canAfford ? '구매' : '코인 부족'}
          </Button>
        </motion.div>
      );
}

const ThemeItem = ({ item, owned, onBuy, user }) => {
    const canAfford = user && user.money >= item.price;
    const cantBuy = owned || !canAfford;

    return (
        <motion.div 
          className="bg-gradient-to-br from-teal-500/10 to-cyan-500/10 p-4 rounded-xl flex flex-col items-center text-center gap-2 border border-cyan-400/20 h-full"
          whileHover={{ scale: 1.02, boxShadow: '0 0 15px rgba(20, 184, 166, 0.5)' }}
          transition={{ type: 'spring', stiffness: 300 }}
        >
          <div className="text-4xl flex-shrink-0">{item.icon || '🎨'}</div>
          <h3 className="font-bold h-10 flex-grow">{item.name}</h3>
          <p className="text-xs text-white/60 h-12 flex-grow">{item.description}</p>
          <div className="flex items-center gap-2 text-yellow-400 font-bold flex-shrink-0">
            <span>💰</span>
            <span>{item.price.toLocaleString()}</span>
          </div>
          <Button onClick={() => onBuy(item)} disabled={cantBuy} className="w-full mt-2 flex-shrink-0">
            {owned ? '보유 중' : canAfford ? '구매' : '코인 부족'}
          </Button>
        </motion.div>
      );
}

const TabButton = ({ label, isActive, onClick }) => (
  <motion.button
    onClick={onClick}
    className={`px-4 py-2 text-lg font-bold rounded-t-lg relative ${isActive ? 'text-white' : 'text-white/50'}`}
    animate={{ scale: isActive ? 1.1 : 1, y: isActive ? -5 : 0 }}
  >
    {label}
    {isActive && <motion.div layoutId="underline" className="absolute bottom-0 left-0 right-0 h-1 bg-emerald-400 rounded-full" />}
  </motion.button>
);

export default function ShopPage() {
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState('items');
  const { user, updateUser, triggerEffect } = useAuth();
  const [shopData, setShopData] = useState({
    profileEffects: [],
    profileDecorations: [],
    cardEffects: [],
    cardDecorations: [],
    bundles: [],
    themes: [],
  });
  const [isLoading, setIsLoading] = useState(true);

  const loadShopItems = async () => {
    setIsLoading(true);
    const [profileEffects, profileDecorations, cardEffects, cardDecorations, bundles, themes] = await Promise.all([
      fetchProfileEffects(),
      fetchProfileDecorations(),
      fetchCardEffects(),
      fetchCardDecorations(),
      fetchBundles(),
      fetchThemes(),
    ]);

    const filterForSale = (items) => (items || []).filter(item => item.isForSale === true);

    setShopData({
      profileEffects: filterForSale(profileEffects.profileEffects),
      profileDecorations: filterForSale(profileDecorations.profileDecorations),
      cardEffects: filterForSale(cardEffects.cardEffects),
      cardDecorations: filterForSale(cardDecorations.cardDecorations),
      bundles: filterForSale(bundles.bundles),
      themes: filterForSale(themes.themes),
    });
    setIsLoading(false);
  };

  useEffect(() => {
    loadShopItems();
  }, []);

  const handleBuy = async (item, itemType) => {
    const result = await buyItem(item.id, itemType);
    if (result.success) {
      updateUser(result.user, true); // silent update
      triggerEffect('Toast', { message: `'${item.name}' 구매 완료!`, type: 'success' });
      triggerEffect('Confetti');
      // Refresh shop data to update owned status, etc.
      loadShopItems();
    } else {
      triggerEffect('Toast', { message: result.message || '구매 실패', type: 'error' });
    }
  };

  const renderItemsSection = (title, itemList, ownedList, itemType) => (
    <div className="mb-8">
      <h2 className="text-2xl font-bold mb-4 text-white">{title}</h2>
      {itemList.length > 0 ? (
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {itemList.map(item => (
            <ShopItem 
              key={item.id} 
              item={item} 
              owned={(ownedList || []).includes(item.id)}
              onBuy={(item) => handleBuy(item, itemType)}
              user={user}
            />
          ))}
        </div>
      ) : (
        <p className="text-white/50">판매 중인 아이템이 없습니다.</p>
      )}
    </div>
  );

  const renderCurrentTab = () => {
    switch (activeTab) {
      case 'items':
        return (
          <motion.div key="items" initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }}>
            {renderItemsSection("프로필 효과", shopData.profileEffects, user?.unlockedEffects, 'profileEffect')}
            {renderItemsSection("프로필 장식", shopData.profileDecorations, user?.unlockedProfileDecorations, 'profileDecoration')}
            {renderItemsSection("카드 효과", shopData.cardEffects, user?.unlockedCardEffects, 'cardEffect')}
            {renderItemsSection("카드 장식", shopData.cardDecorations, user?.unlockedCardDecorations, 'cardDecoration')}
          </motion.div>
        );
      case 'bundles':
        return (
            <motion.div key="bundles" initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }}>
                {shopData.bundles.length > 0 ? (
                    <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                        {shopData.bundles.map(item => (
                            <BundleItem key={item.id} item={item} onBuy={(item) => handleBuy(item, 'bundle')} user={user} />
                        ))}
                    </div>
                ) : (
                    <p className="text-center text-white/70 py-8">판매 중인 묶음 상품이 없습니다.</p>
                )}
            </motion.div>
        );
      case 'themes':
        return (
            <motion.div key="themes" initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }}>
                {shopData.themes.length > 0 ? (
                    <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                        {shopData.themes.map(item => (
                            <ThemeItem key={item.id} item={item} onBuy={(item) => handleBuy(item, 'theme')} user={user} owned={(user?.unlockedThemes || []).includes(item.id)} />
                        ))}
                    </div>
                ) : (
                    <p className="text-center text-white/70 py-8">판매 중인 테마가 없습니다.</p>
                )}
            </motion.div>
        );
      default:
        return null;
    }
  };

  return (
    <div className="flex flex-col gap-4">
      <GlassCard title="상점">
        <div className="p-4">
          <div className="flex border-b border-white/10 mb-4">
            <TabButton label="아이템" isActive={activeTab === 'items'} onClick={() => setActiveTab('items')} />
            <TabButton label="묶음상품" isActive={activeTab === 'bundles'} onClick={() => setActiveTab('bundles')} />
            <TabButton label="테마" isActive={activeTab === 'themes'} onClick={() => setActiveTab('themes')} />
          </div>
          {isLoading ? (
            <p className="text-center text-white/70">아이템을 불러오는 중...</p>
          ) : (
            <AnimatePresence mode="wait">
              {renderCurrentTab()}
            </AnimatePresence>
          )}
        </div>
      </GlassCard>
      <Button onClick={() => navigate('/')} variant="secondary">돌아가기</Button>
    </div>
  );
}
