import React from 'react'
import { Card, CardHeader, CardTitle, CardContent } from './Card'
export default function GlassCard({title, children, actions, className='', contentPaddingTop='', isMainCard = false}){ // contentPaddingTop prop 추가
  return (
        <Card className={`${className} ${isMainCard ? 'main-card' : ''}`}>
      {title && <CardHeader><CardTitle>{title}</CardTitle></CardHeader>}
      <CardContent ptClass={contentPaddingTop}> {/* ptClass로 전달 */}
        {children}
        {actions && <div className="mt-4 flex flex-wrap gap-2">{actions}</div>}
      </CardContent>
    </Card>
  )
}