import React from 'react';
import { motion } from 'framer-motion';

const RainDrop = ({ x, delay, duration }) => (
    <motion.div
        className="absolute h-10 w-0.5 bg-gradient-to-b from-transparent to-blue-300"
        style={{ x }}
        initial={{ y: -100, opacity: 0 }}
        animate={{ y: window.innerHeight + 100, opacity: [0, 1, 1, 0] }}
        transition={{ duration, delay, ease: "linear", repeat: Infinity, repeatDelay: 1 }}
    />
);

const Rain = ({ duration = 3000 }) => {
    const drops = Array.from({ length: 100 }).map((_, i) => ({
        id: i,
        x: Math.random() * window.innerWidth,
        delay: Math.random() * 2,
        duration: Math.random() * 0.5 + 0.5,
    }));

    return (
        <div className="fixed inset-0 pointer-events-none z-[101] overflow-hidden">
            {drops.map(d => <RainDrop key={d.id} {...d} />)}
        </div>
    );
};

export default Rain;
