import React, { useRef, useState, useLayoutEffect } from 'react';
import { motion, useAnimation } from 'framer-motion';
import Flicker from './Flicker';

const MarqueeText = ({ message, onAnimationComplete, isArcadeMode }) => {
  const containerRef = useRef(null);
  const textRef = useRef(null);
  const [textWidth, setTextWidth] = useState(0);
  const [containerWidth, setContainerWidth] = useState(0);
  const controls = useAnimation();

  useLayoutEffect(() => {
    if (containerRef.current && textRef.current) {
      const currentContainerWidth = containerRef.current.offsetWidth;
      const currentTextWidth = textRef.current.offsetWidth;

      setContainerWidth(currentContainerWidth);
      setTextWidth(currentTextWidth);

      const duration = Math.max(3, (currentContainerWidth + currentTextWidth) / 64);

      controls.set({ x: currentContainerWidth });
      controls.start({
        x: -currentTextWidth,
        transition: {
          duration,
          ease: "linear",
          delay: 0,
          onComplete: onAnimationComplete
        }
      });
    }
  }, [message, controls, onAnimationComplete]);

  const className = isArcadeMode ? "arcade-help-message" : "normal-help-message";

  return (
    <motion.div
      ref={containerRef}
      key={message}
      className={className}
      style={{ overflow: 'hidden' }}
    >
      <Flicker>
        <motion.span
          ref={textRef}
          animate={controls}
          style={{ whiteSpace: 'nowrap', display: 'inline-block' }}
        >
          {message}
        </motion.span>
      </Flicker>
    </motion.div>
  );
};

export default MarqueeText;
