import React, { useMemo } from 'react';
import { useOutletContext, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Gamepad2 } from 'lucide-react';
import { useTheme } from '../context/ThemeContext';

const ArcadeWelcomeScreen = () => {
  const navigate = useNavigate();
  return (
    <div 
      className="h-full flex flex-col items-center justify-center text-center p-8 bg-black"
      style={{ fontFamily: "'Press Start 2P', cursive" }}
    >
      <motion.h1 
        className="text-5xl md:text-6xl font-bold text-white mb-4"
        style={{ textShadow: '0 0 8px #fff, 0 0 15px #ff00ff, 0 0 25px #ff00ff' }}
        initial={{ opacity: 0, y: -50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.7, type: 'spring' }}
      >
        STARCADE
      </motion.h1>
      
      <motion.button
        onClick={() => navigate('/games')}
        className="mt-12 px-8 py-4 text-2xl bg-gray-800 text-white border-4 border-gray-600 rounded-lg shadow-lg focus:outline-none focus:ring-4 ring-yellow-400 ring-opacity-50"
        animate={{ scale: [1, 1.03, 1] }}
        transition={{ duration: 1.2, repeat: Infinity, ease: "easeInOut" }}
      >
        PRESS START
      </motion.button>
      <p className="text-yellow-400 mt-10 text-sm animate-pulse">SELECT GAME FROM MENU</p>
    </div>
  );
};

const GeneralWelcomeScreen = () => {
  const { games } = useOutletContext();
  const navigate = useNavigate();

  return (
    <div className="h-full flex flex-col items-center justify-center text-center p-8">
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="max-w-4xl w-full"
      >
        <div className="flex justify-center items-center gap-4 mb-6">
            <Gamepad2 size={48} className="text-emerald-400" />
            <h1 className="text-5xl font-bold text-white">
                Welcome to Starcade
            </h1>
        </div>
        <p className="text-white/70 mb-10 max-w-xl mx-auto">
          아래 목록에서 플레이할 게임을 선택하거나, 오른쪽 메뉴에서 다른 기능을 확인해보세요.
        </p>
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
            {(games || []).map((game, i) => (
                <motion.button
                    key={game.id}
                    className="bg-white/5 p-4 rounded-xl flex flex-col items-center gap-2 w-full h-28 justify-center border border-white/10 hover:bg-white/10 transition-colors duration-200"
                    style={{ willChange: 'transform' }}
                    whileHover={{ scale: 1.05 }}
                    transition={{ type: 'spring', stiffness: 300 }}
                    onClick={() => navigate(`/games/${game.id}`)}
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0, transition: { delay: 0.1 + i * 0.05 } }}
                >
                    <span className="text-4xl">{game.icon}</span>
                    <span className="text-sm font-semibold truncate">{game.name}</span>
                </motion.button>
            ))}
        </div>
      </motion.div>
    </div>
  );
};


export default function MainPage() {
  const { isArcadeMode } = useTheme();

  return isArcadeMode ? <ArcadeWelcomeScreen /> : <GeneralWelcomeScreen />;
}
