import React, { useState, useCallback, useEffect } from 'react';
import Dialog from '../ui/Dialog';
import Button from '../ui/Button';
import Select from '../ui/Select';
import { useAuth } from '../../context/AuthContext';
import { useEffects } from '../../effects/EffectLayer';

const avatarStyles = [
    { value: 'bottts', label: '로봇' },
    { value: 'adventurer', label: '모험가' },
    { value: 'pixel-art', label: '픽셀 아트' },
    { value: 'miniavs', label: '미니 아바타' },
    { value: 'micah', label: '캐릭터' },
    { value: 'initials', label: '이니셜' },
];

export default function AvatarEditorDialog({ open, onOpenChange }) {
    const { user, updateUser } = useAuth();
    const { triggerEffect } = useEffects();

    const [avatarStyle, setAvatarStyle] = useState('bottts');
    const [avatarSeed, setAvatarSeed] = useState(user?.name || '');
    const [previewAvatarUrl, setPreviewAvatarUrl] = useState(user?.avatar || '');

    const updatePreview = useCallback(() => {
        const query = new URLSearchParams();
        query.set('seed', avatarSeed);
        setPreviewAvatarUrl(`https://api.dicebear.com/8.x/${avatarStyle}/svg?${query.toString()}`);
    }, [avatarStyle, avatarSeed]);

    useEffect(() => {
        if (open) {
            const currentUrl = new URL(user.avatar);
            const pathParts = currentUrl.pathname.split('/');
            const style = pathParts[2] || 'bottts';
            const seed = currentUrl.searchParams.get('seed') || user.name;
            setAvatarStyle(style);
            setAvatarSeed(seed);
        } else {
            setPreviewAvatarUrl(user.avatar);
        }
    }, [open, user]);

    useEffect(() => {
        if(open) {
            updatePreview();
        }
    }, [avatarStyle, avatarSeed, open, updatePreview]);

    const handleStyleChange = (style) => {
        setAvatarStyle(style);
    };

    const handleSeedChange = (e) => {
        setAvatarSeed(e.target.value);
    };

    const handleRandomSeed = () => {
        setAvatarSeed(Math.random().toString(36).substring(7));
    };

    const handleAvatarSave = () => {
        updateUser({ avatar: previewAvatarUrl });
        onOpenChange(false);
        triggerEffect('Toast', { message: '프로필 사진이 변경되었습니다.', type: 'success' });
    };

    return (
        <Dialog open={open} onOpenChange={onOpenChange} title="프로필 사진 변경">
            <div className="flex flex-col items-center gap-4 p-4">
                <img src={previewAvatarUrl} alt="avatar preview" className="w-24 h-24 rounded-full bg-white/10 border-2 border-white/20" />
                <div className="w-full space-y-3">
                    <div>
                        <label htmlFor="avatar-style" className="font-semibold text-sm text-white/70 mb-1 block">스타일</label>
                        <Select
                            id="avatar-style"
                            options={avatarStyles}
                            value={avatarStyle}
                            onChange={handleStyleChange}
                            className="w-full"
                        />
                    </div>
                    <div>
                        <label htmlFor="avatar-seed" className="font-semibold text-sm text-white/70 mb-1 block">시드 (Seed)</label>
                        <div className="flex items-center gap-2">
                            <input
                                id="avatar-seed"
                                type="text"
                                value={avatarSeed}
                                onChange={handleSeedChange}
                                className="w-full px-3 py-2 rounded-xl bg-black/20 border border-white/10 focus:border-emerald-500 focus:ring-emerald-500/50 focus:ring-1 outline-none transition-colors"
                                placeholder="원하는 문자를 입력하세요"
                            />
                            <Button variant="secondary" onClick={handleRandomSeed} className="flex-shrink-0">랜덤</Button>
                        </div>
                    </div>
                </div>
                <div className="flex justify-center gap-4 mt-4">
                    <Button variant="secondary" onClick={() => onOpenChange(false)}>취소</Button>
                    <Button onClick={handleAvatarSave}>저장</Button>
                </div>
            </div>
        </Dialog>
    );
}
