import React, { useEffect, useState, useCallback, useRef } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import Button from '../components/ui/Button';
import { motion, AnimatePresence } from 'framer-motion';

export const meta = {
  id: 'arena2048',
  name: '2048 아레나',
  icon: '⚔️',
  topic: 'arena2048',
  description: '합성, 게이지, 공격! 최후의 1인이 되세요.',
  category: 'multi-player',
};

const tileColors = {
    2: 'bg-gray-700', 4: 'bg-gray-600', 8: 'bg-orange-500', 16: 'bg-orange-600',
    32: 'bg-red-500', 64: 'bg-red-600', 128: 'bg-yellow-500', 256: 'bg-yellow-600',
    512: 'bg-green-500', 1024: 'bg-green-600', 2048: 'bg-blue-500', 4096: 'bg-blue-600',
};

const getTileClass = (tile) => {
    if (tile.value === 'LOCK') {
        return `rounded-md h-full w-full grid place-items-center font-extrabold border-2 border-red-500 bg-black text-red-500 text-xs`;
    }
    const colorClass = tileColors[tile.value] || 'bg-purple-500';
    const sizeClass = tile.value > 1000 ? 'text-xs' : 'text-sm';
    const attackClass = tile.isAttack ? 'border-2 border-pink-500 shadow-lg shadow-pink-500/50' : 'border border-white/10';
    return `rounded-md h-full w-full grid place-items-center font-extrabold ${colorClass} ${sizeClass} ${attackClass}`;
};

const PlayerBoard = ({ playerState, boardSize, isMe }) => {
    if (!playerState) return null;
    const TILE_SIZE = isMe ? 64 : 24;
    const GAP_SIZE = isMe ? 12 : 4;
    const BOARD_SIZE = (TILE_SIZE * boardSize) + (GAP_SIZE * (boardSize - 1));
    const teamColor = playerState.team === 'team1' ? 'text-cyan-400' : 'text-orange-400';

    return (
        <div className="flex flex-col items-center gap-2">
            <h3 className={`font-bold ${isMe ? 'text-lg' : 'text-base'} ${teamColor}`}>{playerState.name}</h3>
            <div className="p-2 bg-black/20 rounded-lg">
                <div className="relative" style={{ width: BOARD_SIZE, height: BOARD_SIZE }}>
                    {Array.from({length: boardSize * boardSize}).map((_, i) => (
                        <div key={`bg-${i}`} className="absolute rounded-md bg-white/5"
                             style={{
                                 width: TILE_SIZE, height: TILE_SIZE,
                                 top: Math.floor(i / boardSize) * (TILE_SIZE + GAP_SIZE),
                                 left: (i % boardSize) * (TILE_SIZE + GAP_SIZE),
                             }}
                        />
                    ))}
                    <AnimatePresence>
                        {playerState.board.map((tile, i) => {
                            if (tile === null) return null;
                            const y = Math.floor(i / boardSize) * (TILE_SIZE + GAP_SIZE);
                            const x = (i % boardSize) * (TILE_SIZE + GAP_SIZE);
                            return (
                                <motion.div
                                    key={tile.id}
                                    layout
                                    initial={{ opacity: 0, scale: 0.5 }}
                                    animate={{ opacity: 1, scale: 1, x, y }}
                                    exit={{ opacity: 0, scale: 0.5 }}
                                    transition={{ type: "spring", stiffness: 500, damping: 30 }}
                                    className={`absolute ${getTileClass(tile)}`}
                                    style={{ width: TILE_SIZE, height: TILE_SIZE }}
                                >
                                    {tile.value === 'LOCK' ? `LOCKED(${tile.lockedTurns})` : tile.value}
                                </motion.div>
                            );
                        })}
                    </AnimatePresence>
                    {playerState.isGameOver && (
                        <div className="absolute inset-0 bg-black/70 rounded-lg flex flex-col items-center justify-center">
                            <p className={`font-bold ${isMe ? 'text-2xl' : 'text-lg'}`}>GAME OVER</p>
                        </div>
                    )}
                </div>
            </div>
            <div className="w-full flex flex-col items-center gap-1">
                <div className="text-xs w-full text-center">점수: {playerState.score}</div>
                <div className="w-full bg-black/20 rounded-full h-4 border border-white/10">
                    <motion.div 
                        className="bg-emerald-500 h-full rounded-full"
                        initial={{ width: 0 }}
                        animate={{ width: `${(playerState.gauge / playerState.maxGauge) * 100}%` }}
                        transition={{ duration: 0.5 }}
                    />
                </div>
            </div>
        </div>
    );
}

export default function Arena2048({ isPaused, lastKeyEvent }) {
  const { roomId } = useParams();
  const { user, socket } = useAuth();
  const navigate = useNavigate();
  const [room, setRoom] = useState(null);
  const [error, setError] = useState('');
  const [finalResult, setFinalResult] = useState(null); // To store winner info
  const keyProcessedRef = useRef(null);

  useEffect(() => {
    if (!socket || !roomId) return;

    const handleGameUpdate = (newGameState) => {
        setRoom(prevRoom => ({ ...prevRoom, gameState: newGameState }));
    };
    const handleRoomJoined = (joinedRoom) => {
        setRoom(joinedRoom);
        if (joinedRoom.status === 'in-progress') {
            setGameState(joinedRoom.gameState);
        }
    };
    const handleGameStart = (initialGameState) => {
        setGameState(initialGameState);
        setRoom(prevRoom => ({ ...prevRoom, status: 'in-progress' }));
    };
    const handleRoomError = ({ message }) => setError(message);
    const handlePlayerJoined = (joinedUser) => {
        setRoom(prevRoom => ({
            ...prevRoom,
            players: [...prevRoom.players, joinedUser]
        }));
    };
    const handlePlayerLeft = (leftUserId) => {
        setRoom(prevRoom => ({
            ...prevRoom,
            players: prevRoom.players.filter(p => p.id !== leftUserId)
        }));
    };
    const handleGameEnd = (result) => {
        setFinalResult(result);
    };

    socket.on('gameUpdate', handleGameUpdate);
    socket.on('roomJoined', handleRoomJoined);
    socket.on('gameStart', handleGameStart);
    socket.on('roomError', handleRoomError);
    socket.on('playerJoined', handlePlayerJoined);
    socket.on('playerLeft', handlePlayerLeft);
    socket.on('gameEnd', handleGameEnd);

    socket.emit('joinRoom', roomId);

    return () => {
      socket.off('gameUpdate', handleGameUpdate);
      socket.off('roomJoined', handleRoomJoined);
      socket.off('gameStart', handleGameStart);
      socket.off('roomError', handleRoomError);
      socket.off('playerJoined', handlePlayerJoined);
      socket.off('playerLeft', handlePlayerLeft);
      socket.off('gameEnd', handleGameEnd);
    };
  }, [socket, roomId]);

  useEffect(() => {
    if (isPaused || !lastKeyEvent || !socket || !roomId || finalResult || room?.status !== 'in-progress') return;
    if (keyProcessedRef.current === lastKeyEvent.time) return;
    keyProcessedRef.current = lastKeyEvent.time;

    const keyMap = {
        'arrowup': 'up', 'w': 'up',
        'arrowdown': 'down', 's': 'down',
        'arrowleft': 'left', 'a': 'left',
        'arrowright': 'right', 'd': 'right',
    };
    const direction = keyMap[lastKeyEvent.key.toLowerCase()];

    if (direction) {
        socket.emit('gameAction', { roomId, action: { type: 'move', payload: direction } });
    }

    if (lastKeyEvent.key === ' ') { // Spacebar for attack
        socket.emit('gameAction', { roomId, action: { type: 'attack', payload: { targetId: 'random', attackType: 'auto' } } });
    }
  }, [lastKeyEvent, isPaused, socket, roomId, finalResult, room?.status]);

  const handleStartGame = () => {
      if (socket && room && room.hostId === user.id) {
          socket.emit('startGame', roomId);
      }
  };

  const handleLeaveRoom = () => {
    if (socket) socket.emit('leaveRoom', roomId);
    navigate('/lobby');
  };

  if (error) {
    return (
        <div className="p-4 text-center">
            <p className="text-red-400 mb-4">오류: {error}</p>
            <Button onClick={() => navigate('/lobby')}>로비로 돌아가기</Button>
        </div>
    );
  }

  if (!room || !user) {
    return <div className="text-center text-white/70">게임방 정보를 기다리는 중...</div>;
  }

  if (room.status === 'waiting') {
    return (
        <div className="flex flex-col items-center gap-4 w-full h-full p-4">
            <h2 className="text-3xl font-bold">{room.gameId} 대기 중...</h2>
            <div className="w-full max-w-md text-left">
                <h3 className="font-bold text-lg mb-2">플레이어 목록 ({room.players.length})</h3>
                <ul className="space-y-2">
                    {room.players.map(p => (
                        <li key={p.id} className="flex items-center gap-2 p-2 bg-white/10 rounded-lg">
                            <img src={p.avatar} alt={p.name} className="w-8 h-8 rounded-full" />
                            <span>{p.name} {p.id === room.hostId && '(방장)'}</span>
                        </li>
                    ))}
                </ul>
            </div>
            {room.hostId === user.id && (
                <Button onClick={handleStartGame} className="mt-4">게임 시작</Button>
            )}
            <Button onClick={handleLeaveRoom} variant="secondary" className="mt-2">방 나가기</Button>
        </div>
    );
  }

  const me = gameState?.players[user.id];
  const opponents = gameState ? Object.values(gameState.players).filter(p => p.id !== user.id) : [];

  return (
    <div className="flex flex-col items-center gap-4 w-full h-full">
        <div className="flex justify-around w-full">
            {opponents.map(player => (
                <PlayerBoard key={player.id} playerState={player} boardSize={gameState.options.boardSize || 4} isMe={false} />
            ))}
        </div>
        
        {me && <PlayerBoard playerState={me} boardSize={gameState.options.boardSize || 4} isMe={true} />}

        <div className="mt-4">
            <Button onClick={handleLeaveRoom} variant="danger">방 나가기</Button>
        </div>

        <AnimatePresence>
            {finalResult && (
                <motion.div 
                    className="absolute inset-0 bg-black/80 flex flex-col items-center justify-center gap-4"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                >
                    <h2 className="text-5xl font-bold text-yellow-400">게임 종료!</h2>
                    {finalResult.winner ? (
                        <p className="text-2xl">
                            {finalResult.isTeamWin ? `승리: ${finalResult.winner}` : `승자: ${finalResult.winner.name}`}
                        </p>
                    ) : (
                        <p className="text-2xl">무승부!</p>
                    )}
                    <Button onClick={handleLeaveRoom} variant="primary">로비로 돌아가기</Button>
                </motion.div>
            )}
        </AnimatePresence>
    </div>
  );
}