import React, { createContext, useState, useContext, useEffect, useCallback, useRef } from 'react';
import { io } from 'socket.io-client';
import config from '../config';
import { EffectContext } from '../effects/EffectLayer';
import * as api from '../api/starcadeApi';

const AuthContext = createContext();

export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const socketRef = useRef(null);
  const { triggerEffect } = useContext(EffectContext);
  const hasConnected = useRef(false);

  const findUser = useCallback(async (identifier) => {
    const data = await api.findUser(identifier);
    return data.success ? data.user : null;
  }, []);

  const isIdentifierAvailable = useCallback(async (identifier) => {
    const data = await api.isIdentifierAvailable(identifier);
    return data.exists === false;
  }, []);

  const logout = useCallback(async () => {
    await api.logout();
    localStorage.removeItem('starcade-token');
    triggerEffect('Toast', { message: '로그아웃되었습니다.', type: 'info' });
    setUser(null);
    if (socketRef.current) {
      socketRef.current.disconnect();
      socketRef.current = null;
    }
  }, [triggerEffect]);

  useEffect(() => {
    const sessionCheck = async () => {
      setIsLoading(true);
      const token = localStorage.getItem('starcade-token');
      if (token) {
        const data = await api.checkSession();
        if (data.success) {
            setUser(data.user);
            // Effect triggering will be handled on socket connect
          } else {
          setUser(null);
          localStorage.removeItem('starcade-token');
        }
      }
      setIsLoading(false);
    };
    sessionCheck();
  }, []); // triggerEffect removed from dependency array

  useEffect(() => {
    if (user) {
      if (!socketRef.current) {
        socketRef.current = io(config.backendUrl || window.location.origin, { 
          auth: { token: localStorage.getItem('starcade-token') }
        });
        const socket = socketRef.current;

        socket.on('connect', () => {
          socket.emit('setUserId', user.id);
          // setUser(prev => (prev ? { ...prev, connectionStatus: 'connected' } : null)); // This is now handled by statusUpdated

          if (hasConnected.current) {
            triggerEffect('Toast', { message: '서버에 다시 연결되었습니다!', type: 'success' });
          }
        });

        socket.on('disconnect', () => {
          setUser(prev => (prev ? { ...prev, connectionStatus: 'disconnected' } : null));
          triggerEffect('Toast', { message: '서버와 연결이 끊어졌습니다. 재연결 중...', type: 'error' });
        });

        const refreshUser = async () => {
          const freshUser = await findUser(user.id);
          if (freshUser) setUser(prev => ({...prev, ...freshUser}));
        };

        socket.on('friendRequestReceived', refreshUser);
        socket.on('friendAccepted', refreshUser);
        socket.on('friendRequestDeclined', refreshUser);
        socket.on('friendRemoved', refreshUser);
        socket.on('friendRequestCanceled', refreshUser);

        socket.on('statusUpdated', ({ status, connectionStatus, user: updatedUser }) => {
            if (updatedUser) {
                // This is the main state update.
                setUser({ ...updatedUser, connectionStatus });

                // Handle initial connection logic here, as we now have the freshest user data.
                if (!hasConnected.current) {
                    if (updatedUser.isBirthday && updatedUser.settings?.enableBirthdayEffect !== false) {
                        triggerEffect('Birthday');
                    }
                    // Check for any unlocks that happened offline
                    api.checkSession().then(data => {
                        if (data.success) {
                            if (data.newlyUnlockedTitles && data.newlyUnlockedTitles.length > 0) {
                                data.newlyUnlockedTitles.forEach(title => {
                                    triggerEffect('Toast', { message: `칭호 해금! [${title}]`, type: 'celebration', duration: 4000 });
                                });
                            }
                            if (data.newlyUnlockedEffects && data.newlyUnlockedEffects.length > 0) {
                                data.newlyUnlockedEffects.forEach(effect => {
                                    triggerEffect('Toast', { message: `프로필 효과 해금! [${effect}]`, type: 'celebration', duration: 4000 });
                                });
                            }
                        }
                    });
                    hasConnected.current = true;
                }
            } else { // Fallback for older status updates without the full user object
                setUser(prevUser => {
                    if (!prevUser) return null;
                    const newUserState = { ...prevUser, onlineStatus: status };
                    if (connectionStatus) {
                        newUserState.connectionStatus = connectionStatus;
                    }
                    return newUserState;
                });
            }
        });

        socket.on('friendStatusChange', ({ userId, onlineStatus, connectionStatus }) => {
            setUser(prevUser => {
              if (!prevUser || !prevUser.friends) return prevUser;
              const newFriends = prevUser.friends.map(f =>
                f.id === userId ? { ...f, onlineStatus, connectionStatus } : f
              );
              return { ...prevUser, friends: newFriends };
            });
        });

      }
    } else if (socketRef.current) {
      socketRef.current.disconnect();
      socketRef.current = null;
      hasConnected.current = false;
    }

    return () => {
      if (socketRef.current) {
        socketRef.current.off();
      }
    };
  }, [user?.id, triggerEffect, findUser]);

  const requestLoginCode = useCallback(api.requestLoginCode, []);

  const verifyLoginCode = useCallback(async (email, code) => {
    const data = await api.verifyLoginCode(email, code);
    if (data.success && data.token) {
      localStorage.setItem('starcade-token', data.token);
      setUser(data.user);
      triggerEffect('Toast', { message: `환영합니다, ${data.user.name}님!`, type: 'success' });
    }
    return data;
  }, [triggerEffect]);

  const updateUser = useCallback(async (newUserData, silent = false) => {
    if (!user) return { success: false, message: '로그인된 사용자가 없습니다.' };

    if (silent) {
      setUser(prev => ({ ...prev, ...newUserData }));
      return { success: true };
    }

    const data = await api.updateUser(user.id, newUserData);
    if (data.success) {
      setUser(prev => ({ ...prev, ...data.user }));
      triggerEffect('Toast', { message: '프로필이 업데이트되었습니다.', type: 'success' });
    } else {
      triggerEffect('Toast', { message: data.message || '업데이트 실패', type: 'error' });
    }
    return data;
  }, [user, triggerEffect]);

  const requestRegistrationCode = useCallback(api.requestRegistrationCode, []);

  const verifyRegistrationCode = useCallback(async (email, code) => {
    const data = await api.verifyRegistrationCode(email, code);
    if (data.success && data.token) {
      localStorage.setItem('starcade-token', data.token);
      setUser(data.user);
      triggerEffect('Toast', { message: `환영합니다, ${data.user.name}님! 계정이 생성되었습니다.`, type: 'success' });
    }
    return data;
  }, [triggerEffect]);

  const requestDeleteCode = useCallback(api.requestDeleteCode, []);

  const deleteAccount = useCallback(async (code, rankingAction) => {
    const data = await api.deleteAccount(code, rankingAction);
    if (data.success) {
      localStorage.removeItem('starcade-token');
      setUser(null);
      triggerEffect('Toast', { message: '계정이 성공적으로 삭제되었습니다.', type: 'success' });
    }
    return data;
  }, [triggerEffect]);

  const sendFriendRequest = useCallback(async (recipientIdentifier) => {
    if (!user) return { success: false, message: '로그인해야 친구 요청을 보낼 수 있습니다.' };
    const data = await api.sendFriendRequest(recipientIdentifier);
    if (data.success) {
      const recipient = await findUser(recipientIdentifier);
      if (recipient) {
        setUser(prev => ({ ...prev, pendingRequests: [...prev.pendingRequests, { toId: recipient.id, toName: recipient.name, toAvatar: recipient.avatar, timestamp: new Date().toISOString() }] }));
      }
    }
    return data;
  }, [user, findUser]);

  const acceptFriendRequest = useCallback(async (requestorId) => {
    if (!user) return { success: false, message: '로그인해야 합니다.' };
    const data = await api.acceptFriendRequest(requestorId);
    if (data.success) {
      triggerEffect('Toast', { message: '친구 요청을 수락했습니다.', type: 'success' });
      const freshUser = await findUser(user.id);
      if (freshUser) setUser(prev => ({...prev, ...freshUser}));
    }
    return data;
  }, [user, findUser, triggerEffect]);

  const declineFriendRequest = useCallback(async (requestorId) => {
    if (!user) return { success: false, message: '로그인해야 합니다.' };
    const data = await api.declineFriendRequest(requestorId);
    if (data.success) {
      triggerEffect('Toast', { message: '친구 요청을 거절했습니다.', type: 'info' });
      const freshUser = await findUser(user.id);
      if (freshUser) setUser(prev => ({...prev, ...freshUser}));
    }
    return data;
  }, [user, findUser, triggerEffect]);

  const removeFriend = useCallback(async (friendId) => {
    if (!user) return { success: false, message: '로그인해야 합니다.' };
    const data = await api.removeFriend(friendId);
    if (data.success) {
      triggerEffect('Toast', { message: '친구를 삭제했습니다.', type: 'info' });
      const freshUser = await findUser(user.id);
      if (freshUser) setUser(prev => ({...prev, ...freshUser}));
    }
    return data;
  }, [user, findUser, triggerEffect]);

  const cancelFriendRequest = useCallback(async (recipientId) => {
    if (!user) return { success: false, message: '로그인해야 합니다.' };
    const data = await api.cancelFriendRequest(recipientId);
    if (data.success) {
      triggerEffect('Toast', { message: '친구 요청을 취소했습니다.', type: 'info' });
      const freshUser = await findUser(user.id);
      if (freshUser) setUser(prev => ({...prev, ...freshUser}));
    }
    return data;
  }, [user, findUser, triggerEffect]);

  const deleteUserByMaster = useCallback(async (userIdToDelete, options) => {
    if (!user || !user.isMaster) return { success: false, message: '권한이 없습니다.' };
    const data = await api.deleteUserByMaster(userIdToDelete, options);
    return data;
  }, [user]);

  const value = {
    user,
    isLoggedIn: !!user,
    socket: socketRef.current,
    requestLoginCode,
    verifyLoginCode,
    logout,
    updateUser,
    findUser,
    isIdentifierAvailable,
    requestRegistrationCode,
    verifyRegistrationCode,
    requestDeleteCode,
    deleteAccount,
    sendFriendRequest,
    acceptFriendRequest,
    declineFriendRequest,
    removeFriend,
    cancelFriendRequest,
    deleteUserByMaster,
    isLoading,
  };

  return (
    <AuthContext.Provider value={value}>
      {!isLoading && children}
    </AuthContext.Provider>
  );
};