import React, { useState, useRef, useEffect } from 'react';
import { ChevronDown } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export default function Select({ options, value, onChange, className = '', buttonContent, disabled = false }) {
  const [isOpen, setIsOpen] = useState(false);
  const wrapperRef = useRef(null);

  const handleSelect = (optionValue) => {
    onChange(optionValue);
    setIsOpen(false);
  };

  const toggleDropdown = () => {
    if (disabled) return;
    setIsOpen(prev => !prev);
  };

  useEffect(() => {
    if (!isOpen) return;
    const handleClickOutside = (event) => {
      if (wrapperRef.current && !wrapperRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [isOpen]);

  const selectedLabel = options.find(option => option.value === value)?.label || '';

  return (
    <div className={`relative inline-block text-left ${className}`} ref={wrapperRef}>
      <button
        type="button"
        className={`inline-flex justify-between items-center w-full h-full rounded-xl glass px-3 py-2 text-sm font-medium text-white/90 hover:bg-white/20 focus:outline-none focus:ring-2 focus:ring-emerald-500 disabled:opacity-50 disabled:cursor-not-allowed`}
        onMouseDown={(e) => e.preventDefault()}
        onClick={toggleDropdown}
        aria-haspopup="true"
        aria-expanded={isOpen}
        disabled={disabled}
      >
        {buttonContent || (
          <>
            {selectedLabel}
            <ChevronDown className="-mr-1 ml-2 h-5 w-5" aria-hidden="true" />
          </>
        )}
      </button>

      <AnimatePresence>
        {isOpen && (
          <motion.div
            className="absolute right-0 mt-2 w-full origin-top-right rounded-xl shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none dropdown-menu-bg glass z-[10010]"
            role="menu"
            aria-orientation="vertical"
            aria-labelledby="menu-button"
            initial={{ opacity: 0, scale: 0.95, y: -10 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.95, y: -10 }}
            transition={{ duration: 0.15, ease: 'easeOut' }}
          >
            <div className="py-1 max-h-60 overflow-y-auto" role="none">
              {options.map((option) => (
                <button
                  key={option.value}
                  onClick={() => handleSelect(option.value)}
                  disabled={option.disabled}
                  className="text-white/90 block w-full text-left px-4 py-2 text-sm hover:bg-white/10 disabled:opacity-50 disabled:cursor-not-allowed"
                  role="menuitem"
                >
                  {option.render ? option.render() : option.label}
                </button>
              ))}
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}