import React from 'react'
import { createRoot } from 'react-dom/client'
import { BrowserRouter } from 'react-router-dom'
import App from './App'
import './styles/index.css'
import { EffectProvider } from './effects/EffectLayer'
import { AuthProvider } from './context/AuthContext'

const root = createRoot(document.getElementById('root'))
root.render(
  <React.StrictMode>
    <BrowserRouter basename={import.meta.env.VITE_APP_BASENAME || '/starcade'}>
      <EffectProvider> {/* EffectProvider now wraps AuthProvider */}
        <AuthProvider>
          <App />
        </AuthProvider>
      </EffectProvider>
    </BrowserRouter>
  </React.StrictMode>
)
