import React from 'react';
import Dialog from '../ui/Dialog';
import Button from '../ui/Button';
import { useAuth } from '../../context/AuthContext';
import { useEffects } from '../../effects/EffectLayer';

const MAX_DISPLAYED_ACHIEVEMENTS = 3;

export default function AchievementSlotDialog({ open, onOpenChange, achievementToDisplay, allAchievementDefs }) {
    const { user, updateUser } = useAuth();
    const { triggerEffect } = useEffects();
    const userDisplayedAchievements = user?.displayedAchievements || [];
    const userAchievements = user?.achievements || [];

    const isAlreadyDisplayed = userDisplayedAchievements.includes(achievementToDisplay?.id);

    const handleCancelDisplay = () => {
        if (!achievementToDisplay) return;
        const newDisplayed = userDisplayedAchievements.filter(id => id !== achievementToDisplay.id);
        updateUser({ displayedAchievements: newDisplayed });
        onOpenChange(false);
        triggerEffect('Toast', { message: '진열이 취소되었습니다.', type: 'info' });
    };

    const handleSelectDisplaySlot = (slotIndex) => {
        if (!achievementToDisplay) return;

        const newDisplayed = [...userDisplayedAchievements];
        const sourceId = achievementToDisplay.id;
        const targetId = newDisplayed[slotIndex]; // Can be undefined if the slot is empty

        const sourceIndex = newDisplayed.indexOf(sourceId);

        // If the source achievement was already displayed, handle the swap/move
        if (sourceIndex > -1) {
            newDisplayed[sourceIndex] = targetId; // Put the target's ID (or undefined) in the source's old spot
        } 

        // Place the source achievement in the target slot
        newDisplayed[slotIndex] = sourceId;

        // Clean up the array by filtering out empty slots, then update
        const finalDisplayed = newDisplayed.filter(Boolean);

        updateUser({ displayedAchievements: finalDisplayed });
        onOpenChange(false);
        triggerEffect('Toast', { message: '진열된 도전과제가 변경되었습니다.', type: 'success' });
    };

    return (
        <Dialog open={open} onOpenChange={() => onOpenChange(false)} title="도전과제 진열하기">
            <div className="flex flex-col items-center gap-4 p-4">
                <h3 className="text-lg font-bold">'{achievementToDisplay?.name}'을(를) 어디에 진열할까요?</h3>
                <div className="w-full space-y-3">
                    {[...Array(MAX_DISPLAYED_ACHIEVEMENTS)].map((_, index) => {
                        const displayedAchId = userDisplayedAchievements[index];
                        const achievement = displayedAchId ? allAchievementDefs.find(def => def.id === displayedAchId) : null;
                        const userAch = displayedAchId ? userAchievements.find(ua => ua.id === displayedAchId) : null;
                        const currentTier = userAch?.tier || { name: '기본', icon: '✨' };

                        return (
                            <button 
                                key={index} 
                                onClick={() => handleSelectDisplaySlot(index)}
                                className="w-full text-left p-3 bg-white/5 hover:bg-white/10 rounded-lg border border-white/10 transition-colors flex items-center gap-4"
                            >
                                <span className="font-bold text-xl">{index + 1}</span>
                                {achievement ? (
                                    <>
                                        <span className="text-2xl">{achievement.icon}</span>
                                        <div>
                                            <h4 className="font-bold">{achievement.name} {currentTier.icon && <span className="text-lg">{currentTier.icon}</span>}</h4>
                                            <p className="text-sm text-white/70">{achievement.description}</p>
                                        </div>
                                    </>
                                ) : (
                                    <span className="text-white/50">비어있는 슬롯</span>
                                )}
                            </button>
                        );
                    })}
                </div>
                <div className="flex justify-center gap-4 mt-4">
                    <Button variant="secondary" onClick={() => onOpenChange(false)}>취소</Button>
                    {isAlreadyDisplayed && (
                        <Button variant="danger" onClick={handleCancelDisplay}>진열 취소</Button>
                    )}
                </div>
            </div>
        </Dialog>
    );
}
