// index.jsx

import React, { useMemo, useEffect } from 'react';
import { motion } from 'framer-motion';
import config from './config';

// ------------------------------------------
// 부유하는 아이템 (코인, 보석, 금괴 등)
// ------------------------------------------
const FloatingItem = ({ type, duration }) => {
  const itemStyles = useMemo(() => {
    let src;
    let baseScale = 1;
    let rotationDeg = Math.random() * 360;
    let initialX, initialY, finalX, finalY;
    let delay = Math.random() * 0.5; // 등장 딜레이

    switch (type) {
      case 'coin':
        src = '🪙';
        baseScale = 0.8 + Math.random() * 0.7;
        break;
      case 'diamond':
        src = '💎';
        baseScale = 1.0 + Math.random() * 0.8;
        break;
      case 'money':
        src = '💰';
        baseScale = 0.9 + Math.random() * 0.6;
        break;
      case 'dollar':
        src = '💸';
        baseScale = 1.1 + Math.random() * 0.9;
        break;
      default:
        src = '👑';
        baseScale = 0.5 + Math.random() * 0.5;
    }

    const centerX = window.innerWidth / 2;
    const centerY = window.innerHeight / 2;
    const angle = Math.random() * Math.PI * 2;
    const initialRadius = 50 + Math.random() * 100;
    const finalRadius = Math.max(window.innerWidth, window.innerHeight) * (0.8 + Math.random() * 0.5);

    initialX = centerX + Math.cos(angle) * initialRadius;
    initialY = centerY + Math.sin(angle) * initialRadius;
    finalX = centerX + Math.cos(angle) * finalRadius;
    finalY = centerY + Math.sin(angle) * finalRadius;

    return { src, baseScale, rotationDeg, initialX, initialY, finalX, finalY, delay };
  }, [type]);

  return (
    <motion.div
      className="money-floating-item"
      initial={{
        x: itemStyles.initialX,
        y: itemStyles.initialY,
        scale: 0,
        opacity: 0,
        rotate: 0,
      }}
      animate={{
        x: itemStyles.finalX,
        y: itemStyles.finalY,
        scale: [0, itemStyles.baseScale * 1.2, itemStyles.baseScale, itemStyles.baseScale * 0.5],
        opacity: [0, 1, 1, 0],
        rotate: itemStyles.rotationDeg + (Math.random() > 0.5 ? 360 : -360) * (1 + Math.random()),
      }}
      transition={{
        duration: duration / 1000 * (1 + Math.random() * 0.5),
        delay: itemStyles.delay,
        ease: "easeOut",
        times: [0, 0.1, 0.8, 1],
      }}
      style={{
        position: 'absolute',
        fontSize: `${30 * itemStyles.baseScale}px`,
        lineHeight: 1,
        pointerEvents: 'none',
        // textShadow는 렌더링 비용이 매우 비싸므로 성능을 위해 제거하거나,
        // 아래 style.css에서처럼 클래스로 대체하여 부담을 줄입니다.
        // textShadow: `0 0 10px rgba(255, 255, 0, 0.8), 0 0 20px rgba(255, 165, 0, 0.6)`
        
        // 브라우저에 변경될 속성을 미리 알려주어 GPU 가속을 유도합니다.
        willChange: 'transform, opacity',
      }}
    >
      {itemStyles.src}
    </motion.div>
  );
};

// ------------------------------------------
// 메인 Money 컴포넌트
// ------------------------------------------
const Money = () => {
  const { duration, bodyClassName, itemCount } = config;

  useEffect(() => {
    const body = document.body;
    if (bodyClassName) {
      body.classList.add(bodyClassName);
      const fadeOutTimer = setTimeout(() => {
        body.classList.remove(bodyClassName);
      }, duration - 1500);

      return () => {
        clearTimeout(fadeOutTimer);
        body.classList.remove(bodyClassName);
      };
    }
  }, [duration, bodyClassName]);

  const itemsToGenerate = useMemo(() => {
    const items = [];
    const types = ['coin', 'diamond', 'money', 'dollar'];
    for (let i = 0; i < itemCount; i++) { // config에서 정의한 개수만큼 생성
      items.push(types[Math.floor(Math.random() * types.length)]);
    }
    return items;
  }, [itemCount]);

  return (
    <motion.div
      className="money-overlay"
      initial={{ opacity: 0 }}
      animate={{ opacity: [0, 1, 1, 0] }}
      transition={{ duration: duration / 1000, times: [0, 0.1, 0.9, 1] }}
    >
      <motion.div
        className="money-flash"
        initial={{ opacity: 0, scale: 0 }}
        animate={{ opacity: [0, 1, 0.5, 0], scale: [0, 1, 1.2, 1.5] }}
        transition={{ duration: 0.8, ease: "easeOut", times: [0, 0.2, 0.5, 1] }}
      />
      {itemsToGenerate.map((type, index) => (
        <FloatingItem key={index} type={type} duration={duration} />
      ))}
    </motion.div>
  );
};

export default Money;