import React, { useEffect, useRef, useState, useCallback } from 'react';
import Button from '../components/ui/Button';
import Celebration from '../components/Celebration';
import { motion, AnimatePresence } from 'framer-motion';
import PauseOverlay from '../components/PauseOverlay';
import { useEffects } from '../effects/EffectLayer';
import { useAuth } from '../context/AuthContext';
import { submitScore } from '../lib/scoreManager';
import { notifyGameOver } from '../api/starcadeApi';
import SubmitScoreDialog from '../components/ui/SubmitScoreDialog';

export const meta = { id:'g2048', name:'2048', icon:'🧮', topic:'g2048', description:'타일 합치기', category: 'single-player' };

const GAME_ID = meta.id;

const range = (n) => Array.from({ length: n }, (_, i) => i);
const sample = (a) => a[rnd(a.length)];
const rnd = (n) => Math.floor(Math.random() * n);

function useSwipe(onDir, isPaused) {
  const start = useRef(null);
  useEffect(() => {
    if (isPaused) return;
    const onTouchStart = (e) => (start.current = { x: e.touches[0].clientX, y: e.touches[0].clientY });
    const onTouchEnd = (e) => {
      if (!start.current) return;
      const dx = e.changedTouches[0].clientX - start.current.x;
      const dy = e.changedTouches[0].clientY - start.current.y;
      const ax = Math.abs(dx), ay = Math.abs(dy);
      if (Math.max(ax, ay) < 20) return;
      if (ax > ay) onDir(dx > 0 ? 'right' : 'left'); else onDir(dy > 0 ? 'down' : 'up');
      start.current = null;
    };
    window.addEventListener('touchstart', onTouchStart, { passive: true });
    window.addEventListener('touchend', onTouchEnd, { passive: true });
    return () => { window.removeEventListener('touchstart', onTouchStart); window.removeEventListener('touchend', onTouchEnd) };
  }, [onDir, isPaused]);
}

export default function G2048({ isPaused, lastKeyEvent }) {
    const size = 4;
    const nextId = useRef(0);
    const keyProcessedRef = useRef(null);
    const mountTimeRef = useRef(Date.now());
    const { triggerEffect } = useEffects();
    const { user, updateUser } = useAuth();
    const [showSubmitScoreDialog, setShowSubmitScoreDialog] = useState(false);
    const [showCelebration, setShowCelebration] = useState(false);

    const spawn = (boardArray, currentMaxTile) => {
        const newBoard = boardArray.map(tile => tile ? {...tile, isNew: false, isMerged: false} : null);
        const emptyCells = [];
        newBoard.forEach((tile, index) => {
            if (tile === null) emptyCells.push(index)
        });

        if (emptyCells.length === 0) return { board: newBoard, maxTile: currentMaxTile };

        const maxTileInBoard = Math.max(2, ...newBoard.filter(t => t).map(t => t.value));
        const maxNewValue = Math.max(4, maxTileInBoard / 16);
        const possibleValues = [2, 4, 8, 16, 32, 64, 128, 256, 512].filter(v => v <= maxNewValue);
        const newValue = sample(possibleValues) || 2;

        const randomIndex = sample(emptyCells);
        newBoard[randomIndex] = { id: nextId.current++, value: newValue, isNew: true, isMerged: false };
        
        return { board: newBoard, maxTile: Math.max(currentMaxTile, newValue) };
    };
    
    const initialSpawn = (board) => {
        let newBoard = [...board];
        for(let i=0; i<2; i++) {
            const emptyCells = [];
            newBoard.forEach((tile, index) => { if (tile === null) emptyCells.push(index) });
            if(emptyCells.length === 0) break;
            const randomIndex = sample(emptyCells);
            newBoard[randomIndex] = { id: nextId.current++, value: 2, isNew: true, isMerged: false };
        }
        return newBoard;
    }

    const getInitialState = () => {
        try {
            const savedState = localStorage.getItem(`starcade-gamestate-${GAME_ID}`);
            if (savedState) {
                const parsed = JSON.parse(savedState);
                if (parsed.board && typeof parsed.score === 'number') {
                    nextId.current = Math.max(...parsed.board.filter(t => t).map(t => t.id)) + 1;
                    return parsed;
                }
            }
        } catch (e) {
            console.error("Failed to load saved game state", e);
        }
        return {
            board: initialSpawn(Array(size * size).fill(null)),
            score: 0,
            hasWon: false,
            isGameOver: false,
            maxTileValue: 0,
        };
    };

    const [gameState, setGameState] = useState(getInitialState);

    useEffect(() => {
        if (!gameState.isGameOver && checkGameOver(gameState.board)) {
            setGameState(prev => ({ ...prev, isGameOver: true }));
            setShowSubmitScoreDialog(true);
            triggerEffect('Defeat');
        }
    }, []);

    useEffect(() => {
        try {
            localStorage.setItem(`starcade-gamestate-${GAME_ID}`, JSON.stringify(gameState));
        } catch (e) {
            console.error("Failed to save game state", e);
        }
    }, [gameState]);

    const restartGame = () => {
        localStorage.removeItem(`starcade-gamestate-${GAME_ID}`);
        nextId.current = 0;
        setGameState({
            board: initialSpawn(Array(size * size).fill(null)),
            score: 0,
            hasWon: false,
            isGameOver: false,
            maxTileValue: 0,
        });
        setShowSubmitScoreDialog(false);
    };

    const tileColors = {
        2: 'bg-gray-700', 4: 'bg-gray-600', 8: 'bg-orange-500', 16: 'bg-orange-600',
        32: 'bg-red-500', 64: 'bg-red-600', 128: 'bg-yellow-500', 256: 'bg-yellow-600',
        512: 'bg-green-500', 1024: 'bg-green-600', 2048: 'bg-blue-500', 4096: 'bg-blue-600',
    };

    const getTileClass = (v) => {
        const colorClass = tileColors[v] || 'bg-purple-500';
        const sizeClass = v > 1000 ? 'text-3xl' : 'text-4xl';
        return `rounded-xl h-16 w-16 grid place-items-center font-extrabold border border-white/10 ${colorClass} ${sizeClass}`;
    };

    const compressLine = (line, currentMaxTile) => {
        const tiles = line.filter(t => t);
        const newTiles = [];
        let scoreGained = 0;
        let maxTileInLine = currentMaxTile;

        for (let i = 0; i < tiles.length; i++) {
            if (i + 1 < tiles.length && tiles[i].value === tiles[i+1].value) {
                const newValue = tiles[i].value * 2;
                scoreGained += newValue;
                newTiles.push({ ...tiles[i+1], value: newValue, isMerged: true });
                if (newValue > maxTileInLine) maxTileInLine = newValue;
                i++;
            } else {
                newTiles.push(tiles[i]);
            }
        }
    
        const newLine = Array(size).fill(null);
        newTiles.forEach((t, i) => { newLine[i] = t; });
        return { newLine, scoreGained, maxTileInLine };
    }

    const checkGameOver = (currentBoard) => {
        if (currentBoard.some(tile => tile === null)) return false;

        for (let r = 0; r < size; r++) {
            for (let c = 0; c < size; c++) {
                const current = currentBoard[r * size + c];
                if (c + 1 < size && current.value === currentBoard[r * size + (c + 1)].value) return false;
                if (r + 1 < size && current.value === currentBoard[(r + 1) * size + c].value) return false;
            }
        }
        return true;
    };

    const move = useCallback((dir) => {
        if (gameState.isGameOver) return;

        let currentBoard = gameState.board.map(tile => tile ? { ...tile } : null);
        let totalGained = 0;
        let currentMaxTile = gameState.maxTileValue;
        const boardBefore = JSON.stringify(currentBoard);

        if (dir === 'left' || dir === 'right') {
            for (let r = 0; r < size; r++) {
                const row = [];
                for (let c = 0; c < size; c++) { row.push(currentBoard[r * size + c]); }
                if (dir === 'right') row.reverse();
                const { newLine, scoreGained, maxTileInLine } = compressLine(row, currentMaxTile);
                if (dir === 'right') newLine.reverse();
                totalGained += scoreGained;
                currentMaxTile = Math.max(currentMaxTile, maxTileInLine);
                for (let c = 0; c < size; c++) { currentBoard[r * size + c] = newLine[c]; }
            }
        } else {
            for (let c = 0; c < size; c++) {
                const col = [];
                for (let r = 0; r < size; r++) { col.push(currentBoard[r * size + c]); }
                if (dir === 'down') col.reverse();
                const { newLine, scoreGained, maxTileInLine } = compressLine(col, currentMaxTile);
                if (dir === 'down') newLine.reverse();
                totalGained += scoreGained;
                currentMaxTile = Math.max(currentMaxTile, maxTileInLine);
                for (let r = 0; r < size; r++) { currentBoard[r * size + c] = newLine[r]; }
            }
        }

        const boardAfter = JSON.stringify(currentBoard);
        if (boardBefore !== boardAfter) {
            const { board: finalBoard, maxTile: newMaxTile } = spawn(currentBoard, currentMaxTile);
            
            setGameState(prev => ({
                ...prev,
                board: finalBoard,
                score: prev.score + totalGained,
                maxTileValue: newMaxTile,
            }));

            if (!gameState.hasWon && finalBoard.some(tile => tile && tile.value === 2048)) {
                setShowCelebration(true);
                setGameState(prev => ({ ...prev, hasWon: true }));
                triggerEffect('Confetti');
            }

            if (checkGameOver(finalBoard)) {
                setShowSubmitScoreDialog(true);
                triggerEffect('Defeat');
            }
        }
    }, [gameState, size, triggerEffect]);

    const handleSubmitScore = () => {
        const scoreData = {
            gameId: meta.id,
            score: gameState.score,
            user: user,
            options: { maxTile: gameState.maxTileValue }
        };
        submitScore(scoreData, triggerEffect, updateUser);
        setShowSubmitScoreDialog(false);
        setGameState(prev => ({ ...prev, isGameOver: true }));
    };

    const handleCloseSubmitDialog = () => {
        if (user) {
            notifyGameOver({
                gameId: meta.id,
                score: gameState.score,
                userId: user.id
            }).then(result => {
                if (result.success && result.user) {
                    updateUser(result.user, true);
                }
            });
        }
        setShowSubmitScoreDialog(false);
        setGameState(prev => ({ ...prev, isGameOver: true }));
    }

    useEffect(() => {
        if (!lastKeyEvent || isPaused) return;
        if (Date.now() - mountTimeRef.current < 500) return;

        if (keyProcessedRef.current === lastKeyEvent.time) {
          return;
        }
        keyProcessedRef.current = lastKeyEvent.time;

        const { key } = lastKeyEvent;
        
        if (['ArrowLeft', 'a', 'A'].includes(key)) move('left');
        if (['ArrowRight', 'd', 'D'].includes(key)) move('right');
        if (['ArrowUp', 'w', 'W'].includes(key)) move('up');
        if (['ArrowDown', 's', 'S'].includes(key)) move('down');
    }, [lastKeyEvent, isPaused, move]);

    useSwipe(move, isPaused);

    const TILE_SIZE = 64;
    const GAP_SIZE = 12;
    const BOARD_SIZE = (TILE_SIZE * size) + (GAP_SIZE * (size - 1));

    const tileVariants = {
        hidden: { opacity: 0, scale: 0.5 },
        visible: { opacity: 1, scale: 1, transition: { duration: 0.15 } },
        spawn: {
            opacity: 1,
            scale: [0.7, 1.1, 1],
            transition: { type: "spring", stiffness: 400, damping: 15, duration: 0.3 }
        },
        merged: {
            opacity: 1,
            scale: [1, 1.2, 1],
            transition: { duration: 0.2, ease: "easeInOut" }
        }
    };

    return (
        <div className="flex flex-col items-center gap-4 relative">
            <div className="text-sm text-white/70">점수: {gameState.score}</div>
            <div className="rounded-3xl p-3 bg-white/5 border border-white/10">
                <div className="relative" style={{ width: BOARD_SIZE, height: BOARD_SIZE }}>
                    {range(size * size).map(i => (
                        <div key={`bg-${i}`} className="absolute rounded-xl h-16 w-16 bg-white/5"
                             style={{
                                 top: Math.floor(i / size) * (TILE_SIZE + GAP_SIZE),
                                 left: (i % size) * (TILE_SIZE + GAP_SIZE),
                             }}
                        />
                    ))}

                    <AnimatePresence>
                        {gameState.board.map((tile, i) => {
                            if (tile === null) return null;
                            const y = Math.floor(i / size) * (TILE_SIZE + GAP_SIZE);
                            const x = (i % size) * (TILE_SIZE + GAP_SIZE);
                            
                            let animateState = tile.isNew ? "spawn" : tile.isMerged ? "merged" : "visible";

                            return (
                                <motion.div
                                    key={tile.id}
                                    layout
                                    variants={tileVariants}
                                    initial="hidden"
                                    animate={{ x, y, ...tileVariants[animateState] }}
                                    exit="hidden"
                                    className={`absolute ${getTileClass(tile.value)}`}
                                >
                                    {tile.value}
                                </motion.div>
                            );
                        })}
                    </AnimatePresence>

                    {gameState.isGameOver && (
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            className="absolute inset-0 grid place-items-center rounded-xl bg-black/50 backdrop-blur-sm"
                        >
                            <div className="text-center">
                                <h2 className="text-5xl font-extrabold text-white mb-4">Game Over</h2>
                                <Button onClick={restartGame}>다시 시작</Button>
                            </div>
                        </motion.div>
                    )}
                </div>
            </div>
            <div className="grid grid-cols-3 gap-1 w-36">
                <div></div>
                <Button variant="secondary" onClick={() => move('up')}>▲</Button>
                <div></div>
                <Button variant="secondary" onClick={() => move('left')}>◀</Button>
                <div className="h-12 w-12"></div>
                <Button variant="secondary" onClick={() => move('right')}>▶</Button>
                <div></div>
                <Button variant="secondary" onClick={() => move('down')}>▼</Button>
                <div></div>
            </div>
            <Celebration show={showCelebration} message="🎉 2048 달성! 🎉" onComplete={() => setShowCelebration(false)} />
            <PauseOverlay isPaused={isPaused && window.innerWidth < 768} />

            <SubmitScoreDialog
                open={showSubmitScoreDialog}
                onOpenChange={setShowSubmitScoreDialog}
                score={gameState.score}
                user={user}
                onSubmit={handleSubmitScore}
                onClose={handleCloseSubmitDialog}
            />
        </div>
    )
}
