import React, { Suspense } from 'react';
import { useOutletContext, useParams } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import GlassCard from '../components/ui/GlassCard';
import Button from '../components/ui/Button';
import { RefreshCcw } from 'lucide-react';
import PauseOverlay from '../components/PauseOverlay';
import MultiplayerGameDetail from './MultiplayerGameDetail'; // Import the new component

function Splash() {
    return <div className="grid place-items-center min-h-[40vh]"><div className="animate-pulse text-white/70">로딩 중…</div></div>;
}

export default function GamePage() {
    const { game, handleRefresh, gameInstance, isGamePaused, lastKeyEvent } = useOutletContext();
    const { roomId } = useParams(); // Get roomId from URL

    // If it's a multiplayer game but there's no roomId, show the detail/lobby page for that game.
    if (game && game.category === 'multi-player' && !roomId) {
        return <MultiplayerGameDetail game={game} />;
    }

    return (
        <>
            {game ? (
                <GlassCard title={
                    <div className="flex justify-between items-center">
                        <div className="flex items-center gap-2">
                            <span className="text-2xl">{game.icon}</span>
                            <span>{game.name}</span>
                        </div>
                        <Button variant="secondary" className="p-2 h-10 w-10 flex items-center justify-center" onClick={handleRefresh}>
                            <RefreshCcw className="w-5 h-5" />
                        </Button>
                    </div>
                } className="h-full flex flex-col">
                    <div className="flex-grow p-4 flex flex-col items-center justify-center relative">
                        <Suspense fallback={<Splash />}>
                            <game.Component game={game} key={gameInstance} isPaused={isGamePaused} lastKeyEvent={lastKeyEvent} />
                        </Suspense>
                        <PauseOverlay isPaused={isGamePaused} />
                    </div>
                </GlassCard>
            ) : (
                <GlassCard title="환영합니다!" className="h-full flex flex-col items-center justify-center">
                    <p className="text-white/70">플레이할 게임을 선택해주세요.</p>
                </GlassCard>
            )}
        </>
    );
}
