import React, { useState, useEffect } from 'react';
import Dialog from '../ui/Dialog';
import Button from '../ui/Button';

export default function DeleteAccountDialog({ open, onOpenChange, user, requestDeleteCode, deleteAccount, triggerEffect, logout }) {
  const [step, setStep] = useState('request_code');
  const [email, setEmail] = useState(user?.email || '');
  const [code, setCode] = useState('');
  const [error, setError] = useState('');
  const [isRequesting, setIsRequesting] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [rankingAction, setRankingAction] = useState('rename');

  useEffect(() => {
    if (!open) {
      // Reset state when dialog is closed
      setTimeout(() => {
        setStep('request_code');
        setEmail(user?.email || '');
        setCode('');
        setError('');
        setIsRequesting(false);
        setIsDeleting(false);
        setRankingAction('rename');
      }, 300); // Allow exit animation to complete
    }
  }, [open, user]);

  const handleRequestCode = async () => {
    setError('');
    setIsRequesting(true);
    const result = await requestDeleteCode();
    setIsRequesting(false);
    if (result.success) {
      setStep('verify_code');
      triggerEffect('Toast', { message: result.message, type: 'success' });
    } else {
      setError(result.message);
      triggerEffect('Toast', { message: result.message, type: 'error' });
    }
  };

  const handleDeleteAccount = async () => {
    setError('');
    setIsDeleting(true);
    const result = await deleteAccount(code, rankingAction);
    setIsDeleting(false);
    if (result.success) {
      triggerEffect('Toast', { message: result.message, type: 'success' });
      onOpenChange(false);
      logout();
    } else {
      setError(result.message);
      triggerEffect('Toast', { message: result.message, type: 'error' });
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange} title="계정 삭제">
      {step === 'request_code' ? (
        <div className='flex flex-col gap-4'>
          <p className='text-white/70'>계정을 삭제하려면 가입 시 사용한 이메일로 인증 코드를 받아야 합니다. 계속하시겠습니까?</p>
          {error && <p className='text-red-400 text-sm'>{error}</p>}
          <Button onClick={handleRequestCode} disabled={isRequesting}>
            {isRequesting ? '전송 중...' : '인증 코드 요청'}
          </Button>
        </div>
      ) : (
        <div className='flex flex-col gap-4'>
          <p className='text-white/70'>이메일로 전송된 인증 코드를 입력하세요.</p>
          <input
            type='text'
            value={code}
            onChange={(e) => setCode(e.target.value)}
            placeholder='인증 코드'
            className='w-full px-3 py-2 rounded-xl bg-black/20 border border-white/10 focus:border-emerald-500 focus:ring-emerald-500/50 focus:ring-1 outline-none transition-colors'
            disabled={isDeleting}
          />
          <div className="my-2 text-left text-sm">
              <p className="font-semibold mb-2 text-white/80">랭킹 기록 처리:</p>
              <div className="flex flex-col gap-2">
                  <label htmlFor="ranking-action-rename" className="flex items-center gap-2 p-2 rounded-lg bg-black/20 hover:bg-black/30 cursor-pointer">
                      <input id="ranking-action-rename" type="radio" name="rankingAction" value="rename" checked={rankingAction === 'rename'} onChange={(e) => setRankingAction(e.target.value)} className="form-radio h-4 w-4 text-emerald-600 bg-gray-700 border-gray-600 focus:ring-emerald-500" />
                      <span className="text-white/80">랭킹에서 '삭제된 계정'으로 변경</span>
                  </label>
                  <label htmlFor="ranking-action-delete" className="flex items-center gap-2 p-2 rounded-lg bg-black/20 hover:bg-black/30 cursor-pointer">
                      <input id="ranking-action-delete" type="radio" name="rankingAction" value="delete" checked={rankingAction === 'delete'} onChange={(e) => setRankingAction(e.target.value)} className="form-radio h-4 w-4 text-emerald-600 bg-gray-700 border-gray-600 focus:ring-emerald-500" />
                      <span className="text-white/80">랭킹에서 완전히 삭제</span>
                  </label>
              </div>
          </div>
          {error && <p className='text-red-400 text-sm'>{error}</p>}
          <Button onClick={handleDeleteAccount} disabled={isDeleting}>
            {isDeleting ? '삭제 중...' : '계정 삭제'}
          </Button>
        </div>
      )}
      <div className='flex justify-end gap-2 mt-4'>
        <Button variant='secondary' onClick={() => onOpenChange(false)} disabled={isRequesting || isDeleting}>취소</Button>
      </div>
    </Dialog>
  );
}