import React, { useState, useEffect } from 'react';
import Button from '../components/ui/Button';
import Select from '../components/ui/Select';
import { useEffects } from '../effects/EffectLayer';
import { fetchProfileEffects } from '../api/starcadeApi';

export default function EffectDemo() {
  const { triggerEffect, activeEffectCount, stopAllEffects } = useEffects();
  const [availableEffectNames, setAvailableEffectNames] = useState([]);
  const [selectedEffect, setSelectedEffect] = useState('');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const loadProfileEffects = async () => {
      try {
        const data = await fetchProfileEffects();
        if (data.success) {
          const effectsList = data.profileEffects.map(effect => ({
            value: effect.id,
            label: effect.name,
          }));
          setAvailableEffectNames(effectsList);
          if (effectsList.length > 0) {
            setSelectedEffect(effectsList[0].value);
          }
        } else {
          console.error("Failed to fetch profile effects:", data.message);
        }
      } catch (error) {
        console.error("Error fetching profile effects:", error);
      } finally {
        setIsLoading(false);
      }
    };
    loadProfileEffects();
  }, []);

  const isPlaying = activeEffectCount > 0;

  const handlePlayEffect = () => {
    if (selectedEffect) {
      if (selectedEffect === 'Toast') {
        triggerEffect('Toast', { message: 'Toast 이펙트는 이렇게 표시됩니다!', type: 'info' });
      } else {
        triggerEffect(selectedEffect);
      }
    }
  };

  const handleButtonClick = () => {
    if (isPlaying) {
      stopAllEffects();
    } else {
      handlePlayEffect();
    }
  };

  if (isLoading) {
    return (
      <div className="flex flex-col items-center justify-center gap-4 p-4 text-white/70 min-h-[200px]">
        <div className="animate-pulse">Loading Effects...</div>
      </div>
    );
  }

  if (availableEffectNames.length === 0) {
    return (
      <div className="flex flex-col items-center gap-4 p-4 text-white/70">
        <h2 className="text-xl font-bold text-white">이펙트 미리보기</h2>
        <p>표시할 이펙트가 없습니다.</p>
        <p className="text-xs">`src/effects/patterns` 폴더에 이펙트 폴더를 추가하세요.</p>
      </div>
    )
  }

  return (
    <div className="flex flex-col items-center gap-4 p-4">
      <h2 className="text-xl font-bold text-white">이펙트 미리보기</h2>
      <div className="flex items-center gap-2">
        <Select
          options={availableEffectNames}
          value={selectedEffect}
          onChange={setSelectedEffect}
          className="w-48"
          disabled={isPlaying}
        />
        <Button onClick={handleButtonClick} variant={isPlaying ? 'danger' : 'primary'}>
          {isPlaying ? '중지' : '재생'}
        </Button>
      </div>
      <p className="text-sm text-white/70">선택한 이펙트를 화면에 재생합니다.</p>
    </div>
  );
}