import React, { useEffect } from 'react';
import config from './config.js';

export default function ScreenShake({ duration }) {
  const ms = duration ?? config.duration; // 500ms와 동일하게
  useEffect(() => {
    const target = document.querySelector('.arcade-machine') || document.getElementById('root');
    if (!target) return;

    target.style.setProperty('--shake-duration', `${ms}ms`);
    target.classList.add(config.rootClassName);

    const t = setTimeout(() => {
      target.classList.remove(config.rootClassName);
      target.style.removeProperty('--shake-duration');
    }, ms + 16); // 한 프레임 버퍼

    return () => {
      clearTimeout(t);
      target.classList.remove(config.rootClassName);
      target.style.removeProperty('--shake-duration');
    };
  }, [ms]);

  return null;
}
