import React, { useEffect, useState, useCallback } from 'react';

import config from '../config';

export default function useMessageRotator({ topic, messages, intervalMs = 5000 }) {
    const [msg, setMsg] = useState('...');
    const [animationState, setAnimationState] = useState('idle'); // idle, playing, finished

    const getNextMessage = useCallback(async () => {
        if (messages && messages.length > 0) {
            const randomIndex = Math.floor(Math.random() * messages.length);
            setMsg(messages[randomIndex]);
            setAnimationState('playing'); // Change state to start animation
            return;
        }

        try {
            let url = `${config.backendUrl}/api/messages`;
            if (topic) {
                url += `?topic=${encodeURIComponent(topic)}`;
            }
            
            const response = await fetch(url);
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            const data = await response.json();

            if (data.success) {
                setMsg(data.message);
            } else {
                setMsg('게임을 즐겨주세요!');
            }
        } catch (error) {
            console.error('Failed to fetch message:', error);
            setMsg('게임을 즐겨주세요!');
        } finally {
            setAnimationState('playing'); // Change state to start animation
        }
    }, [topic, messages]);

    useEffect(() => {
        // This effect now only triggers when the animation completes.
        if (animationState === 'finished') {
            getNextMessage();
        }
    }, [animationState, getNextMessage]);

    useEffect(() => {
        // Initial message fetch
        getNextMessage();
    }, [getNextMessage]);

    return { msg, setAnimationState };
}
