import React, { useEffect } from 'react';
import { motion } from 'framer-motion';
import '../crt-styles.css';
import config from './config'; // duration(ms) 사용

const CRToff = () => {
  const { duration } = config;
  const d = duration / 1000; // sec

  return (
    <motion.div className="crt-off-effect" initial={{ opacity: 1 }} animate={{ opacity: 1 }}>
      {/* 1) 세로 수축 (투명 배경) */}
      <motion.div
        className="crt-v-collapse"
        initial={{ scaleY: 1, scaleX: 1 }}
        animate={{ scaleY: 0.0, scaleX: 1 }}
        transition={{ duration: d * 0.55, ease: [0.4, 0.0, 0.2, 1] }}
      />

      {/* 2) 중앙 가로선 (끝까지 뻗게) */}
      <motion.div
        className="crt-h-line"
        initial={{ opacity: 0, scaleX: 1 }}
        animate={{
          opacity: [0, 1, 1, 0],
          scaleX: [1, 1, 0, 0],
        }}
        transition={{
          duration: d * 0.45,
          delay: d * 0.48,
          times: [0, 0.1, 0.75, 1],
          ease: 'easeInOut',
        }}
      />

      {/* 3) 밝은 점 번쩍 */}
      <motion.div
        className="crt-bright-dot"
        initial={{ scale: 0, opacity: 0 }}
        animate={{
          scale: [0, 0.8, 2.8, 0.15, 1.8, 0],
          opacity: [0, 0.85, 1, 0.7, 1, 0],
        }}
        transition={{
          duration: d,
          times: [0, 0.55, 0.7, 0.78, 0.9, 1],
          ease: 'easeInOut',
        }}
      />

      {/* 마지막 블랙아웃: 섬광 끝과 동시에 바로 덮음 */}
      <motion.div
        className="crt-blackout"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ 
          duration: 0.05,    // 빠르게 퍽! 하고 덮음
          delay: d * 0.7,    // 섬광의 마지막 구간 시작점과 맞춤
          ease: "easeIn"
        }}
      />
    </motion.div>
  );
};

export default CRToff;
