import React, { useEffect, useMemo, useState, useRef, useCallback } from 'react';
import Button from '../components/ui/Button';
import Dialog from '../components/ui/Dialog';
import { motion, useAnimation } from 'framer-motion';
import PauseOverlay from '../components/PauseOverlay';
import { useEffects } from '../effects/EffectLayer';
import { useAuth } from '../context/AuthContext';
import Reel from './Reel';
import SubmitScoreDialog from '../components/ui/SubmitScoreDialog';
import { submitScore } from '../lib/scoreManager';
import { notifyGameOver } from '../api/starcadeApi';

export const meta = { id:'slot', name:'슬롯머신', icon:'🎰', topic:'slot', description:'가벼운 운빨', category: 'single-player' };

const GAME_ID = meta.id;
const ALL_SYMBOLS_WEIGHTED = [
  { symbol: '🍒', weight: 19.4, name: '체리' },
  { symbol: '🍋', weight: 19.4, name: '레몬' },
  { symbol: '🍀', weight: 14.9, name: '클로버' },
  { symbol: '🔔', weight: 14.9, name: '종' },
  { symbol: '💎', weight: 11.9, name: '다이아몬드' },
  { symbol: '💰', weight: 11.9, name: '코인' },
  { symbol: '7️⃣', weight: 7.5, name: '세븐' },
];
const ALL_SYMBOLS = ALL_SYMBOLS_WEIGHTED.map(s => s.symbol);
const TOTAL_WEIGHT = ALL_SYMBOLS_WEIGHTED.reduce((sum, s) => sum + s.weight, 0);
const getWeightedRandomSymbol = () => {
  const randomNum = Math.random() * TOTAL_WEIGHT;
  let weightSum = 0;
  for (const symbolData of ALL_SYMBOLS_WEIGHTED) {
    weightSum += symbolData.weight;
    if (randomNum <= weightSum) {
      return symbolData.symbol;
    }
  }
  return ALL_SYMBOLS_WEIGHTED[0].symbol;
};
const NUM_REELS = 5;
const NUM_ROWS = 3;
const FIXED_PATTERNS = {
  '지그': { name: '지그', multiplier: 4.0, indices: [[0,2], [1,1], [1,3], [2,0], [2,4]] },
  '재그': { name: '재그', multiplier: 4.0, indices: [[0,0], [0,4], [1,1], [1,3], [2,2]] },
  '위': { name: '위', multiplier: 7.0, indices: [[0,2], [1,1], [1,3], [2,0], [2,1], [2,2], [2,3], [2,4]] },
  '아래': { name: '아래', multiplier: 7.0, indices: [[0,0], [0,1], [0,2], [0,3], [0,4], [1,1], [1,3], [2,2]] },
  '눈': { name: '눈', multiplier: 8.0, indices: [[0,1],[0,2],[0,3], [1,0],[1,1],[1,3],[1,4], [2,1],[2,2],[2,3]] },
  '잭팟': { name: '잭팟', multiplier: 10.0, indices: Array.from({ length: 15 }, (_, i) => [Math.floor(i / 5), i % 5]) },
};
const symbolPayouts = {
  '🍒': 2, '🍋': 2, '🍀': 3, '🔔': 3, '💎': 5, '💰': 5, '7️⃣': 7,
};
const INITIAL_COINS = 1000;
const INITIAL_BET = 10;

const generateSpinDurations = () =>
  Array(NUM_REELS).fill(0).map(() => 3.5 + Math.random() * 1.0);

const generateBankruptcySpinDurations = () =>
  Array(NUM_REELS).fill(0).map(() => 2.8 + Math.random() * 0.6);

export function SlotMachine({ isMenuOpen, lastKeyEvent }) {
  const { user, updateUser } = useAuth();
  const initialReels = useMemo(() => Array(NUM_REELS).fill(0).map(() => Array(NUM_ROWS).fill('🍀')), []);

  const getInitialState = () => {
      try {
          const savedState = localStorage.getItem(`starcade-gamestate-${GAME_ID}`);
          if (savedState) {
              const parsed = JSON.parse(savedState);
              if (parsed.reels && typeof parsed.coins === 'number') {
                  return parsed;
              }
          }
      } catch (e) {
          console.error("Failed to load saved game state", e);
      }
      return {
          reels: initialReels,
          coins: INITIAL_COINS,
          bet: INITIAL_BET,
          isBankrupt: false,
          isReset: false,
      };
  };

  const [gameState, setGameState] = useState(getInitialState);
  const [visibleReels, setVisibleReels] = useState(gameState.reels);
  const [reelsSymbolLists, setReelsSymbolLists] = useState(() => Array(NUM_REELS).fill(ALL_SYMBOLS));
  const [spinning, setSpinning] = useState(false);
  const [message, setMessage] = useState("스핀하세요!");
  const [showPayouts, setShowPayouts] = useState(false);
  const [highlightedCells, setHighlightedCells] = useState([]);
  const winIntervalRef = useRef(null);
  const keyProcessedRef = useRef(null);
  const { triggerEffect, stopAllEffects } = useEffects();
  const [isBankrupting, setIsBankrupting] = useState(false);
  const [isResetting, setIsResetting] = useState(false);
  const [showSubmitScoreDialog, setShowSubmitScoreDialog] = useState(false);
  const [showBankruptcySubmitDialog, setShowBankruptcySubmitDialog] = useState(false);
  const isBankruptcyInProgress = useRef(false);
  const betBeforeFocus = useRef(INITIAL_BET);
  const reelContainerControls = useAnimation();
  const [spinDurations, setSpinDurations] = useState(generateSpinDurations);

  useEffect(() => {
      if (spinning || isBankrupting || isResetting) return;
      try {
          localStorage.setItem(`starcade-gamestate-${GAME_ID}`, JSON.stringify(gameState));
      } catch (e) {
          console.error("Failed to save game state", e);
      }
  }, [gameState, spinning, isBankrupting, isResetting]);

  const checkWin = useCallback((currentReels) => {
    const rawWins = [];
    let allPossiblePatterns = [...Object.values(FIXED_PATTERNS)];
    for (let r = 0; r < NUM_ROWS; r++) {
      for (let c = 0; c <= 2; c++) allPossiblePatterns.push({ name: '가로', multiplier: 1.0, indices: [[r,c], [r,c+1], [r,c+2]]});
      for (let c = 0; c <= 1; c++) allPossiblePatterns.push({ name: '가로-L', multiplier: 2.0, indices: [[r,c], [r,c+1], [r,c+2], [r,c+3]]});
      allPossiblePatterns.push({ name: '가로-XL', multiplier: 3.0, indices: [[r,0], [r,1], [r,2], [r,3], [r,4]]});
    }
    for (let c = 0; c < NUM_REELS; c++) allPossiblePatterns.push({ name: '세로', multiplier: 1.0, indices: [[0,c], [1,c], [2,c]]});
    for (let c = 0; c <= 2; c++) allPossiblePatterns.push({ name: '대각', multiplier: 1.0, indices: [[0,c], [1,c+1], [2,c+2]]});
    for (let c = 0; c <= 2; c++) allPossiblePatterns.push({ name: '대각', multiplier: 1.0, indices: [[2,c], [1,c+1], [0,c+2]]});
    for (const pattern of allPossiblePatterns) {
      const symbolsOnLine = pattern.indices.map(([r, c]) => currentReels[c][r]);
      const firstSymbol = symbolsOnLine[0];
      if (symbolsOnLine.every(s => s === firstSymbol)) {
        const basePayout = symbolPayouts[firstSymbol];
        if (basePayout) {
          rawWins.push({ ...pattern, symbol: firstSymbol, payout: basePayout * pattern.multiplier * gameState.bet });
        }
      }
    }
    if (rawWins.length === 0) return [];
    if (rawWins.some(win => win.name === '잭팟')) return rawWins;
    const finalWins = [];
    const sortedWins = rawWins.sort((a, b) => b.indices.length - a.indices.length);
    for (const win of sortedWins) {
      const winIndicesStr = win.indices.map(([r,c]) => `${r},${c}`);
      const isSubsumed = finalWins.some(finalWin => {
          const finalWinIndicesSet = new Set(finalWin.indices.map(([r,c]) => `${r},${c}`));
          return winIndicesStr.every(coord => finalWinIndicesSet.has(coord));
      });
      if (!isSubsumed) finalWins.push(win);
    }
    return finalWins;
  }, [gameState.bet]);

  const handleBankruptcy = useCallback(() => {
    if (isBankruptcyInProgress.current) return;
    isBankruptcyInProgress.current = true;

    setIsBankrupting(true);
    setMessage(".......");

    reelContainerControls.start({
      scale: [1, 1.2, 0.95, 1],
      rotate: [-2, 2, -1, 1, 0],
      transition: { type: "spring", stiffness: 600, damping: 15 }
    });

    setTimeout(() => {
      const finalSkullReels = Array(NUM_REELS).fill(0).map(() => Array(NUM_ROWS).fill('💀'));
      setVisibleReels(gameState.reels);
      setGameState(s => ({ ...s, reels: finalSkullReels }));

      const newDurations = generateBankruptcySpinDurations();
      setSpinDurations(newDurations);
      setSpinning(true);

      setTimeout(() => {
        for (let i = 0; i < NUM_REELS; i++) {
          setTimeout(() => {
            setReelsSymbolLists(currentLists => {
              const newLists = [...currentLists];
              newLists[i] = ['💀'];
              return newLists;
            });
          }, i * 120);
        }
      }, 100);

      const maxSpinTime = Math.max(...newDurations) * 1000;
      const totalStopTime = (NUM_REELS - 1) * 200;

      setTimeout(() => {
          setSpinning(false);
          setIsBankrupting(false);
          setGameState(s => ({ ...s, isBankrupt: true }));
          setMessage("💸 돈은 다 잃었지만... 아직 담보로 걸만한 무언가 남아있는 것 같습니다.");
          triggerEffect('GiantSkull');
          if (!gameState.isReset) {
            setShowBankruptcySubmitDialog(true);
          } else {
            triggerEffect('Toast', { message: '리셋된 게임은 랭킹에 기록할 수 없습니다.', type: 'warning' });
          }
      }, maxSpinTime + totalStopTime);

    }, 600);
  }, [reelContainerControls, triggerEffect, gameState.isReset, gameState.reels]);

  const processResults = useCallback((reelsToProcess) => {
    setSpinning(false);

    setTimeout(() => {
      const winningLines = checkWin(reelsToProcess);
      if (winIntervalRef.current) clearInterval(winIntervalRef.current);

      let totalWin = 0;
      if (winningLines.length > 0) {
        const sortedWins = winningLines.sort((a, b) => a.payout - b.payout);
        totalWin = sortedWins.reduce((sum, line) => sum + line.payout, 0);

        const isJackpotWin = sortedWins.some(win => win.name === '잭팟');
        if (isJackpotWin) {
            triggerEffect('Money');
            triggerEffect('Toast', { message: '잭팟! 인생 역전의 기회!', type: 'celebration', duration: 5000 });
        }
        let winIndex = 0;
        const showNextWin = () => {
          if (winIndex < sortedWins.length) {
            const currentWin = sortedWins[winIndex];
            setHighlightedCells(currentWin.indices.map(([r, c]) => `${r},${c}`));
            setMessage(`${currentWin.name} (${currentWin.symbol}): +${Math.round(currentWin.payout)}`);
            winIndex++;
          } else {
            clearInterval(winIntervalRef.current);
            const allFinalIndices = [...new Set(sortedWins.flatMap(win => win.indices.map(([r,c]) => `${r},${c}`)))]
              .map(s => s.split(',').map(Number));
            setHighlightedCells(allFinalIndices.map(([r,c]) => `${r},${c}`));
            const netWin = totalWin - gameState.bet;
            if (netWin > 0) setMessage(`🎉 총 ${Math.round(totalWin)} 획득! (+${Math.round(netWin)})`);
            else setMessage(`본전! ${Math.round(totalWin)} 획득. 😅`);
          }
        }
        showNextWin();
        const intervalDuration = isJackpotWin ? 300 : 1200;
        winIntervalRef.current = setInterval(showNextWin, intervalDuration);

      } else {
        setHighlightedCells([]);
        setMessage(`아쉽지만... 다음 기회에! 😭`);
      }

      setGameState(s => {
        const finalCoins = s.coins + totalWin;
        if (finalCoins <= 0 && !s.isBankrupt && !isBankrupting) {
          handleBankruptcy();
        }
        return { ...s, coins: finalCoins };
      });

    }, (NUM_REELS - 1) * 200 + 600);
  }, [gameState.bet, checkWin, handleBankruptcy, isBankrupting, triggerEffect]);

  const processResultsRef = useRef(processResults);
  useEffect(() => { processResultsRef.current = processResults; }, [processResults]);

  const spin = useCallback(async () => {
    if (gameState.isBankrupt || isBankrupting || spinning || isResetting || (isMenuOpen && window.innerWidth < 768)) return;
    const currentBet = Number(gameState.bet);
    if (currentBet <= 0) { setMessage("베팅액을 설정해주세요."); return; }
    if (gameState.coins < currentBet) { setMessage("코인이 부족합니다."); return; }

    const newDurations = generateSpinDurations();
    setSpinDurations(newDurations);

    setGameState(s => ({ ...s, coins: s.coins - currentBet, isReset: false }));
    setMessage("스핀 중...");
    setHighlightedCells([]);
    if (winIntervalRef.current) clearInterval(winIntervalRef.current);

    const finalReels = Array(NUM_REELS).fill(0).map(() => Array(NUM_ROWS).fill(0).map(() => getWeightedRandomSymbol()));

    setVisibleReels(gameState.reels);
    setGameState(s => ({ ...s, reels: finalReels }));

    const isFirstSpin = gameState.reels.flat().every(s => s === '🍀');
    if (isFirstSpin) {
        setReelsSymbolLists(Array(NUM_REELS).fill([]));
        setTimeout(() => {
            for (let i = 0; i < NUM_REELS; i++) {
                setTimeout(() => {
                    setReelsSymbolLists(currentLists => {
                        const newLists = [...currentLists];
                        newLists[i] = ALL_SYMBOLS;
                        return newLists;
                    });
                }, i * 120);
            }
        }, 100);
    } else {
        setReelsSymbolLists(Array(NUM_REELS).fill(ALL_SYMBOLS));
    }

    setSpinning(true);

    const maxSpinTime = Math.max(...newDurations) * 1000;
    const totalStopTime = (NUM_REELS - 1) * 200;
    setTimeout(() => {
      processResultsRef.current(finalReels);
    }, maxSpinTime + totalStopTime);
  }, [gameState, spinning, isBankrupting, isResetting, isMenuOpen]);

  const resetGame = useCallback(() => {
    stopAllEffects();
    isBankruptcyInProgress.current = false;
    setIsResetting(true);
    setMessage("재시작 중...");

    const newDurations = generateSpinDurations();
    setSpinDurations(newDurations);

    setVisibleReels(gameState.reels);
    setGameState(s => ({ ...s, reels: initialReels }));

    setSpinning(true);
    setTimeout(() => {
      for (let i = 0; i < NUM_REELS; i++) {
        setTimeout(() => {
          setReelsSymbolLists(currentLists => {
            const newLists = [...currentLists];
            newLists[i] = ALL_SYMBOLS;
            return newLists;
          });
        }, i * 120);
      }
    }, 100);

    const maxSpinTime = Math.max(...newDurations) * 1000;
    const totalStopTime = (NUM_REELS - 1) * 200;

    setTimeout(() => {
      setGameState(s => ({ ...s, coins: INITIAL_COINS, bet: INITIAL_BET, isBankrupt: false, isReset: true }));
      setIsBankrupting(false);
      setIsResetting(false);
      setMessage("스핀하세요!");
      setHighlightedCells([]);
      setSpinning(false);
    }, maxSpinTime + totalStopTime);
  }, [initialReels, gameState.reels]);

  useEffect(() => {
    if (!lastKeyEvent || (isMenuOpen && window.innerWidth < 768)) return;
    if (keyProcessedRef.current === lastKeyEvent.time) return;
    keyProcessedRef.current = lastKeyEvent.time;

    const key = lastKeyEvent.key;

    if (key === 'v') { setShowPayouts(prev => !prev); return; }
    if (key === 'Escape') { if (showPayouts) { setShowPayouts(false); } return; }

    if (spinning || isBankrupting || isResetting) return;

    if (lastKeyEvent.target.tagName === 'INPUT' && key === 'Backspace') return;

    switch (key) {
      case ' ': if (gameState.isBankrupt) { resetGame(); } else { spin(); } break;
      case 'w': case 'ArrowUp': setGameState(s => ({ ...s, bet: Math.min(s.coins, s.bet + 10) })); break;
      case 's': case 'ArrowDown': setGameState(s => ({ ...s, bet: Math.max(10, s.bet - 10) })); break;
      default: break;
    }
  }, [ lastKeyEvent, isMenuOpen, spin, resetGame, gameState, spinning, isBankrupting, isResetting, showPayouts ]);

  const handleBetChange = (e) => {
    const value = e.target.value;
    if (value === "") { setGameState(s => ({ ...s, bet: '' })); return; }
    const numValue = parseInt(value, 10);
    if (!isNaN(numValue)) { setGameState(s => ({ ...s, bet: Math.max(0, Math.min(numValue, s.coins)) })); }
  };

  const handleBetFocus = (e) => {
    betBeforeFocus.current = gameState.bet;
    setGameState(s => ({ ...s, bet: '' }));
  };

  const handleBetBlur = (e) => {
    if (e.target.value === '' || Number(e.target.value) <= 0) {
      setGameState(s => ({ ...s, bet: betBeforeFocus.current > 0 ? betBeforeFocus.current : INITIAL_BET }));
    }
  };

  const setBetFraction = useCallback((fraction) => {
    if (gameState.coins <= 0) return;
    const newBet = Math.floor(gameState.coins / fraction);
    setGameState(s => ({ ...s, bet: newBet }));
  }, [gameState.coins]);

  const handleSubmitScore = useCallback((finalCoins, isBankruptcy = false) => {
    if (gameState.isReset) {
      triggerEffect('Toast', { message: '리셋된 게임은 랭킹에 기록할 수 없습니다.', type: 'warning' });
      return;
    }
    submitScore({ gameId: meta.id, score: finalCoins, user: user, options: { finalCoins, isBankruptcy } }, triggerEffect, updateUser);
    resetGame();
  }, [gameState.isReset, user, resetGame, triggerEffect, updateUser]);

  const handleQuitGame = useCallback(() => { 
    if (user) {
        notifyGameOver({
            gameId: meta.id,
            score: gameState.coins,
            userId: user.id
        }).then(result => {
            if (result.success && result.user) {
                updateUser(result.user, true);
            }
        });
    }
    setShowSubmitScoreDialog(true); 
  }, [user, gameState.coins, updateUser]);

  return (
    <div className="flex flex-col items-center gap-4 relative p-6 rounded-2xl">
      <div className="flex gap-4 text-lg font-bold w-full justify-center items-center">
        <motion.div key={gameState.coins} initial={{ scale: 0.8 }} animate={{ scale: 1 }}>
          코인: {Math.round(gameState.coins)}
        </motion.div>
        {!gameState.isBankrupt && <div>베팅: {gameState.bet}</div>}
      </div>
      <div className="text-sm text-white/70 whitespace-pre-wrap text-center min-h-[2.5rem]">{message}</div>
      <motion.div
        className="relative p-2 rounded-2xl shadow-inner"
        animate={reelContainerControls}
        style={{ backgroundColor: 'rgba(0, 0, 0, 0.2)' }}
      >
        <motion.div
          className="absolute inset-0 bg-red-900/80 rounded-2xl"
          initial={{ opacity: 0 }}
          animate={{ opacity: gameState.isBankrupt || isBankrupting ? 1 : 0 }}
          transition={{ duration: 0.5 }}
        />

        <div className="flex justify-center gap-2 relative">
          {Array(NUM_REELS).fill(0).map((_, colIndex) => (
            <Reel
              key={colIndex}
              symbols={reelsSymbolLists[colIndex]}
              startColumn={visibleReels[colIndex]}
              finalColumn={gameState.reels[colIndex]}
              spinning={spinning}
              delayIndex={colIndex}
              numRows={NUM_ROWS}
              spinDuration={spinDurations[colIndex]}
              isBankrupt={gameState.isBankrupt}
            />
          ))}
        </div>
        <div className="absolute inset-0 grid grid-cols-5 gap-2 pointer-events-none">
          {Array(NUM_REELS * NUM_ROWS).fill(0).map((_, i) => {
            const row = Math.floor(i / NUM_REELS);
            const col = i % NUM_REELS;
            const isHighlighted = highlightedCells.includes(`${row},${col}`);
            return (
              <motion.div
                key={`${row}-${col}`}
                className="rounded-lg bg-yellow-400/30"
                initial={{ opacity: 0, scale: 1, boxShadow: "none" }}
                animate={{
                  opacity: isHighlighted ? 1 : 0,
                  scale: isHighlighted ? 1.15 : 1,
                  boxShadow: isHighlighted ? "0 0 15px 3px rgba(250, 204, 21, 0.7)" : "none",
                }}
                transition={{ duration: 0.3, ease: "backOut" }}
              />
            );
          })}
        </div>
        <div className="absolute inset-0 rounded-2xl pointer-events-none bg-gradient-to-b from-black/50 via-transparent to-black/50"></div>
      </motion.div>

      <div className="flex flex-col justify-center items-center gap-2 mt-2 w-full max-w-md">
        {gameState.isBankrupt ? (
            <Button onClick={resetGame} className="w-full" disabled={isResetting}>
                {isResetting ? "재시작 중..." : "다시하기 (Space)"}
            </Button>
        ) : (
          <>
            <div className="flex flex-wrap justify-center gap-1">
              {[1, 2, 4, 6, 8, 10].map(fraction => {
                const betAmount = Math.floor(gameState.coins / fraction);
                const isDisabled = spinning || isBankrupting || isResetting || betAmount < 10;
                return (
                  <Button 
                    key={fraction}
                    variant="secondary" 
                    className={`px-2 py-1 text-xs ${isDisabled ? 'opacity-50' : ''}`}
                    onClick={() => setBetFraction(fraction)} 
                    disabled={isDisabled}
                  >
                    {fraction === 1 ? '올인' : `1/${fraction}`}
                  </Button>
                )
              })}
            </div>
            <div className="flex flex-wrap justify-center items-center gap-2">
                <Button variant="secondary" onClick={() => setShowPayouts(true)} disabled={spinning || isBankrupting || isResetting}>배당표 (V)</Button>
                <div className="flex items-center gap-1">
                <Button variant="secondary" className="px-2" onClick={() => setGameState(s => ({ ...s, bet: Math.max(10, s.bet - 10) }))} disabled={spinning || isBankrupting || isResetting}>-10 (↓/S)</Button>
                <input
                    type="number"
                    inputMode="numeric"
                    pattern="[0-9]*"
                    value={gameState.bet}
                    onChange={handleBetChange}
                    onFocus={handleBetFocus}
                    onBlur={handleBetBlur}
                    disabled={spinning || isBankrupting || isResetting}
                    className="w-24 px-2 py-1 rounded-xl bg-black/20 border border-white/10 text-center font-bold disabled:opacity-50"
                />
                <Button variant="secondary" className="px-2" onClick={() => setGameState(s => ({ ...s, bet: Math.min(s.coins, s.bet + 10) }))} disabled={spinning || isBankrupting || isResetting}>+10 (↑/W)</Button>
                </div>
                <Button onClick={spin} disabled={spinning || isBankrupting || isResetting}>{spinning ? "스핀 중" : "스핀 (Space)"}</Button>
                <Button variant="secondary" onClick={handleQuitGame} disabled={spinning || isBankrupting || isResetting}>게임 종료 및 기록</Button>
            </div>
          </>
        )}
      </div>

      <Dialog open={showPayouts} onOpenChange={setShowPayouts} title="배당표 및 규칙">
        <div className="text-sm text-white/80 max-h-[60vh] overflow-y-auto p-1">
          <h3 className="text-lg font-bold text-yellow-400 mb-2">기본 배당</h3>
          <ul className="grid grid-cols-2 gap-2 mb-4">
            {Object.entries(symbolPayouts).map(([symbol, payout]) => (
              <li key={symbol} className="flex items-center gap-2">
                <span className="text-2xl">{symbol}</span>
                <span>: 3개 연결 시 베팅액의 {payout}배</span>
              </li>
            ))}
          </ul>
          <h3 className="text-lg font-bold text-yellow-400 mb-2">특수 패턴</h3>
          <ul className="space-y-2">
            {Object.values(FIXED_PATTERNS).map(p => (
              <li key={p.name}><strong>{p.name} ({p.indices.length}칸):</strong> 기본 배당 x {p.multiplier}</li>
            ))}
          </ul>
        </div>
        <div className="mt-4 flex justify-end">
            <Button onClick={() => setShowPayouts(false)}>닫기 (V or ESC)</Button>
        </div>
      </Dialog>

      <SubmitScoreDialog
        open={showSubmitScoreDialog}
        onOpenChange={setShowSubmitScoreDialog}
        score={Math.round(gameState.coins)}
        scoreLabel="코인"
        user={user}
        onSubmit={() => handleSubmitScore(gameState.coins)}
        onClose={() => {
          setShowSubmitScoreDialog(false);
          resetGame();
        }}
      />

      <SubmitScoreDialog
        open={showBankruptcySubmitDialog}
        onOpenChange={setShowBankruptcySubmitDialog}
        score={Math.round(gameState.coins)}
        scoreLabel="코인"
        user={user}
        onSubmit={() => handleSubmitScore(gameState.coins, true)}
        onClose={() => {
          setShowBankruptcySubmitDialog(false);
          resetGame();
        }}
      />

      <PauseOverlay isPaused={isMenuOpen && window.innerWidth < 768} />
    </div>
  );
}