import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import GlassCard from '../components/ui/GlassCard';
import Button from '../components/ui/Button';
import Dialog from '../components/ui/Dialog';
import Input from '../components/ui/Input';
import Select from '../components/ui/Select';
import { useAuth } from '../context/AuthContext';
import { fetchCalendarEvents, addCalendarEvent, deleteCalendarEvent } from '../api/starcadeApi';
import { ChevronLeft, ChevronRight, PlusCircle, X } from 'lucide-react';

const EventIcon = ({ event }) => {
    const defaultIcon = '✨';
    const icon = event.icon || defaultIcon;
    const shortName = event.name ? event.name.substring(0, 3) : ''; // Take first 3 chars
    return (
        <span title={event.name} className="text-xs cursor-pointer flex items-center gap-0.5">
            {icon}
            {shortName && <span className="text-[0.6rem]">{shortName}</span>}
        </span>
    );
};

const AddEventDialog = ({ open, onOpenChange, onEventAdded, date }) => {
    const [name, setName] = useState('');
    const [description, setDescription] = useState('');
    const [icon, setIcon] = useState('🎉');
    const [recurringType, setRecurringType] = useState('none');
    const [recurringValue, setRecurringValue] = useState(1);
    const { triggerEffect } = useAuth();

    const handleSubmit = async (e) => {
        e.preventDefault();
        let recurring = null;
        if (recurringType !== 'none') {
            recurring = { unit: recurringType, value: recurringType === 'day' ? parseInt(recurringValue, 10) : 1 };
        }

        const eventData = {
            name,
            description,
            icon,
            date: date.toISOString().split('T')[0],
            recurring,
        };

        const result = await addCalendarEvent(eventData);
        if (result.success) {
            triggerEffect('Toast', { message: '이벤트가 추가되었습니다.', type: 'success' });
            onEventAdded();
            onOpenChange(false);
        } else {
            triggerEffect('Toast', { message: result.message || '이벤트 추가 실패', type: 'error' });
        }
    };

    const recurringOptions = [
        { value: 'none', label: '반복 안 함' },
        { value: 'day', label: '일마다 반복' },
        { value: 'month', label: '매월 반복' },
        { value: 'year', label: '매년 반복' },
    ];

    return (
        <Dialog open={open} onOpenChange={onOpenChange} title={`${date.toLocaleDateString()} 이벤트 추가`}>
            <form onSubmit={handleSubmit} className="flex flex-col gap-4 p-4">
                <Input label="이름" value={name} onChange={(e) => setName(e.target.value)} required />
                <Input label="설명" value={description} onChange={(e) => setDescription(e.target.value)} />
                <Input label="아이콘" value={icon} onChange={(e) => setIcon(e.target.value)} maxLength={2} />
                
                <div className="flex items-end gap-2">
                    <div className="flex-grow">
                        <label className="block text-sm font-medium text-white/80 mb-1">반복</label>
                        <Select 
                            options={recurringOptions}
                            value={recurringType}
                            onChange={setRecurringType}
                        />
                    </div>
                    {recurringType === 'day' && (
                        <Input 
                            type="number"
                            value={recurringValue}
                            onChange={(e) => setRecurringValue(Math.max(1, parseInt(e.target.value, 10) || 1))}
                            min="1"
                            className="w-32"
                        />
                    )}
                </div>

                <Button type="submit">추가하기</Button>
            </form>
        </Dialog>
    );
};

const CalendarPage = () => {
  const navigate = useNavigate();
  const { user, triggerEffect } = useAuth();
  const [currentDate, setCurrentDate] = useState(new Date());
  const [events, setEvents] = useState({});
  const [isLoading, setIsLoading] = useState(true);
  const [selectedDate, setSelectedDate] = useState(null);
  const [isAddEventDialogOpen, setIsAddEventDialogOpen] = useState(false);

  const loadEvents = useCallback(async () => {
    if (!user) {
      setIsLoading(false);
      return;
    }
    setIsLoading(true);
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth() + 1;
    const result = await fetchCalendarEvents(year, month);
    if (result.success) {
        setEvents(result.events);
    } else {
        setEvents({}); // Clear events on failure
    }
    setIsLoading(false);
  }, [user, currentDate]);

  useEffect(() => {
    loadEvents();
  }, [loadEvents]);

  const handleDeleteEvent = async (eventId) => {
    const result = await deleteCalendarEvent(eventId);
    if (result.success) {
        triggerEffect('Toast', { message: '이벤트가 삭제되었습니다.', type: 'success' });
        loadEvents(); // Refresh the events
    } else {
        triggerEffect('Toast', { message: result.message || '이벤트 삭제 실패', type: 'error' });
    }
  };

  const firstDayOfMonth = new Date(currentDate.getFullYear(), currentDate.getMonth(), 1);
  const lastDayOfMonth = new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 0);
  const startingDayOfWeek = firstDayOfMonth.getDay();
  const totalDays = lastDayOfMonth.getDate();

  const days = [];
  for (let i = 0; i < startingDayOfWeek; i++) {
    days.push(<div key={`empty-start-${i}`} className="w-full h-20 sm:h-24"></div>);
  }
  for (let day = 1; day <= totalDays; day++) {
    const date = new Date(currentDate.getFullYear(), currentDate.getMonth(), day);
    const dateStr = date.toISOString().split('T')[0];
    const dayEvents = events[dateStr] || [];
    const isToday = new Date().toDateString() === date.toDateString();

    days.push(
      <div 
        key={day} 
        className={`w-full h-20 sm:h-24 p-1.5 rounded-lg transition-colors duration-200 cursor-pointer ${isToday ? 'bg-emerald-500/30' : 'bg-white/5 hover:bg-white/10'} ${selectedDate?.toDateString() === date.toDateString() ? 'ring-2 ring-amber-400' : ''}`}
        onClick={() => setSelectedDate(date)}
      >
        <div className="font-bold text-sm">{day}</div>
        {dayEvents.length > 0 && (
          <div className="flex flex-wrap gap-1 mt-1">
            {dayEvents.slice(0, 4).map((event, i) => (
              <EventIcon key={i} event={event} />
            ))}
            {dayEvents.length > 4 && <span className="text-xs">+...</span>}
          </div>
        )}
      </div>
    );
  }

  const handlePrevMonth = () => {
    setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() - 1, 1));
    setSelectedDate(null);
  };

  const handleNextMonth = () => {
    setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 1));
    setSelectedDate(null);
  };

  return (
    <div className="flex flex-col gap-4">
      <GlassCard>
        <div className="flex items-center justify-between p-4">
          <Button onClick={handlePrevMonth} variant="secondary" className="p-2"><ChevronLeft className="w-5 h-5" /></Button>
          <h2 className="text-xl font-bold">{currentDate.getFullYear()}년 {currentDate.getMonth() + 1}월</h2>
          <Button onClick={handleNextMonth} variant="secondary" className="p-2"><ChevronRight className="w-5 h-5" /></Button>
        </div>
        <div className="grid grid-cols-7 gap-1 px-2 pb-2 text-center text-xs text-white/60">
          {['일', '월', '화', '수', '목', '금', '토'].map(day => <div key={day}>{day}</div>)}
        </div>
        {isLoading ? (
            <div className="grid place-items-center p-8"><p className="text-white/70 animate-pulse">이벤트 로딩 중...</p></div>
        ) : !user ? (
            <div className="grid place-items-center p-8">
                <p className="text-white/70">로그인이 필요한 서비스입니다.</p>
            </div>
        ) : (
            <div className="grid grid-cols-7 gap-1 p-2">
                {days}
            </div>
        )}
      </GlassCard>

      {selectedDate && user && (
        <GlassCard title={`${selectedDate.toLocaleDateString()} 이벤트 목록`}>
            <div className="p-4 space-y-2 max-h-60 overflow-y-auto">
                {(events[selectedDate.toISOString().split('T')[0]] || []).length > 0 ? (
                    events[selectedDate.toISOString().split('T')[0]].map((event) => (
                        <div key={event.id} className="flex items-center justify-between p-2 bg-white/5 rounded-lg">
                            <div className="flex items-center gap-3">
                                <span className="text-2xl">{event.icon || '✨'}</span>
                                <div>
                                    <h3 className="font-bold">{event.name}</h3>
                                    {event.description && <p className="text-sm text-white/70">{event.description}</p>}
                                </div>
                            </div>
                            {user.isMaster && event.type === 'anniversary' && (
                                <Button 
                                    variant="danger" 
                                    className="px-2 py-1 text-xs"
                                    onClick={() => handleDeleteEvent(event.id)}
                                >
                                    삭제
                                </Button>
                            )}
                        </div>
                    ))
                ) : (
                    <p className="text-center text-white/50 py-4">이 날에는 특별한 이벤트가 없습니다.</p>
                )}
            </div>
            {user.isMaster && (
                <div className="p-4 border-t border-white/10">
                    <Button onClick={() => setIsAddEventDialogOpen(true)} variant="primary" className="w-full flex items-center justify-center gap-2">
                        <PlusCircle className="w-5 h-5" />
                        이벤트 추가
                    </Button>
                </div>
            )}
        </GlassCard>
      )}

      {user && user.isMaster && selectedDate && (
        <AddEventDialog 
            open={isAddEventDialogOpen} 
            onOpenChange={setIsAddEventDialogOpen} 
            onEventAdded={loadEvents} 
            date={selectedDate} 
        />
      )}
       <Button onClick={() => navigate('/')} variant="secondary">돌아가기</Button>
    </div>
  );
};

export default CalendarPage;