import React, { useEffect, useMemo, useState, useRef, useCallback } from 'react';
import Button from '../components/ui/Button';
import Celebration from '../components/Celebration';
import { motion } from 'framer-motion';
import PauseOverlay from '../components/PauseOverlay';
import { useEffects } from '../effects/EffectLayer';
import { useAuth } from '../context/AuthContext';
import { submitScore } from '../lib/scoreManager';
import SubmitScoreDialog from '../components/ui/SubmitScoreDialog';
import { notifyGameOver } from '../api/starcadeApi';

export const meta = { id:'wordle', name:'워들', icon:'🔤', topic:'wordle', description:'영어 단어 맞추기', category: 'single-player' };

const GAME_ID = meta.id;

const evaluateGuess = (guess, solution) => {
  const splitSolution = solution.split('');
  const splitGuess = guess.split('');
  const result = Array(5).fill({ status: 'absent' });
  const solutionLetterCount = {};

  splitGuess.forEach((letter, i) => {
    if (splitSolution[i] === letter) {
      result[i] = { status: 'correct' };
      splitSolution[i] = null;
    } else {
        solutionLetterCount[splitSolution[i]] = (solutionLetterCount[splitSolution[i]] || 0) + 1;
    }
  });

  splitGuess.forEach((letter, i) => {
    if (result[i].status !== 'correct') {
      if (solutionLetterCount[letter] > 0) {
        result[i] = { status: 'present' };
        solutionLetterCount[letter]--;
      }
    }
  });
  return result;
};

export default function Wordle({ isPaused, lastKeyEvent }){
  const [words, setWords] = useState([]);
  const mountTimeRef = useRef(Date.now());
  const { triggerEffect, stopAllEffects } = useEffects();
  const { user, updateUser } = useAuth();
  const [showSubmitScoreDialog, setShowSubmitScoreDialog] = useState(false);
  const [showCelebration, setShowCelebration] = useState(false);
  const keyProcessedRef = useRef(null);

  const getInitialState = useCallback(() => {
    try {
        const savedState = localStorage.getItem(`starcade-gamestate-${GAME_ID}`);
        if (savedState) {
            const parsed = JSON.parse(savedState);
            if (parsed.answer && parsed.rows) {
                return parsed;
            }
        }
    } catch (e) { console.error("Failed to load saved game state", e); }
    
    return {
        answer: null,
        rows: Array(6).fill(''),
        cur: 0,
        rowStatuses: Array(6).fill(null),
        keyboardStatus: {},
        isGameOver: false,
        invalidGuessCount: 0,
    };
  }, []);

  const [gameState, setGameState] = useState(getInitialState);

  const fetchAndInitialize = useCallback(async () => {
    try {
        const response = await fetch(`${import.meta.env.BASE_URL}wordle.txt`);
        if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
        const text = await response.text();
        const loadedWords = text.split('\n').map(w => w.trim().toUpperCase()).filter(w => w.length === 5 && !w.startsWith('#'));
        setWords(loadedWords);

        setGameState(s => {
            if (s.answer && loadedWords.includes(s.answer)) {
                return s; // Keep loaded state if answer is valid
            }
            const randomAnswer = loadedWords[Math.floor(Math.random() * loadedWords.length)];
            return { ...getInitialState(), answer: randomAnswer };
        });

    } catch (error) {
        console.error("Failed to fetch words:", error);
        const fallbackWords = ["APPLE", "BAKER", "CRANE", "DREAM", "EAGLE"];
        setWords(fallbackWords);
        if (!gameState.answer) {
            const randomAnswer = fallbackWords[Math.floor(Math.random() * fallbackWords.length)];
            setGameState(s => ({ ...s, answer: randomAnswer }));
        }
    }
  }, [getInitialState, gameState.answer]);

  useEffect(() => {
    fetchAndInitialize();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (gameState.isGameOver) {
        if (!showSubmitScoreDialog) {
            setShowSubmitScoreDialog(true);
        }
        return;
    };
    try {
        localStorage.setItem(`starcade-gamestate-${GAME_ID}`, JSON.stringify(gameState));
    } catch (e) {
        console.error("Failed to save game state", e);
    }
  }, [gameState, showSubmitScoreDialog]);

  const resetGame = useCallback(() => {
    stopAllEffects();
    localStorage.removeItem(`starcade-gamestate-${GAME_ID}`);
    const randomAnswer = words[Math.floor(Math.random() * words.length)];
    setGameState({ ...getInitialState(), answer: randomAnswer });
    setShowCelebration(false);
    setShowSubmitScoreDialog(false);
  }, [words, getInitialState, stopAllEffects]);

  const addChar = useCallback((ch) => {
    if (gameState.isGameOver) return;
    setGameState(s => ({ ...s, rows: s.rows.map((row, i) => i === s.cur ? (row + ch).slice(0, 5) : row) }));
  }, [gameState.isGameOver]);

  const backspace = useCallback(() => {
    if (gameState.isGameOver) return;
    setGameState(s => ({ ...s, rows: s.rows.map((row, i) => i === s.cur ? row.slice(0, -1) : row) }));
  }, [gameState.isGameOver]);

  const submit = useCallback(() => {
    if (gameState.isGameOver || !gameState.answer || gameState.cur > 5) return;
    const guess = gameState.rows[gameState.cur].trim().toUpperCase();
    if(guess.length !== 5) return;

    if (!words.includes(guess)) {
      triggerEffect('Toast', { message: "단어 목록에 없는 단어입니다!", type: 'warning' });
      setGameState(s => ({ ...s, invalidGuessCount: s.invalidGuessCount + 1 }));
      return;
    }

    const newStatus = evaluateGuess(guess, gameState.answer);
    const newKeyboardStatus = { ...gameState.keyboardStatus };
    guess.split('').forEach((char, i) => {
        const currentStatus = newKeyboardStatus[char];
        const newTileStatus = newStatus[i].status;
        if (currentStatus !== 'correct') {
            if (newTileStatus === 'correct') newKeyboardStatus[char] = 'correct';
            else if (newTileStatus === 'present') newKeyboardStatus[char] = 'present';
            else if (newTileStatus === 'absent' && !currentStatus) newKeyboardStatus[char] = 'absent';
        }
    });

    const isWin = guess === gameState.answer;
    const isLastTry = gameState.cur === 5;

    setGameState(s => ({
        ...s,
        rowStatuses: s.rowStatuses.map((rs, i) => i === s.cur ? newStatus : rs),
        keyboardStatus: newKeyboardStatus,
        cur: isWin || isLastTry ? s.cur : s.cur + 1,
        isGameOver: isWin || isLastTry,
    }));

    if(isWin){
      setShowCelebration(true);
      setShowSubmitScoreDialog(true);
      triggerEffect('Confetti');
    } else if(isLastTry){
      triggerEffect('Toast', { message: `😢 아쉽네요! 정답은 ${gameState.answer}였습니다.`, type: 'error' });
      setShowSubmitScoreDialog(true);
      triggerEffect('Defeat');
    }
  }, [gameState, words, triggerEffect]);

  const handleSubmitScore = () => {
    const finalScore = (6 - gameState.cur) * 100;
    submitScore({
        gameId: meta.id,
        score: finalScore,
        user: user,
        options: { guesses: gameState.cur + 1, invalidGuessCount: gameState.invalidGuessCount }
    }, triggerEffect, updateUser);
    setShowSubmitScoreDialog(false);
  };

  const handleCloseSubmitDialog = () => {
    if (user) {
        notifyGameOver({
            gameId: meta.id,
            score: (6 - gameState.cur) * 100,
            userId: user.id
        }).then(result => {
            if (result.success && result.user) {
                updateUser(result.user, true);
            }
        });
    }
    setShowSubmitScoreDialog(false);
  };

  useEffect(()=>{
    if (!lastKeyEvent || isPaused) return;
    if (Date.now() - mountTimeRef.current < 500) return;
    if (keyProcessedRef.current === lastKeyEvent.time) return;
    keyProcessedRef.current = lastKeyEvent.time;
    
    const { key, ctrlKey, metaKey } = lastKeyEvent;
    if (ctrlKey || metaKey) return;

    if(/^[a-zA-Z]$/.test(key)) addChar(key.toUpperCase());
    if(key==='Backspace') backspace();
    if(key==='Enter') submit();
  }, [lastKeyEvent, isPaused, addChar, backspace, submit]);

  const colorize = (word, rowIndex) => Array.from({length:5}).map((_,i)=>{
    const ch = word[i] || ' ';
    const status = gameState.rowStatuses[rowIndex]?.[i]?.status;
    let backFaceColor = 'bg-white/10';
    const isSubmitted = !!status;

    if (isSubmitted) {
      if (status === 'correct') backFaceColor = 'bg-emerald-500/80';
      else if (status === 'present') backFaceColor = 'bg-amber-500/80';
      else backFaceColor = 'bg-gray-700/80';
    }

    return (
      <motion.div
        key={i}
        className={`w-12 h-12 sm:w-14 sm:h-14 grid place-items-center border border-white/10 rounded-xl font-bold relative`}
        style={{ transformStyle: "preserve-3d" }}
        initial={false}
        animate={{ rotateX: isSubmitted ? 180 : 0 }}
        transition={{ duration: 0.5, delay: i * 0.1 }}
      >
        <div className={`absolute inset-0 grid place-items-center bg-white/10 backface-hidden`}>{ch}</div>
        <div className={`absolute inset-0 grid place-items-center ${backFaceColor} backface-hidden`} style={{ transform: 'rotateX(180deg)' }}>{ch}</div>
      </motion.div>
    );
  });

  const kbEN = "QWERTYUIOP-ASDFGHJKL-ZXCVBNM".split('-');

  if (!gameState.answer) {
    return <div className="flex items-center justify-center h-full"><div className="animate-pulse text-white/70">단어 목록 로딩 중...</div></div>;
  }

  return (
    <div className="flex flex-col items-center gap-4 relative">
      <div className="space-y-2">
        {gameState.rows.map((w,i)=> <div key={i} className="flex gap-2 justify-center">{colorize(w, i)}</div>)}
      </div>
      <div className="flex gap-2">
        <Button type="button" onClick={submit}>입력(Enter)</Button>
        <Button type="button" variant="secondary" onClick={backspace}>지우기(←)</Button>
        <Button type="button" variant="secondary" onClick={resetGame}>새 게임</Button>
      </div>

      <div className="w-full max-w-md select-none">
        {kbEN.map((row,ri)=>(
          <div key={ri} className="flex justify-center gap-1 mb-1">
            {row.split('').map((k)=> {
              const status = gameState.keyboardStatus[k];
              let bgColorClass = 'bg-white/10 hover:bg-white/20';
              if (status === 'correct') bgColorClass = 'bg-emerald-500';
              else if (status === 'present') bgColorClass = 'bg-amber-500';
              else if (status === 'absent') bgColorClass = 'bg-gray-700';

              return (
                <button type="button" key={k} className={`px-3 py-2 rounded-md border border-white/10 text-sm font-medium ${bgColorClass}`} onClick={()=>addChar(k)}>
                  {k}
                </button>
              );
            })}
          </div>
        ))}
      </div>

      <Celebration show={showCelebration} message="🎉 축하합니다! 워들을 맞췄어요! 🎉" onComplete={() => setShowCelebration(false)} />
      <PauseOverlay isPaused={isPaused} />

      <SubmitScoreDialog
          open={showSubmitScoreDialog}
          onOpenChange={setShowSubmitScoreDialog}
          score={(6 - gameState.cur) * 100}
          user={user}
          onSubmit={handleSubmitScore}
          onClose={handleCloseSubmitDialog}
      />
    </div>
  );
}