import React, { useEffect, useState, useCallback, useRef } from 'react';
import Button from '../components/ui/Button';
import { motion, AnimatePresence } from 'framer-motion';
import Celebration from '../components/Celebration';
import PauseOverlay from '../components/PauseOverlay';
import { useEffects } from '../effects/EffectLayer';
import { useAuth } from '../context/AuthContext';
import { submitScore } from '../lib/scoreManager';
import SubmitScoreDialog from '../components/ui/SubmitScoreDialog';

export const meta = { id: 'memory', name: '카드 맞추기', icon: '🃏', topic: 'memory', description: '직관과 기억력의 조화', category: 'single-player' };

const GAME_ID = meta.id;
const icons = ['🍎', '🍋', '🍇', '🍉', '🍒', '🥝', '🍍', '🥥', '🍓', '🍑'];

const shuffle = (array) => {
  const shuffledArray = [...array];
  for (let i = shuffledArray.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [shuffledArray[i], shuffledArray[j]] = [shuffledArray[j], shuffledArray[i]];
  }
  return shuffledArray;
};

export default function MemoryGame({ isPaused }) {
  const [lockBoard, setLockBoard] = useState(false);
  const [showCelebration, setShowCelebration] = useState(false);
  const [showSubmitScoreDialog, setShowSubmitScoreDialog] = useState(false);
  const { triggerEffect } = useEffects();
  const { user, updateUser } = useAuth();

  const getInitialState = useCallback(() => {
    try {
      const savedState = localStorage.getItem(`starcade-gamestate-${GAME_ID}`);
      if (savedState) {
        const parsed = JSON.parse(savedState);
        if (parsed.cards && typeof parsed.moves === 'number') {
          return parsed;
        }
      }
    } catch (e) {
      console.error("Failed to load saved game state", e);
    }
    const gameIcons = icons.slice(0, 6);
    return {
      cards: shuffle(
        [...gameIcons, ...gameIcons].map((v, index) => ({
          id: index,
          value: v,
          isFlipped: false,
          isMatched: false,
        }))
      ),
      flippedIndices: [],
      moves: 0,
    };
  }, []);

  const [gameState, setGameState] = useState(getInitialState);

  useEffect(() => {
    try {
      localStorage.setItem(`starcade-gamestate-${GAME_ID}`, JSON.stringify(gameState));
    } catch (e) {
      console.error("Failed to save game state", e);
    }
  }, [gameState]);

  const initializeGame = useCallback(() => {
    localStorage.removeItem(`starcade-gamestate-${GAME_ID}`);
    setGameState(getInitialState());
    setLockBoard(false);
    setShowCelebration(false);
    setShowSubmitScoreDialog(false);
  }, [getInitialState]);

  const handleCardClick = useCallback((clickedIndex) => {
    if (isPaused || lockBoard) return;
    const clickedCard = gameState.cards[clickedIndex];
    if (clickedCard.isFlipped || clickedCard.isMatched) return;

    const newFlippedIndices = [...gameState.flippedIndices, clickedIndex];
    const newCards = gameState.cards.map((card, idx) =>
      idx === clickedIndex ? { ...card, isFlipped: true } : card
    );

    setGameState(s => ({ ...s, cards: newCards, flippedIndices: newFlippedIndices, moves: s.moves + 1 }));

    if (newFlippedIndices.length === 2) {
      setLockBoard(true);
      const [firstIndex, secondIndex] = newFlippedIndices;
      const firstCard = newCards[firstIndex];
      const secondCard = newCards[secondIndex];

      if (firstCard.value === secondCard.value) {
        setTimeout(() => {
          setGameState(s => ({
            ...s,
            cards: s.cards.map((card, idx) =>
              idx === firstIndex || idx === secondIndex ? { ...card, isMatched: true } : card
            ),
            flippedIndices: [],
          }));
          setLockBoard(false);
        }, 500);
      } else {
        setTimeout(() => {
          setGameState(s => ({
            ...s,
            cards: s.cards.map((card, idx) =>
              idx === firstIndex || idx === secondIndex ? { ...card, isFlipped: false } : card
            ),
            flippedIndices: [],
          }));
          setLockBoard(false);
        }, 1000);
      }
    }
  }, [gameState, lockBoard, isPaused]);

  useEffect(() => {
    if (gameState.cards.length > 0 && gameState.cards.every(card => card.isMatched)) {
      setShowSubmitScoreDialog(true);
      triggerEffect('Confetti');
    }
  }, [gameState.cards, triggerEffect]);

  const finalScore = 100 - gameState.moves;

  const handleSubmitScore = () => {
    const scoreData = {
        gameId: meta.id,
        score: finalScore,
        user: user,
        options: { moves: gameState.moves }
    };
    submitScore(scoreData, triggerEffect, updateUser);
    setShowSubmitScoreDialog(false);
    setShowCelebration(true);
  };

  const handleCloseSubmitDialog = () => {
    if (user) {
        notifyGameOver({
            gameId: meta.id,
            score: finalScore,
            userId: user.id
        }).then(result => {
            if (result.success && result.user) {
                updateUser(result.user, true);
            }
        });
    }
    setShowSubmitScoreDialog(false);
    setShowCelebration(true);
  }

  return (
    <div className="flex flex-col items-center gap-4 relative">
      <div className="text-sm text-white/70">뒤집은 횟수: {gameState.moves}</div>
      <div className="grid grid-cols-4 gap-3">
        <AnimatePresence>
          {gameState.cards.map((card, index) => (
            <motion.div
              key={card.id}
              onClick={() => handleCardClick(index)}
              className={`w-16 sm:w-16 h-20 sm:h-24 rounded-2xl cursor-pointer relative border-2 ${card.isFlipped ? 'border-emerald-400' : 'border-white/10'}`}
              style={{ transformStyle: "preserve-3d" }}
              animate={{
                rotateY: card.isFlipped ? 180 : 0,
                opacity: card.isMatched ? 0.2 : 1,
                scale: card.isMatched ? 0.95 : 1,
              }}
              transition={{ duration: 0.4 }}
            >
              <div className="absolute inset-0 grid place-items-center bg-white/10 backface-hidden">?</div>
              <div className="absolute inset-0 grid place-items-center bg-emerald-600/50 backface-hidden text-4xl" style={{ transform: 'rotateY(180deg)' }}>
                {card.value}
              </div>
            </motion.div>
          ))}
        </AnimatePresence>
      </div>
      <Button onClick={initializeGame} variant="secondary">다시 시작</Button>
      <Celebration show={showCelebration} message="🎉 축하합니다! 모든 짝을 찾았어요! 🎉" onComplete={() => setShowCelebration(false)} />
      <PauseOverlay isPaused={isPaused} />
      <SubmitScoreDialog
          open={showSubmitScoreDialog}
          onOpenChange={setShowSubmitScoreDialog}
          score={finalScore}
          scoreLabel={`점수 (100 - ${gameState.moves})`}
          user={user}
          onSubmit={handleSubmitScore}
          onClose={handleCloseSubmitDialog}
      />
    </div>
  );
}