import React from 'react';
import Dialog from './Dialog';
import Button from './Button';

export default function SubmitScoreDialog({ open, onOpenChange, score, scoreLabel = '점수', onSubmit, onClose, user }) {

  const handleSubmit = () => {
    onSubmit();
    onOpenChange(false);
  };

  const handleClose = () => {
    if (onClose) onClose();
    onOpenChange(false);
  };

  return (
    <Dialog open={open} onOpenChange={handleClose} title="게임 종료">
      <div className="text-center">
        <p className="text-white/80 mb-4">최종 점수: {score}</p>
        <p className="text-white/80 mb-4">랭킹에 점수를 기록하시겠습니까?</p>
        <div className="flex justify-center gap-4">
          <Button onClick={handleSubmit}>기록하기</Button>
          <Button variant="secondary" onClick={handleClose}>나중에</Button>
        </div>
      </div>
    </Dialog>
  );
}