import React, { useEffect, useState, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import GlassCard from '../components/ui/GlassCard';
import Button from '../components/ui/Button';
import CreateRoomDialog from '../components/game/CreateRoomDialog';

export default function MultiplayerGameDetail({ game }) {
  const { user, socket } = useAuth();
  const navigate = useNavigate();
  const [rooms, setRooms] = useState([]);
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [isJoining, setIsJoining] = useState(false);

  useEffect(() => {
    if (!socket) return;

    socket.emit('getRooms');

    const handleRoomsList = (roomList) => {
      setRooms(roomList.filter(r => r.gameId === game.id));
    };
    const handleRoomJoined = (room) => {
        setIsJoining(false);
        navigate(`/games/${room.gameId}/${room.id}`);
    };
    const handleRoomError = (error) => {
        setIsJoining(false);
        console.error("Room Error:", error.message);
    };

    socket.on('roomsList', handleRoomsList);
    socket.on('roomJoined', handleRoomJoined);
    socket.on('roomError', handleRoomError);

    return () => {
      socket.off('roomsList', handleRoomsList);
      socket.off('roomJoined', handleRoomJoined);
      socket.off('roomError', handleRoomError);
    };
  }, [socket, navigate, game.id]);

  const handleCreateRoom = (options) => {
    setIsJoining(true);
    socket.emit('createRoom', { gameId: game.id, options });
  };

  const handleJoinRoom = (roomId) => {
    setIsJoining(true);
    socket.emit('joinRoom', roomId);
  };

  if (isJoining) {
    return (
        <GlassCard title="입장 중...">
            <div className="p-8 text-center">
                <p className="animate-pulse text-white/80">게임방에 입장하는 중입니다. 잠시만 기다려주세요...</p>
            </div>
        </GlassCard>
    );
  }

  return (
    <>
      <GlassCard title={game.name}>
        <div className="p-4">
          <p className="mb-4 text-white/80">{game.description}</p>
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-bold">게임방 목록</h2>
            <Button onClick={() => setIsCreateDialogOpen(true)}>새로운 방 만들기</Button>
          </div>
          <div className="space-y-2">
            {rooms.length > 0 ? (
              rooms.map(room => (
                <div key={room.id} className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
                  <div>
                    <p className="font-bold">{room.players[0]?.name}님의 방</p>
                    <p className="text-xs text-white/60">플레이어: {room.players.length}</p>
                  </div>
                  <Button variant="secondary" onClick={() => handleJoinRoom(room.id)}>참가</Button>
                </div>
              ))
            ) : (
              <p className="text-center text-white/70 py-8">활성화된 게임방이 없습니다. 새로 만들어보세요!</p>
            )}
          </div>
        </div>
      </GlassCard>
      <CreateRoomDialog 
        open={isCreateDialogOpen}
        onOpenChange={setIsCreateDialogOpen}
        onCreate={handleCreateRoom}
        gameId={game.id}
      />
    </>
  );
}
