import React, { createContext, useContext, useMemo, useEffect, useState } from 'react';
import { useAuth } from './AuthContext';
import { fetchThemes } from '../api/starcadeApi';

// Shell Components
const ShellComponents = {
  ArcadeShell: React.lazy(() => import('/src/themes/arcade/ArcadeShell.jsx')),
  GeneralShell: React.lazy(() => import('/src/themes/general/GeneralShell.jsx')),
};

const ThemeContext = createContext();

export const ThemeProvider = ({ children }) => {
  const { user } = useAuth();
  const [allThemes, setAllThemes] = useState([]);

  useEffect(() => {
    const loadThemes = async () => {
        const themeData = await fetchThemes();
        if (themeData.success) {
            setAllThemes(themeData.themes);
        }
    };
    loadThemes();
  }, []);

  const { selectedTheme, CurrentShell, isArcadeMode } = useMemo(() => {
    const selectedThemeId = user?.settings?.theme || 'default';
    const theme = allThemes.find(t => t.id === selectedThemeId) || allThemes.find(t => t.id === 'default');
    
    const shell = theme?.shell || 'GeneralShell';
    const currentShellComponent = ShellComponents[shell] || ShellComponents.GeneralShell;
    const arcadeMode = shell === 'ArcadeShell';

    return { selectedTheme: theme, CurrentShell: currentShellComponent, isArcadeMode: arcadeMode };
  }, [user?.settings?.theme, allThemes]);

  // Apply theme colors to the document
  useEffect(() => {
    const getRandomArcadeColors = () => {
        const hue = Math.floor(Math.random() * 360);
        const cabinetColor = `hsl(${hue}, 50%, 85%)`;
        const shadowColor = `hsl(${hue}, 40%, 20%)`;
        const borderColor = `hsl(${hue}, 20%, 70%)`;
        const glowColor = `hsl(${hue}, 100%, 70%)`;
        return {
          '--cabinet-color': cabinetColor,
          '--cabinet-shadow-color': shadowColor,
          '--cabinet-border-color': borderColor,
          '--glow-color': glowColor,
        };
    };

    if (selectedTheme) {
        if (selectedTheme.id === 'arcade' && selectedTheme.shell === 'ArcadeShell') {
            const randomColors = getRandomArcadeColors();
            Object.keys(randomColors).forEach(key => {
                document.documentElement.style.setProperty(key, randomColors[key]);
            });
        } else if (selectedTheme.colors) {
            Object.keys(selectedTheme.colors).forEach(key => {
                document.documentElement.style.setProperty(key, selectedTheme.colors[key]);
            });
        }
    } else {
        // Fallback to default or clear styles if no theme is selected
    }
  }, [selectedTheme]);


  const value = {
    selectedTheme,
    CurrentShell,
    isArcadeMode,
    allThemes, // Pass all themes to the context
  };

  return (
    <ThemeContext.Provider value={value}>
      {children}
    </ThemeContext.Provider>
  );
};

export const useTheme = () => useContext(ThemeContext);
