const gameModules = import.meta.glob('./*.jsx', { eager: true });

export const gameComponentMap = {};

for (const path in gameModules) {
  const module = gameModules[path];
  // Ensure the module has both meta and a default export
  if (module && module.meta && module.default) {
    gameComponentMap[module.meta.id] = module.default;
  }
}