import React from 'react';

const Input = ({ label, id, value, onChange, type = 'text', placeholder, required = false, className = '', ...props }) => {
  const inputId = id || label;
  return (
    <div className={`w-full ${className}`}>
      {label && <label htmlFor={inputId} className="block text-sm font-medium text-white/80 mb-1">{label}</label>}
      <input
        type={type}
        id={inputId}
        name={inputId}
        value={value}
        onChange={onChange}
        placeholder={placeholder}
        required={required}
        {...props}
        className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg shadow-sm placeholder-white/40 focus:outline-none focus:ring-2 focus:ring-emerald-500 transition"
      />
    </div>
  );
};

export default Input;
