import React, { useMemo } from 'react';
import { motion } from 'framer-motion';
import config from './config';

// ... (Spark 컴포넌트는 이전과 동일)
// 파편(불꽃)을 위한 개별 컴포넌트
const Spark = ({ angle, distance, size, duration }) => {
  const x = distance * Math.cos(angle);
  const y = distance * Math.sin(angle);

  return (
    <motion.div
      className="absolute rounded-full"
      style={{
        width: size,
        height: size,
        backgroundColor: 'rgba(255, 200, 100, 0.8)',
        boxShadow: '0 0 10px rgba(255, 255, 0, 1)',
      }}
      initial={{ x: 0, y: 0, opacity: 1, scale: 0 }}
      animate={{ x, y, opacity: 0, scale: [1, 0.5] }}
      transition={{ duration: duration / 1000, ease: 'easeOut' }}
    />
  );
};


// [수정] 연기 입자를 위한 컴포넌트
const SmokeParticle = ({ delay, duration, size, finalScale, startOpacity }) => {
  // 연기 입자의 불규칙한 초기 위치 및 확산 방향 설정
  const angle = Math.random() * 2 * Math.PI;
  const initialDistance = Math.random() * 20; // 초기에는 중앙에 가깝게
  const finalDistance = Math.random() * 100 + 50; // 최종 확산 거리
  
  const initialX = initialDistance * Math.cos(angle);
  const initialY = initialDistance * Math.sin(angle);
  const finalX = finalDistance * Math.cos(angle);
  const finalY = finalDistance * Math.sin(angle);

  // 불규칙한 border-radius를 위한 값들
  const borderRadiusTL = `${Math.random() * 50 + 50}%`;
  const borderRadiusTR = `${Math.random() * 50 + 50}%`;
  const borderRadiusBL = `${Math.random() * 50 + 50}%`;
  const borderRadiusBR = `${Math.random() * 50 + 50}%`;

  return (
    <motion.div
      className="absolute" // rounded-full 제거하여 다양한 border-radius 적용 가능하게 함
      style={{
        // radial-gradient로 연기 내부의 밀도 표현
        background: `radial-gradient(circle at center, 
                     rgba(100, 100, 100, ${startOpacity + 0.1}) 0%, 
                     rgba(50, 50, 50, ${startOpacity}) 50%, 
                     rgba(30, 30, 30, 0) 100%)`,
        width: size,
        height: size,
        filter: 'blur(3px)', // 가장자리를 더 부드럽게
        borderRadius: `${borderRadiusTL} ${borderRadiusTR} ${borderRadiusBR} ${borderRadiusBL} / ${borderRadiusBR} ${borderRadiusTL} ${borderRadiusTR} ${borderRadiusBL}`, // 불규칙한 형태로 만듦
      }}
      initial={{ 
        x: initialX, 
        y: initialY, 
        scale: 0, 
        opacity: 0, 
        rotate: Math.random() * 360 // 초기 랜덤 회전
      }}
      animate={{
        x: finalX,
        y: finalY,
        scale: finalScale,
        opacity: [0, startOpacity * 1.2, startOpacity * 0.8, 0], // 연기처럼 피어오르다 사라지도록 투명도 조정
        rotate: Math.random() * 720 - 360 // 최종 랜덤 회전
      }}
      transition={{
        delay: delay / 1000,
        duration: duration / 1000,
        ease: 'easeOut',
        times: [0, 0.3, 0.7, 1] // 투명도 변화 시점 조정
      }}
    />
  );
};


const Explosion = ({ duration = config.duration }) => {
  const sparks = useMemo(() => {
    return Array.from({ length: config.sparkCount }).map(() => ({
      angle: Math.random() * 2 * Math.PI,
      distance: Math.random() * 250 + 50,
      size: Math.random() * 5 + 2,
      duration: duration * (Math.random() * 0.4 + 0.6),
    }));
  }, [duration]);

  const smokeParticles = useMemo(() => {
    return Array.from({ length: config.smokeCount }).map(() => ({
      delay: duration * 0.3,
      duration: duration * (Math.random() * 0.8 + 0.5),
      size: Math.random() * 100 + 50,
      finalScale: Math.random() * 1.5 + 1,
      startOpacity: Math.random() * 0.4 + 0.3 // 연기 초기 투명도 다양화
    }));
  }, [duration]);

  return (
    <motion.div
      className="fixed inset-0 flex items-center justify-center pointer-events-none z-[101]"
      initial={{ display: 'flex' }}
      animate={{}}
      transition={{
        delay: duration / 1000,
        onComplete: (e) => {
          e.style.display = 'none';
        },
      }}
    >
      {/* 1. 화면 전체를 덮는 밝은 섬광 */}
      <motion.div
        className="absolute w-full h-full bg-white"
        initial={{ opacity: 0 }}
        animate={{ opacity: [0, 0.8, 0] }}
        transition={{ duration: 0.3, times: [0, 0.1, 1] }}
      />

      {/* 2. 중앙 화염 */}
      <motion.div
        className="w-1 h-1 rounded-full"
        style={{
          background:
            'radial-gradient(circle at center, rgba(255, 255, 220, 1) 0%, rgba(255, 180, 0, 0.9) 30%, rgba(210, 80, 0, 0) 70%)',
        }}
        initial={{ scale: 0, opacity: 1 }}
        animate={{ scale: 300, opacity: [1, 1, 0] }}
        transition={{ duration: duration / 1000, ease: 'easeOut', times: [0, 0.5, 1] }}
      />
      
      {/* 3. 흩어지는 파편들 */}
      <div className="absolute">
        {sparks.map((spark, i) => (
          <Spark key={i} {...spark} />
        ))}
      </div>

      {/* 4. 피어나는 연기 */}
      <div className="absolute">
        {smokeParticles.map((smoke, i) => (
          <SmokeParticle key={i} {...smoke} />
        ))}
      </div>
    </motion.div>
  );
};

export default Explosion;