// Vite's import.meta.glob is used to import all matching files.
const modules = import.meta.glob('./patterns/**/index.jsx', { eager: true });
const configs = import.meta.glob('./patterns/**/config.js', { eager: true });
const cssUrls = import.meta.glob('./patterns/**/style.css', { query: '?url', eager: true });

// Create the automated decorations map.
const decorations = {};

for (const path in modules) {
  // Extract the decoration name (folder name) from the path.
  const decorationName = path.split('/')[2];

  if (decorationName) {
    const cssPath = `./patterns/${decorationName}/style.css`;
    const componentPath = `./patterns/${decorationName}/index.jsx`;
    const configPath = `./patterns/${decorationName}/config.js`;

    decorations[decorationName] = {
      Component: modules[componentPath]?.default,
      cssUrl: cssUrls[cssPath],
      config: configs[configPath]?.default || {},
    };
  }
}

export default decorations;