import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import GlassCard from '../components/ui/GlassCard';
import Button from '../components/ui/Button';

export default function MultiplayerLobbyPage() {
  const { user, socket } = useAuth();
  const navigate = useNavigate();
  const [rooms, setRooms] = useState([]);
  const [isJoining, setIsJoining] = useState(false);

  useEffect(() => {
    if (!socket) return;

    socket.emit('getRooms');

    const handleRoomsList = (roomList) => setRooms(roomList);
    const handleRoomJoined = (room) => {
        setIsJoining(false);
        navigate(`/games/${room.gameId}/${room.id}`);
    };
    const handleRoomError = (error) => {
        setIsJoining(false);
        console.error("Room Error:", error.message);
    };

    socket.on('roomsList', handleRoomsList);
    socket.on('roomJoined', handleRoomJoined);
    socket.on('roomError', handleRoomError);

    return () => {
      socket.off('roomsList', handleRoomsList);
      socket.off('roomJoined', handleRoomJoined);
      socket.off('roomError', handleRoomError);
    };
  }, [socket, navigate]);

  const handleJoinRoom = (roomId) => {
    setIsJoining(true);
    socket.emit('joinRoom', roomId);
  };

  if (!user) {
    return (
        <GlassCard title="접근 불가">
            <p className="p-4 text-center text-white/70">멀티플레이 로비에 들어오려면 로그인이 필요합니다.</p>
        </GlassCard>
    );
  }

  if (isJoining) {
    return (
        <GlassCard title="입장 중...">
            <div className="p-8 text-center">
                <p className="animate-pulse text-white/80">게임방에 입장하는 중입니다. 잠시만 기다려주세요...</p>
            </div>
        </GlassCard>
    );
  }

  return (
    <GlassCard title="전체 멀티플레이 로비">
      <div className="p-4">
        <h2 className="text-xl font-bold mb-4">현재 활성화된 모든 게임방</h2>
        <div className="space-y-2">
          {rooms.length > 0 ? (
            rooms.map(room => (
              <div key={room.id} className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
                <div>
                  <p className="font-bold">{room.gameId} ({room.options.boardSize}x{room.options.boardSize})</p>
                  <p className="text-xs text-white/60">호스트: {room.players[0]?.name} | 플레이어: {room.players.length}</p>
                </div>
                <Button variant="secondary" onClick={() => handleJoinRoom(room.id)}>참가</Button>
              </div>
            ))
          ) : (
            <p className="text-center text-white/70 py-8">활성화된 게임방이 없습니다.</p>
          )}
        </div>
      </div>
    </GlassCard>
  );
}
