import React, { useState, useEffect, useMemo } from 'react';
import Button from '../ui/Button';
import Input from '../ui/Input';
import { useAuth } from '../../context/AuthContext';
import * as api from '../../api/starcadeApi';
import { Check, X } from 'lucide-react';

// Reusable component for managing a list of items
const ItemManagementList = ({ title, items, ownedItemIds, onToggle, itemType, iconResolver }) => {
    const [searchTerm, setSearchTerm] = useState('');

    const filteredItems = useMemo(() => 
        items.filter(item => 
            item.name.toLowerCase().includes(searchTerm.toLowerCase())
        ), 
    [items, searchTerm]);

    return (
        <div className="mb-6">
            <h4 className="font-semibold mb-2">{title} ({items.length})</h4>
            <Input 
                value={searchTerm} 
                onChange={(e) => setSearchTerm(e.target.value)} 
                placeholder={`${title} 검색...`}
                className="mb-2"
            />
            <div className="space-y-2 max-h-48 overflow-y-auto p-2 bg-black/20 rounded-md">
                {filteredItems.map(item => {
                    const hasItem = ownedItemIds.includes(item.id);
                    return (
                        <div key={item.id} className="flex items-center justify-between text-sm">
                            <span className="flex items-center gap-2">
                                {iconResolver(item) && <span className="text-lg">{iconResolver(item)}</span>}
                                {item.name}
                            </span>
                            <Button 
                                onClick={() => onToggle(itemType, item.id, !hasItem)}
                                variant={hasItem ? 'danger' : 'primary'}
                                className="h-7 w-10 flex items-center justify-center text-xs p-0"
                            >
                                {hasItem ? <X size={16} /> : <Check size={16} />}
                            </Button>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

const MasterControls = ({ targetUser, allAchievementDefs, onAction }) => {
    const { triggerEffect } = useAuth();
    const [allItems, setAllItems] = useState({
        titles: [],
        themes: [],
        profileEffects: [],
        profileDecorations: [],
        cardEffects: [],
        cardDecorations: [],
    });

    useEffect(() => {
        const fetchAllItems = async () => {
            const [titles, themes, pEffects, pDecorations, cEffects, cDecorations] = await Promise.all([
                api.fetchAllTitles(),
                api.fetchThemes(),
                api.fetchProfileEffects(),
                api.fetchProfileDecorations(),
                api.fetchCardEffects(),
                api.fetchCardDecorations(),
            ]);
            setAllItems({
                titles: titles.success ? titles.titles.map(t => ({ id: t, name: t })) : [],
                themes: themes.success ? themes.themes : [],
                profileEffects: pEffects.success ? pEffects.profileEffects : [],
                profileDecorations: pDecorations.success ? pDecorations.profileDecorations : [],
                cardEffects: cEffects.success ? cEffects.cardEffects : [],
                cardDecorations: cDecorations.success ? cDecorations.cardDecorations : [],
            });
        };
        fetchAllItems();
    }, []);

    const handleToggleItem = async (itemType, itemId, grant) => {
        const result = await api.toggleUserItem(targetUser.id, itemType, itemId, grant);
        if (result.success) {
            triggerEffect('Toast', { message: `아이템이 ${grant ? '부여' : '회수'}되었습니다.`, type: 'success' });
            onAction(); // Refresh the profile view
        } else {
            triggerEffect('Toast', { message: result.message || '작업 실패', type: 'error' });
        }
    };

    const handleToggleAchievement = async (itemType, achievementId, grant) => {
        const apiCall = grant ? api.grantAchievement : api.revokeAchievement;
        const result = await apiCall(targetUser.id, achievementId);
        if (result.success) {
            triggerEffect('Toast', { message: `도전과제가 ${grant ? '부여' : '회수'}되었습니다.`, type: 'success' });
            onAction();
        } else {
            triggerEffect('Toast', { message: result.message || '작업 실패', type: 'error' });
        }
    };

    return (
        <div className="w-full p-4 mt-4 border-t-2 border-amber-500/30 bg-amber-900/20 rounded-lg">
            <h3 className="text-lg font-bold text-amber-400 mb-4">관리자 컨트롤</h3>

            <ItemManagementList 
                title="도전과제"
                items={allAchievementDefs}
                ownedItemIds={targetUser.achievements.map(a => a.id)}
                onToggle={handleToggleAchievement}
                itemType="achievement"
                iconResolver={(item) => item.icon}
            />

            <ItemManagementList 
                title="칭호"
                items={allItems.titles}
                ownedItemIds={targetUser.unlockedTitles || []}
                onToggle={handleToggleItem}
                itemType="title"
                iconResolver={() => '📜'}
            />

            <ItemManagementList 
                title="테마"
                items={allItems.themes}
                ownedItemIds={targetUser.unlockedThemes || []}
                onToggle={handleToggleItem}
                itemType="theme"
                iconResolver={(item) => item.icon || '🎨'}
            />

            <ItemManagementList 
                title="프로필 효과"
                items={allItems.profileEffects}
                ownedItemIds={targetUser.unlockedEffects || []}
                onToggle={handleToggleItem}
                itemType="profileEffect"
                iconResolver={(item) => item.icon || '✨'}
            />
            
            <ItemManagementList 
                title="프로필 장식"
                items={allItems.profileDecorations}
                ownedItemIds={targetUser.unlockedProfileDecorations || []}
                onToggle={handleToggleItem}
                itemType="profileDecoration"
                iconResolver={(item) => item.icon || '🎀'}
            />

            <ItemManagementList 
                title="카드 효과"
                items={allItems.cardEffects}
                ownedItemIds={targetUser.unlockedCardEffects || []}
                onToggle={handleToggleItem}
                itemType="cardEffect"
                iconResolver={(item) => item.icon || '🃏'}
            />

            <ItemManagementList 
                title="카드 장식"
                items={allItems.cardDecorations.filter(item => item.id !== 'None')}
                ownedItemIds={targetUser.unlockedCardDecorations || []}
                onToggle={handleToggleItem}
                itemType="cardDecoration"
                iconResolver={(item) => item.icon || '🎴'}
            />
        </div>
    );
};

export default MasterControls;
