import React from 'react';
import { motion } from 'framer-motion';

const WindStreak = ({ y, duration, delay, width, opacity }) => (
    <motion.div
        className="absolute h-0.5 bg-gradient-to-r from-transparent to-gray-400"
        style={{ y, width, opacity }}
        initial={{ x: -width }}
        animate={{ x: window.innerWidth }}
        transition={{
            duration,
            delay,
            ease: "linear",
            repeat: Infinity,
            repeatDelay: 1
        }}
    />
);


const Wind = ({ duration = 2500 }) => {
    const streaks = Array.from({ length: 30 }).map((_, i) => ({
        id: i,
        y: Math.random() * window.innerHeight,
        duration: Math.random() * 1 + 0.5,
        delay: Math.random() * 2,
        width: Math.random() * 200 + 100,
        opacity: Math.random() * 0.3 + 0.1,
    }));

    return (
        <motion.div 
            className="fixed inset-0 pointer-events-none z-[101] overflow-hidden"
            initial={{ opacity: 0 }}
            animate={{ opacity: [0, 1, 1, 0] }}
            transition={{ duration: duration / 1000, times: [0, 0.2, 0.8, 1] }}
        >
            {streaks.map(s => <WindStreak key={s.id} {...s} />)}
        </motion.div>
    );
};

export default Wind;