import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { getJokerById } from './cards';

const sizeClasses = {
  small: 'w-16 h-24',
  large: 'w-24 h-36',
};

const fontSizes = {
    small: { rank: 'text-sm', suit: 'text-2xl' },
    large: { rank: 'text-xl', suit: 'text-5xl' },
}

const CardDecoration = ({ effectId, isHovered }) => {
    if (effectId !== 'Birthday') return null;

    return (
        <div className="absolute inset-0 pointer-events-none z-50">
            <motion.div 
                className="absolute top-0 left-0 text-lg" 
                style={{transform: 'translate(-30%, -30%)'}}
                animate={{ rotate: [0, 10, -10, 0], scale: [1, 1.1, 1] }}
                transition={{ duration: 1.5, repeat: Infinity, ease: 'easeInOut' }}
            >
                🎉
            </motion.div>
            <motion.div 
                className="absolute bottom-0 right-0 text-lg"
                style={{transform: 'translate(30%, 30%)'}}
                animate={{ rotate: [0, -10, 10, 0], scale: [1, 1.1, 1] }}
                transition={{ duration: 1.5, repeat: Infinity, ease: 'easeInOut' }}
            >
                🎁
            </motion.div>
            <AnimatePresence>
                {isHovered && Array.from({ length: 10 }).map((_, i) => (
                    <motion.div
                        key={i}
                        className="absolute text-xs"
                        style={{
                            top: '50%',
                            left: '50%',
                            color: ['#ff6b6b', '#f06595', '#cc5de8', '#845ef7', '#5c7cfa'][i % 5],
                        }}
                        initial={{ opacity: 1, scale: 0, x: '-50%', y: '-50%' }}
                        animate={{
                            x: `calc(-50% + ${(Math.random() - 0.5) * 80}px)`,
                            y: `calc(-50% + ${(Math.random() - 0.5) * 80}px)`,
                            scale: [0, 1, 0],
                            opacity: [1, 1, 0],
                        }}
                        exit={{ opacity: 0 }}
                        transition={{ duration: 0.6, delay: Math.random() * 0.2, ease: 'easeOut' }}
                    >
                        ✨
                    </motion.div>
                ))}
            </AnimatePresence>
        </div>
    );
};

export default function Card({ r: rank, s: suit, hidden = false, index = 0, result = null, feedback = null, jokerId = null, size = 'small', cardEffect = null, cardDecoration = null }) {
  console.log('Card props:', { rank, suit, cardEffect, cardDecoration });
  const [isHovered, setIsHovered] = useState(false);
  const isRed = suit === '♥' || suit === '♦';
  const isJoker = rank === 'Joker';
  
  const currentSize = sizeClasses[size] || sizeClasses.small;
  const currentFonts = fontSizes[size] || fontSizes.small;

  let cardBg = 'bg-white';
  if (result === 'win') cardBg = 'bg-emerald-200';
  if (result === 'lose') cardBg = 'bg-red-200';
  if (cardEffect === 'Birthday') cardBg = 'birthday-gradient-bg-animated';

  const feedbackBorder = feedback === 'correct' ? 'border-emerald-500 ring-4 ring-emerald-400' : feedback === 'incorrect' ? 'border-red-500 ring-4 ring-red-400' : 'border-transparent';

  if (hidden) {
    return (
      <motion.div initial={{ y: -50, opacity: 0 }} animate={{ y: 0, opacity: 1 }} transition={{ delay: index * 0.1 }}
        className={`${currentSize} rounded-lg bg-red-900 border-2 border-red-950 flex items-center justify-center text-white font-bold shadow-lg`}>
        <span className="text-red-500 text-4xl">?</span>
      </motion.div>
    );
  }

  if (isJoker && jokerId) {
    const jokerData = getJokerById(jokerId);
    if (!jokerData) return <div className={`${currentSize} rounded-lg bg-gray-500`} />;
    
    return (
      <motion.div 
        initial={{ y: -50, opacity: 0 }} 
        animate={{ y: 0, opacity: 1 }}
        exit={{ y: 50, opacity: 0 }} 
        transition={{ duration: 0.3, ease: "easeOut" }}
        className={`group ${currentSize} rounded-lg shadow-lg grid place-items-center p-1 relative overflow-hidden ${jokerData.className}`}>
        
        <div className="absolute top-0 left-[-100%] w-full h-full bg-gradient-to-r from-transparent via-white/30 to-transparent transition-transform duration-700 ease-in-out group-hover:translate-x-[200%] z-10" />
        <div className={`absolute inset-0 rounded-lg border-4 transition-colors duration-300 ${feedbackBorder}`} />
        
        <div className="text-5xl z-0" style={{ transformStyle: "preserve-3d" }}>
            <div style={{ transform: "translateZ(20px)" }}>
                {jokerData.emoji || '🃏'}
            </div>
        </div>
        <CardDecoration effectId={cardDecoration} isHovered={isHovered} />
      </motion.div>
    );
  }

  const textColor = isRed ? 'text-red-500' : 'text-slate-900';
  return (
    <motion.div 
      onHoverStart={() => setIsHovered(true)}
      onHoverEnd={() => setIsHovered(false)}
      initial={{ y: -50, opacity: 0, scale: 0.8 }} 
      animate={{ y: 0, opacity: 1, scale: 1 }} 
      exit={{ y: 50, opacity: 0, scale: 0.8 }} 
      transition={{ type: "spring", stiffness: 300, damping: 25 }}
      className={`group ${currentSize} rounded-lg ${cardBg} border-white/20 shadow-md flex flex-col justify-between p-1 relative overflow-hidden`}>
      
      <div className="absolute top-0 left-[-100%] w-full h-full bg-gradient-to-r from-transparent via-white/20 to-transparent transition-transform duration-700 ease-in-out group-hover:translate-x-[200%]" />
      <div className={`absolute inset-0 rounded-lg border-4 transition-colors duration-300 ${feedbackBorder}`} />
      
      <div className={`relative z-10 ${currentFonts.rank} font-bold ${textColor}`}>{rank}</div>
      <div className={`relative z-10 ${currentFonts.suit} text-center ${textColor}`}>{suit}</div>
      <div className={`relative z-10 ${currentFonts.rank} font-bold text-right ${textColor}`}>{rank}</div>
      <CardDecoration effectId={cardDecoration} isHovered={isHovered} />
    </motion.div>
  );
}