import React, { useEffect, useRef, useState, useCallback } from 'react';
import Button from '../components/ui/Button';
import { motion } from 'framer-motion';
import PauseOverlay from '../components/PauseOverlay';
import { useEffects } from '../effects/EffectLayer';
import { Card, createDeck, getRandomJoker, getJokerById } from '../components/cards';
import SubmitScoreDialog from '../components/ui/SubmitScoreDialog';
import { useAuth } from '../context/AuthContext';
import { submitScore } from '../lib/scoreManager';
import { notifyGameOver } from '../api/starcadeApi';

export const meta = { id:'blackjack', name:'블랙잭', icon:'♠', topic:'blackjack', description:'딜러와의 눈치싸움', category: 'single-player' };

const GAME_ID = meta.id;
const JOKER_CHANCE = 0.1;
const RESHUFFLE_THRESHOLD = 10;

const calculateHandValue = (hand) => {
  if (!hand || hand.length === 0) return 0;
  if (hand.some(c => c.r === 'Joker' && c.jokerId)) return 21;
  let total = 0, aces = 0;
  hand.forEach(({ r }) => {
    if (r === 'A') { aces++; total += 11; }
    else if (['J', 'Q', 'K'].includes(r)) total += 10;
    else if (r) total += parseInt(r);
  });
  while (total > 21 && aces > 0) { total -= 10; aces--; }
  return total;
};

export default function Blackjack({ isPaused, lastKeyEvent }){
  const keyProcessedRef = useRef(null);
  const mountTimeRef = useRef(Date.now());
  const { triggerEffect, stopAllEffects } = useEffects();
  const { user, updateUser } = useAuth();
  const [showSubmitScoreDialog, setShowSubmitScoreDialog] = useState(false);

  const createNewDeck = useCallback(() => {
    const specialCardsToAdd = [];
    if (Math.random() < JOKER_CHANCE) {
      const randomJoker = getRandomJoker();
      if (randomJoker) specialCardsToAdd.push(randomJoker.id);
    }
    return createDeck({ numDecks: 2, specialCardsToAdd });
  }, []);

  const getInitialState = () => {
      try {
          const savedState = localStorage.getItem(`starcade-gamestate-${GAME_ID}`);
          if (savedState) {
              const parsed = JSON.parse(savedState);
              if (parsed.deck && parsed.player && parsed.dealer) {
                  return { ...parsed, processedJokers: new Set(parsed.processedJokers || []) };
              }
          }
      } catch (e) {
          console.error("Failed to load saved game state", e);
      }
      return {
          deck: createNewDeck(),
          player: [],
          dealer: [],
          gameState: 'betting',
          message: '게임을 시작하세요!',
          gameResult: null,
          totalScore: 0,
          wins: 0,
          losses: 0,
          pushes: 0,
          isNaturalBlackjack: false,
          isBust: false,
          isDealerBust: false,
          processedJokers: new Set(),
      };
  };

  const [state, setState] = useState(getInitialState);

  useEffect(() => {
      try {
          const stateToSave = { ...state, processedJokers: Array.from(state.processedJokers) };
          localStorage.setItem(`starcade-gamestate-${GAME_ID}`, JSON.stringify(stateToSave));
      } catch (e) {
          console.error("Failed to save game state", e);
      }
  }, [state]);

  const showJokerToast = useCallback((jokerCard) => {
    if (!jokerCard?.jokerId || state.processedJokers.has(jokerCard.jokerId)) return;
    
    const newProcessedJokers = new Set(state.processedJokers).add(jokerCard.jokerId);
    setState(s => ({ ...s, processedJokers: newProcessedJokers }));

    const jokerData = getJokerById(jokerCard.jokerId);
    if (jokerData) {
      triggerEffect('Toast', { message: `${jokerData.name}: ${jokerData.description}`, duration: 4000, type: jokerData.id });
      switch (jokerData.id) {
        case 'clown': triggerEffect('Wiggle', { duration: 900 }); triggerEffect('Laughing'); break;
        case 'golden': triggerEffect('Money'); break;
        case 'cosmic': triggerEffect('Stars'); break;
        default: break;
      }
    }
  }, [triggerEffect, state.processedJokers]);

  const stand = useCallback(async () => {
    if (state.gameState !== 'playerTurn') return;
    setState(s => ({ ...s, gameState: 'dealerTurn', message: '딜러의 턴...' }));
    
    let currentDealerHand = [...state.dealer];
    let deck = [...state.deck];
    while (calculateHandValue(currentDealerHand) < 17 && deck.length > 0) {
      await new Promise(resolve => setTimeout(resolve, 800));
      const newCard = deck.pop();
      if (!newCard) break;
      currentDealerHand.push(newCard);
      setState(s => ({ ...s, dealer: [...currentDealerHand], deck: [...deck] }));
      if (newCard.jokerId) {
        showJokerToast(newCard);
        break;
      }
    }

    const playerValue = calculateHandValue(state.player);
    const dealerValue = calculateHandValue(currentDealerHand);
    let resultMessage = ''; 
    let result = null;
    let scoreChange = 0;
    let winChange = 0, lossChange = 0, pushChange = 0;
    let bust = state.isBust, dealerBust = false;

    if (playerValue > 21) { resultMessage = '버스트! 딜러 승리!'; result = 'lose'; triggerEffect('Defeat'); lossChange = 1; scoreChange = -1; bust = true; }
    else if (dealerValue > 21) { resultMessage = '딜러 버스트! 플레이어 승리!'; result = 'win'; triggerEffect('Confetti'); winChange = 1; scoreChange = 1; dealerBust = true; }
    else if (playerValue > dealerValue) { resultMessage = '플레이어 승리!'; result = 'win'; triggerEffect('Confetti'); winChange = 1; scoreChange = 1; }
    else if (playerValue < dealerValue) { resultMessage = '딜러 승리!'; result = 'lose'; triggerEffect('Defeat'); lossChange = 1; scoreChange = -1; }
    else { resultMessage = '무승부!'; result = 'push'; pushChange = 1; }
    
    setState(s => ({
        ...s,
        message: resultMessage,
        gameResult: result,
        gameState: 'finished',
        totalScore: s.totalScore + scoreChange,
        wins: s.wins + winChange,
        losses: s.losses + lossChange,
        pushes: s.pushes + pushChange,
        isBust: bust,
        isDealerBust: dealerBust,
    }));
  }, [state, triggerEffect, showJokerToast]);

  const dealCards = useCallback(() => {
    let deck = [...state.deck];
    if (deck.length < RESHUFFLE_THRESHOLD) {
      setShowSubmitScoreDialog(true);
      return;
    }

    const playerHand = [deck.pop(), deck.pop()];
    const dealerHand = [deck.pop(), deck.pop()];

    setState(s => ({
        ...s,
        deck,
        player: playerHand,
        dealer: dealerHand,
        gameState: 'playerTurn',
        message: '히트 또는 스탠드?',
        isNaturalBlackjack: calculateHandValue(playerHand) === 21 && playerHand.length === 2,
    }));
  }, [state.deck]);

  const nextGame = useCallback(() => {
    stopAllEffects();
    setState(s => ({
        ...s,
        gameResult: null,
        processedJokers: new Set(),
        isNaturalBlackjack: false,
        isBust: false,
        isDealerBust: false,
    }));
    dealCards();
  }, [dealCards, stopAllEffects]);
  
  const startGame = useCallback(() => {
      localStorage.removeItem(`starcade-gamestate-${GAME_ID}`);
      const newDeck = createNewDeck();
      const playerHand = [newDeck.pop(), newDeck.pop()];
      const dealerHand = [newDeck.pop(), newDeck.pop()];
      setState({
          deck: newDeck,
          player: playerHand,
          dealer: dealerHand,
          gameState: 'playerTurn',
          message: '히트 또는 스탠드?',
          gameResult: null, totalScore: 0, wins: 0, losses: 0, pushes: 0,
          isNaturalBlackjack: calculateHandValue(playerHand) === 21 && playerHand.length === 2,
          isBust: false, isDealerBust: false, processedJokers: new Set(),
      });
  }, [createNewDeck]);

  const hit = useCallback(() => {
    if (state.gameState !== 'playerTurn' || state.player.some(c => c.jokerId)) return;
    let deck = [...state.deck];
    if (deck.length === 0) { setShowSubmitScoreDialog(true); return; }
    
    const newCard = deck.pop();
    if (!newCard) return;
    
    setState(s => ({ ...s, player: [...s.player, newCard], deck }));
  }, [state.gameState, state.player, state.deck]);

  useEffect(() => {
    if (state.gameState !== 'playerTurn' || state.player.length === 0) return;

    const dealtJoker = state.player.find(card => card.jokerId && !state.processedJokers.has(card.jokerId));
    if (dealtJoker) {
      showJokerToast(dealtJoker);
      setState(s => ({ ...s, message: '조커! 21 고정!' }));
      stand();
      return;
    }

    if (calculateHandValue(state.player) > 21) {
      triggerEffect('Defeat');
      setState(s => ({
          ...s,
          message: '버스트! 딜러 승리',
          gameResult: 'lose',
          losses: s.losses + 1,
          totalScore: s.totalScore - 1,
          gameState: 'finished',
          isBust: true,
      }));
    }
  }, [state.player, state.gameState, stand, showJokerToast, triggerEffect, state.processedJokers]);
  
  useEffect(() => {
    if (!lastKeyEvent || isPaused || Date.now() - mountTimeRef.current < 500 || keyProcessedRef.current === lastKeyEvent.time) return;
    
    keyProcessedRef.current = lastKeyEvent.time;
    const lowerKey = lastKeyEvent.key.toLowerCase();

    if (state.gameState === 'playerTurn') {
      if (lowerKey === 'h') hit();
      if (lowerKey === 's') stand();
    }
    if (state.gameState === 'finished' && lowerKey === 'r') nextGame();
  }, [lastKeyEvent, isPaused, hit, stand, nextGame, state.gameState]);

  const handleSubmitScore = () => {
    const scoreData = { 
      gameId: meta.id, 
      score: state.totalScore, 
      user, 
      options: { 
        isWin: state.gameResult === 'win',
        isNaturalBlackjack: state.isNaturalBlackjack,
        isBust: state.isBust,
        isDealerBust: state.isDealerBust,
        pushes: state.pushes,
      }
    };
    submitScore(scoreData, triggerEffect, updateUser);
    setShowSubmitScoreDialog(false);
    startGame();
  };

  const handleCloseSubmitDialog = () => {
    if (user) {
        notifyGameOver({
            gameId: meta.id,
            score: state.totalScore,
            userId: user.id
        }).then(result => {
            if (result.success && result.user) {
                updateUser(result.user, true);
            }
        });
    }
    setShowSubmitScoreDialog(false);
    startGame();
  };

  return (
    <div className="flex flex-col gap-4 items-center relative">
      {state.gameState === 'betting' ? (
        <div className="text-center p-8">
          <h2 className="text-2xl font-bold mb-4">블랙잭에 오신 것을 환영합니다!</h2>
          <p className="text-white/80">딜러와 21에 가까운 숫자를 만들어 승부하세요.</p>
          <p className="text-white/80 mb-8">J, Q, K는 10으로, A는 1 또는 11로 계산됩니다.</p>
        </div>
      ) : (
        <>
          <div className="text-sm text-white/70">덱에 남은 카드: {state.deck.length}장</div>
          <div className="text-sm text-white/70">이번 덱 총점: {state.totalScore} (승: {state.wins} / 패: {state.losses} / 무: {state.pushes})</div>
          
          <div className="flex flex-col items-center gap-2 min-h-[6rem]">
            <div className="text-sm text-white/70">딜러 ({state.gameState !== 'playerTurn' && state.gameState !== 'betting' ? calculateHandValue(state.dealer) : (state.dealer.length > 0 ? calculateHandValue([state.dealer[0]]) : 0)})</div>
            <div className="flex gap-2">
              {state.dealer.map((card, index) => (<Card key={`${card.r}-${card.s}-${index}-d`} {...card} size="small" hidden={state.gameState === 'playerTurn' && index === 1} index={index} />))}
            </div>
          </div>
          <div className="flex flex-col items-center gap-2 min-h-[6rem]">
            <div className="text-sm text-white/70">플레이어 ({calculateHandValue(state.player)})</div>
            <div className="flex gap-2">
              {state.player.map((card, index) => (<Card key={`${card.r}-${card.s}-${index}-p`} {...card} size="small" index={index} result={state.gameResult} />))}
            </div>
          </div>
          <div className="text-lg font-bold min-h-[2rem]">{state.message}</div>
        </>
      )}
      <div className="flex gap-2">
        {state.gameState === 'betting' && (
          <Button onClick={startGame}>게임 시작</Button>
        )}
        <Button variant="secondary" onClick={hit} disabled={state.gameState !== 'playerTurn'}>히트(H)</Button>
        <Button onClick={stand} disabled={state.gameState !== 'playerTurn'}>스탠드(S)</Button>
        <Button variant="secondary" onClick={nextGame} disabled={state.gameState !== 'finished'}>다음 라운드(R)</Button>
      </div>
      <PauseOverlay isPaused={isPaused} />
      <SubmitScoreDialog
          open={showSubmitScoreDialog}
          onOpenChange={setShowSubmitScoreDialog}
          score={state.totalScore}
          user={user}
          onSubmit={handleSubmitScore}
          onClose={handleCloseSubmitDialog}
      />
    </div>
  );
}