import React from 'react';
import { motion } from 'framer-motion';

const colors = ["#FFC300", "#FF5733", "#C70039", "#900C3F", "#581845", "#33FF57", "#3357FF"];

const SparkleParticle = ({ x, y, size, delay, duration }) => {
  const sparkleColors = ['#FFFFFF', '#FFD700', '#FFA500', '#FF69B4', '#8A2BE2', '#00BFFF']; // Pink, Purple, Blue added
  const color = sparkleColors[Math.floor(Math.random() * sparkleColors.length)];

  return (
    <motion.div
      className="absolute rounded-full"
      style={{
        left: x,
        top: y,
        width: size,
        height: size,
        backgroundColor: color,
        boxShadow: `0 0 ${size * 2}px ${color}`,
      }}
      initial={{ opacity: 0, scale: 0 }}
      animate={{ 
        opacity: [0, 1, 1, 0],
        scale: [0, 1, 1, 0],
      }}
      transition={{ 
        duration: duration,
        delay: delay,
        ease: 'easeInOut',
        times: [0, 0.2, 0.8, 1]
      }}
    />
  );
};

const FireworkParticle = ({ x, y, size, color, delay }) => {
    const destinationX = x + (Math.random() - 0.5) * 400;
    const destinationY = y + (Math.random() - 0.5) * 400;

    return (
        <motion.div
            className="absolute rounded-full"
            style={{ x, y, width: size, height: size, backgroundColor: color, boxShadow: `0 0 15px ${color}` }}
            initial={{ opacity: 1, scale: 0 }}
            animate={{ opacity: [1, 1, 0], scale: [0, 1.5, 0], x: destinationX, y: destinationY }}
            transition={{ duration: 1.8, delay, ease: "easeOut" }}
        />
    );
};

const FireworkBurst = ({ x, y, count = 70, burstDelay }) => {
    const particles = Array.from({ length: count }).map((_, i) => ({
        id: i,
        size: Math.random() * 5 + 2,
        color: colors[Math.floor(Math.random() * colors.length)],
        delay: burstDelay + Math.random() * 0.3,
    }));

    return <>{particles.map(p => <FireworkParticle key={p.id} x={x} y={y} {...p} />)}</>;
};

const PaperConfetti = ({ x, y, rotate, delay, duration }) => (
    <motion.div
        className="absolute w-3 h-5"
        style={{
            backgroundColor: colors[Math.floor(Math.random() * colors.length)],
        }}
        initial={{ opacity: 0, x: x, y: y, scale: 0.5, rotate: rotate }}
        animate={{ opacity: [0, 1, 1, 0], x: x + (Math.random() - 0.5) * window.innerWidth * 1.5, y: y + (Math.random() - 0.5) * window.innerHeight * 1.5, scale: [0.5, 1, 1, 0.8], rotate: rotate + 720 }}
        transition={{ duration, delay, ease: "easeOut" }}
    />
);

const GiftBox = ({ x, y, delay, duration }) => (
    <motion.div
        className="absolute text-4xl"
        style={{ x, y }}
        initial={{ opacity: 0, y: -100, scale: 0.5 }}
        animate={{ opacity: [0, 1, 1, 0], y: [y, y + 50, window.innerHeight - 50, window.innerHeight + 50], scale: [0.5, 1, 1, 0.8] }}
        transition={{ duration, delay, ease: "easeOut", times: [0, 0.2, 0.8, 1] }}
    >
        🎁
    </motion.div>
);

const Birthday = ({ duration = 5000 }) => {
    const numFireworks = 3;
    const numConfetti = 300;
    const numGifts = 5;
    const numSparkles = 75;

    const fireworks = Array.from({ length: numFireworks }).map((_, index) => {
        const burstX = Math.random() * window.innerWidth;
        const burstY = Math.random() * (window.innerHeight * 0.7) + (window.innerHeight * 0.1);
        return {
            id: `firework-burst-${index}`,
            x: burstX,
            y: burstY,
            burstDelay: index * (duration / 1000 / numFireworks),
        };
    });

    const confettiPieces = Array.from({ length: numConfetti }).map((_, i) => {
        const isLeftCorner = Math.random() < 0.5; // Randomly choose left or right corner
        const startX = isLeftCorner ? 0 : window.innerWidth; // Start from left or right edge
        const startY = 0; // Start from top edge
        return {
            id: i,
            x: startX,
            y: startY,
            rotate: Math.random() * 360,
            delay: Math.random() * (duration / 1000),
            duration: 2,
        };
    });

    const giftBoxes = Array.from({ length: numGifts }).map((_, i) => ({
        id: i,
        x: Math.random() * window.innerWidth,
        y: -50,
        delay: Math.random() * (duration / 1000),
        duration: 3,
    }));

    const sparkles = Array.from({ length: numSparkles }).map((_, i) => ({
        id: i,
        x: `${Math.random() * 100}%`,
        y: `${Math.random() * 100}%`,
        size: Math.random() * 6 + 3,
        delay: Math.random() * (duration / 1000 - 0.5),
        duration: 0.5 + Math.random() * 0.5,
    }));

    return (
        <div className="fixed inset-0 pointer-events-none overflow-hidden">
            {fireworks.map(firework => (
                <FireworkBurst
                    key={firework.id}
                    x={firework.x}
                    y={firework.y}
                    burstDelay={firework.burstDelay}
                />
            ))}
            {confettiPieces.map(p => (
                <PaperConfetti key={p.id} x={p.x} y={p.y} rotate={p.rotate} delay={p.delay} duration={p.duration} />
            ))}
            {giftBoxes.map(g => (
                <GiftBox key={g.id} x={g.x} y={g.y} delay={g.delay} duration={g.duration} />
            ))}
            {sparkles.map(s => <SparkleParticle key={s.id} {...s} />)}
            <motion.div
                className="absolute inset-0 flex items-center justify-center"
                initial={{ opacity: 0, scale: 0.5 }}
                animate={{ opacity: [0, 1, 1, 0], scale: 1 }}
                transition={{ duration: duration / 1000, times: [0, 0.1, 0.9, 1] }}
            >
                <motion.div
                    className="text-6xl font-extrabold text-white text-center"
                    style={{ textShadow: '0 0 15px rgba(255,105,180,0.8), 0 0 25px rgba(255,105,180,0.5)' }}
                    initial={{ y: -20 }} // Keep the subtle vertical animation
                    animate={{ y: [ -20, 0, -20 ] }}
                    transition={{ repeat: Infinity, duration: 2, ease: "easeInOut" }}
                >
                    HAPPY BIRTHDAY!
                </motion.div>
            </motion.div>
        </div>
    );
};

export default Birthday;