import React from 'react';

const statusConfig = {
  online: {
    color: 'bg-emerald-500',
    label: '온라인',
  },
  away: {
    color: 'bg-amber-500',
    label: '자리 비움',
  },
  offline: {
    color: 'bg-gray-500',
    label: '오프라인',
  },
};

export default function StatusIndicator({ status, connectionStatus, onlineStatus, className }) {
  let displayStatus;

  // If a specific 'status' is provided (for dropdown options), use it directly.
  if (status) {
    displayStatus = status;
  } 
  // Otherwise, determine the status based on connection.
  else {
    const isConnected = connectionStatus === 'connected';
    displayStatus = isConnected ? onlineStatus : 'offline';
  }

  const { color, label } = statusConfig[displayStatus] || statusConfig.offline;

  return (
    <div className={`flex items-center gap-2 text-sm font-semibold ${className}`}>
      <div className={`w-2.5 h-2.5 rounded-full ${color}`}></div>
      <span className="whitespace-nowrap">{label}</span>
    </div>
  );
}