import React from 'react';
import { motion } from 'framer-motion';

// 해 SVG 데이터 (가독성을 위해 간략화)
// 실제 사용 시에는 더 복잡한 SVG 경로가 들어갈 수 있습니다.
const SunSVG = ({ color = "white", flameColor = "orange" }) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    viewBox="0 0 512 512"
    fill="currentColor"
    style={{ filter: `drop-shadow(0 0 10px ${flameColor})` }}
  >
    {/* 기본적인 해 형태 */}
    <path d="M256 0C114.6 0 0 114.6 0 256s114.6 256 256 256 256-114.6 256-256S397.4 0 256 0zm-70.9 313.3c-13.8 0-25-11.2-25-25s11.2-25 25-25 25 11.2 25 25-11.2 25-25 25zm141.8 0c-13.8 0-25-11.2-25-25s11.2-25 25-25 25 11.2 25 25-11.2 25-25 25zm-70.9-106.6c-48.4 0-87.7 39.3-87.7 87.7s39.3 87.7 87.7 87.7 87.7-39.3 87.7-87.7-39.3-87.7-87.7-87.7zm0 131.5c-24.1 0-43.8-19.7-43.8-43.8s19.7-43.8 43.8-43.8 43.8 19.7 43.8 43.8-19.7 43.8-43.8 43.8z"/>
    {/* 해 눈 부분 (타오르는 불꽃 효과) */}
    <circle cx="185.1" cy="288.3" r="15" fill={flameColor} />
    <circle cx="326.9" cy="288.3" r="15" fill={flameColor} />
    {/* 코 부분 */}
    <path d="M256 340c-16.6 0-30-13.4-30-30s13.4-30 30-30 30 13.4 30 30-13.4 30-30 30z" fill={flameColor} />
  </svg>
);


const Sun = ({ duration = 3000 }) => {
  return (
    <motion.div
      className="fixed inset-0 flex items-center justify-center pointer-events-none z-[101]"
      initial={{ opacity: 0, scale: 0.5, rotate: -30 }}
      animate={{ 
        opacity: [0, 1, 1, 0], // 나타났다 유지되다 사라짐
        scale: [0.5, 1, 1.2, 0.5], // 커졌다가 약간 더 커지고 줄어듬
        rotate: [-30, 0, 5, -30], // 회전하며 나타남
      }}
      transition={{ 
        duration: duration / 1000, 
        ease: "easeInOut",
        times: [0, 0.2, 0.8, 1] // 각 키프레임 도달 시간
      }}
      style={{
        // 불타는 효과를 위한 그림자
        filter: 'drop-shadow(0 0 20px rgba(255, 69, 0, 0.8))'
      }}
    >
      <motion.div
        className="relative w-[min(80vw,80vh,500px)] h-[min(80vw,80vh,500px)]" // 화면 크기에 따라 유연하게 크기 조정
        animate={{ 
          filter: [
            'brightness(1) contrast(100%) hue-rotate(0deg)',
            'brightness(1.5) contrast(150%) hue-rotate(30deg)',
            'brightness(1) contrast(100%) hue-rotate(0deg)'
          ],
          color: [ // 밝기 변화에 맞춰 색상 애니메이션 추가
            'rgba(255, 140, 0, 0.9)',
            'rgba(255, 215, 0, 1)', // 밝아질 때 더 밝은 노란색 계열로 변경
            'rgba(255, 140, 0, 0.9)'
          ]
        }}
        transition={{ 
          duration: duration / 1000, 
          ease: "linear",
          repeat: Infinity, // 불꽃이 일렁이는 것처럼 무한 반복
          repeatType: "reverse" 
        }}
      >
        <SunSVG color="rgba(255, 140, 0, 0.9)" flameColor="rgba(255, 69, 0, 0.9)" />
        {/* 추가적인 불꽃 효과 레이어 (선택 사항) */}
        <div 
          className="absolute inset-0 z-10" 
          style={{
            backgroundImage: 'radial-gradient(circle at center, rgba(255,165,0,0.5) 0%, rgba(255,69,0,0.3) 30%, transparent 70%)',
            animation: 'fire-flicker 0.3s infinite alternate',
            mixBlendMode: 'overlay', // 다른 요소와 섞이는 방식
            filter: 'blur(5px)'
          }}
        ></div>
      </motion.div>
    </motion.div>
  );
};

export default Sun;