import React from 'react';

export default function Toast({ message, type = 'info', customColor = {} }) {
  const typeConfig = {
    info: {
      bg: 'bg-sky-600/70',
      border: 'border-sky-400/50',
      text: 'text-sky-100',
      shadow: 'shadow-sky-500/20',
      icon: 'ℹ️',
    },
    success: {
      bg: 'bg-emerald-600/70',
      border: 'border-emerald-400/50',
      text: 'text-emerald-100',
      shadow: 'shadow-emerald-500/20',
      icon: '✅',
    },
    warning: {
      bg: 'bg-amber-600/70',
      border: 'border-amber-400/50',
      text: 'text-amber-100',
      shadow: 'shadow-amber-500/20',
      icon: '⚠️',
    },
    error: {
      bg: 'bg-red-600/70',
      border: 'border-red-400/50',
      text: 'text-red-100',
      shadow: 'shadow-red-500/20',
      icon: '❌',
    },
    celebration: {
      bg: 'bg-yellow-500/70',
      border: 'border-yellow-300/50',
      text: 'text-yellow-50',
      shadow: 'shadow-yellow-400/30',
      icon: '🎉',
    },
    achievement: {
      bg: 'bg-purple-600/70',
      border: 'border-purple-400/50',
      text: 'text-purple-100',
      shadow: 'shadow-purple-500/30',
      icon: '🏆',
    },
    clown: {
      bg: 'bg-red-700/70',
      border: 'border-red-500/50',
      text: 'text-red-100',
      shadow: 'shadow-red-600/20',
      icon: '🤡',
    },
    cosmic: {
      bg: 'bg-blue-800/70',
      border: 'border-blue-600/50',
      text: 'text-blue-100',
      shadow: 'shadow-blue-700/20',
      icon: '🌌',
    },
    golden: {
      bg: 'bg-yellow-600/70',
      border: 'border-yellow-400/50',
      text: 'text-yellow-100',
      shadow: 'shadow-yellow-500/20',
      icon: '🪙',
    },
  };

  const selectedType = customColor.bg ? { ...typeConfig.info, ...customColor } : (typeConfig[type] || typeConfig.info);

  return (
    <div
      className={`relative w-auto max-w-sm pointer-events-auto ${selectedType.bg} backdrop-blur-md border ${selectedType.border} shadow-2xl ${selectedType.shadow} rounded-2xl px-4 py-3 text-base font-bold ${selectedType.text} flex items-center gap-3`}
    >
      <span className="text-xl">{selectedType.icon}</span>
      <span>{message}</span>
    </div>
  );
}