import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const colors = ["#FFC300", "#FF5733", "#C70039", "#900C3F", "#581845", "#33FF57", "#3357FF"];

const FireworkParticle = ({ x, y, size, color, delay }) => {
    const destinationX = x + (Math.random() - 0.5) * 400; // 더 넓게 퍼지도록
    const destinationY = y + (Math.random() - 0.5) * 400; // 더 넓게 퍼지도록

    return (
        <motion.div
            className="absolute rounded-full"
            style={{ x, y, width: size, height: size, backgroundColor: color, boxShadow: `0 0 15px ${color}` }} // 그림자 강화
            initial={{ opacity: 1, scale: 0 }}
            animate={{ opacity: [1, 1, 0], scale: [0, 1.5, 0], x: destinationX, y: destinationY }} // 스케일 애니메이션 강화
            transition={{ duration: 1.8, delay, ease: "easeOut" }} // 지속 시간 증가
        />
    );
};

const FireworkBurst = ({ x, y, count = 70, burstDelay }) => { // 파티클 개수 증가
    const particles = Array.from({ length: count }).map((_, i) => ({
        id: i,
        size: Math.random() * 5 + 2, // 파티클 크기 범위 증가
        color: colors[Math.floor(Math.random() * colors.length)],
        delay: burstDelay + Math.random() * 0.3, // 지연 시간 약간 증가
    }));

    return <>{particles.map(p => <FireworkParticle key={p.id} x={x} y={y} {...p} />)}</>;
};

const Fireworks = ({ duration = 4000 }) => {
    const [bursts, setBursts] = useState([]);
    const totalBursts = 5; // 총 폭발 횟수

    // [수정] useEffect 로직 변경
    useEffect(() => {
        // 반복문으로 모든 불꽃 데이터를 미리 생성
        const newBursts = Array.from({ length: totalBursts }).map((_, index) => {
            const burstX = Math.random() * window.innerWidth;
            const burstY = Math.random() * (window.innerHeight * 0.7) + (window.innerHeight * 0.1);

            return {
                // Date.now() 대신 index를 사용하여 중복되지 않는 고유한 id 생성
                id: `firework-burst-${index}`, 
                x: burstX,
                y: burstY,
                burstDelay: index * (duration / 1000 / totalBursts),
            };
        });
        
        // 생성된 불꽃 배열로 상태를 한 번만 업데이트
        setBursts(newBursts);

    }, [duration]);

    return (
        <div className="fixed inset-0 pointer-events-none z-[101] overflow-hidden">
            {bursts.map(firework => (
                <FireworkBurst
                    key={firework.id}
                    x={firework.x}
                    y={firework.y}
                    burstDelay={firework.burstDelay}
                />
            ))}
        </div>
    );
};

export default Fireworks;