import React from 'react'
import { motion } from 'framer-motion'
export function Card({className='', ...props}){
  return <motion.div layout style={{ willChange: 'transform, opacity' }} transition={{ duration: 0.5, type: 'spring', bounce: 0.2 }} className={['glass rounded-2xl shadow-2xl', className].join(' ')} {...props}/>;
}
export function CardHeader({className='', ...props}){ return <div className={['px-4 sm:px-6 pt-4 sm:pt-6 pb-0', className].join(' ')} {...props}/>}
export function CardTitle({className='', ...props}){ return <div className={['text-lg sm:text-xl font-semibold', className].join(' ')} {...props}/>}
export function CardContent({className='', ptClass='', ...props}){ // ptClass 추가
  return <div className={['p-4 sm:p-6', ptClass, className].filter(Boolean).join(' ')} {...props}/>} // ptClass 적용
