import { submitScoreApi } from '../api/starcadeApi';

/**
 * 새로운 점수를 기록에 제출합니다.
 * @param {Object} scoreData - 기록할 점수 정보
 * @param {string} scoreData.gameId - 게임의 고유 ID (예: 'sudoku')
 * @param {number} scoreData.score - 획득한 점수
 * @param {Object} scoreData.user - 사용자 정보 객체 (AuthContext의 user)
 * @param {Object} [scoreData.options] - 난이도 등 기타 게임별 옵션
 */
export const submitScore = async (scoreData, triggerEffect, updateUser) => {
  const { gameId, score, user, options = {} } = scoreData;

  if (!gameId || typeof score !== 'number') {
    console.error('점수 제출에 필요한 정보가 부족합니다.', scoreData);
    return;
  }

  const data = await submitScoreApi({
    gameId,
    score,
    userId: user ? user.id : 'anonymous',
    options,
    timestamp: new Date().toISOString(),
  });

  if (data.success) {
    console.log('새로운 점수가 기록되었습니다:', data.score);

    // Handle all unlocks
    if (data.unlockedAchievements && data.unlockedAchievements.length > 0) {
      data.unlockedAchievements.forEach(ach => {
        if (ach.isNew) {
          triggerEffect('Toast', {
            message: `도전과제 달성! ${ach.name}`,
            type: 'achievement',
            duration: 3000
          });
          triggerEffect('Confetti'); // Add celebration effect
        } else if (ach.isNewTier) {
          triggerEffect('Toast', {
            message: `티어 달성! ${ach.name} (${ach.tier.name})`,
            type: 'achievement',
            duration: 3000
          });
          triggerEffect('Fireworks'); // Add celebration effect for new tiers
        }
      });
    }
    if (data.newlyUnlockedEffects && data.newlyUnlockedEffects.length > 0) {
      data.newlyUnlockedEffects.forEach(effectId => {
        triggerEffect('Toast', {
          message: `프로필 효과 해금! [${effectId}]`,
          type: 'celebration',
          duration: 4000
        });
      });
    }
    if (data.newlyUnlockedTitles && data.newlyUnlockedTitles.length > 0) {
      data.newlyUnlockedTitles.forEach(titleId => {
        triggerEffect('Toast', {
          message: `칭호 해금! [${titleId}]`,
          type: 'celebration',
          duration: 4000
        });
      });
    }

    // Handle currency gain
    if (data.currencyGained > 0) {
        triggerEffect('Toast', {
            message: `💰 ${data.currencyGained.toLocaleString()} 코인을 획득했습니다!`,
            type: 'success'
        });
    }

    // Update user context if it was returned
    if (data.user && updateUser) {
        updateUser(data.user, false); // full update
    }

  } else {
    console.error('점수 제출 실패:', data.message);
  }
};