import React, { useMemo, useState, useLayoutEffect } from 'react';
import './style.css';

const PartyHat = ({ color }) => {
  const patternId = `hatPattern-${color.replace('#', '')}`;
  const patternType = useMemo(() => Math.floor(Math.random() * 3), []); // 0: solid, 1: polka, 2: diagonal

  return (
    <svg width="100%" height="100%" viewBox="0 0 100 120" preserveAspectRatio="xMidYMin meet">
      <defs>
        {/* Patterns */}
        <pattern id={`${patternId}-polka`} patternUnits="userSpaceOnUse" width="20" height="20">
          <circle cx="10" cy="10" r="6" fill="white" opacity="0.8" />
        </pattern>
        <pattern id={`${patternId}-diagonal`} patternUnits="userSpaceOnUse" width="15" height="15">
          <path d="M-5,5 l10,-10 M0,15 l15,-15 M10,20 l10,-10" stroke="white" strokeWidth="3" opacity="0.7"/>
        </pattern>
        
        {/* Shading Gradient */}
        <linearGradient id="coneShading" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop offset="0%" stopColor="rgba(0,0,0,0.3)" />
          <stop offset="50%" stopColor="rgba(255,255,255,0.1)" />
          <stop offset="100%" stopColor="rgba(0,0,0,0.3)" />
        </linearGradient>

        {/* Pom-pom Filter */}
        <filter id="pompom-filter">
            <feTurbulence type="fractalNoise" baseFrequency="0.9" numOctaves="5" result="turbulence"/>
            <feDisplacementMap in="SourceGraphic" in2="turbulence" scale="3" />
        </filter>
      </defs>

      {/* Main Cone Shape */}
      <polygon points="50,15 5,100 95,100" fill={color} />

      {/* Apply Pattern */}
      {patternType === 1 && <polygon points="50,15 5,100 95,100" fill={`url(#${patternId}-polka)`} />}
      {patternType === 2 && <polygon points="50,15 5,100 95,100" fill={`url(#${patternId}-diagonal)`} />}

      {/* Apply Shading */}
      <polygon points="50,15 5,100 95,100" fill="url(#coneShading)" />

      {/* Bottom Fringe */}
      <path d="M 5 100 C 15 105, 25 95, 35 100 S 55 95, 65 100 S 85 105, 95 100" fill="none" stroke="#f1c40f" strokeWidth="4" />

      {/* Pom-pom */}
      <g transform="translate(0, -5)">
        <circle cx="50" cy="15" r="15" fill="white" filter="url(#pompom-filter)" />
        <circle cx="50" cy="15" r="15" fill="white" opacity="0.5" />
      </g>
    </svg>
  );
};

const BirthdayDecoration = ({ target = 'profile', dialogRef }) => {
  const [fontSize, setFontSize] = useState('3rem');

  useLayoutEffect(() => {
    const calculateSize = () => {
      if (dialogRef?.current) {
        const { width, height } = dialogRef.current.getBoundingClientRect();
        const newSize = Math.max(16, Math.min(24, width * 0.07));
        setFontSize(`${newSize}px`);
      }
    };

    // Initial calculation
    calculateSize();

    // Recalculate on window resize
    window.addEventListener('resize', calculateSize);
    return () => window.removeEventListener('resize', calculateSize);
  }, [dialogRef]);

  const hatColor = useMemo(() => {
    const colors = ['#ff6b6b', '#f06595', '#cc5de8', '#845ef7', '#5c7cfa', '#339af0', '#22b8cf', '#20c997', '#51cf66', '#fcc419'];
    return colors[Math.floor(Math.random() * colors.length)];
  }, []);

  if (target === 'profile') {
    return (
      <>
        <div className="halo-overlay" />
        <div className="birthday-hat-container">
          <PartyHat color={hatColor} />
        </div>
        <div className="birthday-title-container">
          <span className="neon-text" style={{ fontSize }}>HAPPY BIRTHDAY!</span>
        </div>
      </>
    );
  }
  
  // For lists or other targets
  return (
      <div className="absolute inset-0 pointer-events-none">
          <div className="absolute top-0 right-0 text-lg" style={{transform: 'translate(40%, -40%)'}}>🎉</div>
          <div className="absolute bottom-0 left-0 text-lg" style={{transform: 'translate(-40%, 40%)'}}>🎁</div>
      </div>
  );
};

export default BirthdayDecoration;